e intonations and intricate grammar. Elven literature is rich and varied, and their songs and poems are famous among other races. Many bards learn their language so they can add Elvish ballads to their repertoires.")])]),_c('div',{staticClass:"img-container halforc-aside"},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../assets/races/Half-Orc.png"),"alt":"Half Orc","aria-label":"Half Orc Features"}})]),_c('h5',[_vm._v("Species Details")]),_c('ul',{staticClass:"links-raza"},[_c('li',[_c('a',{attrs:{"href":"#scarred-and-strong","aria-label":"Link Scarred and Strong"}},[_vm._v("Scarred and Strong")])]),_c('li',[_c('a',{attrs:{"href":"#the-mark-of-gruumsh","aria-label":"Link The Mark of Gruumsh"}},[_vm._v("The Mark of Gruumsh")])]),_c('li',[_c('a',{attrs:{"href":"#half-orc-names","aria-label":"Link Half-Orc Names"}},[_vm._v("Half-Orc Names")])])]),_c('h5',[_vm._v("Traits")]),_c('ul',[_c('li',[_vm._v("Ability Score Increase")]),_c('p',[_vm._v("Your Dexterity score increases by 2.")]),_c('li',[_vm._v("Age")]),_c('p',[_vm._v("Although elves reach physical maturity at about the same age as humans, the elven understanding of adulthood goes beyond physical growth to encompass worldly experience. An elf typically claims adulthood and an adult name around the age of 100 and can live to be 750 years old.")]),_c('li',[_vm._v("Size")]),_c('p',[_vm._v("Elves range from under 5 to over 6 feet tall and have slender builds. Your size is Medium.")]),_c('li',[_vm._v("Speed")]),_c('p',[_vm._v("Your base walking speed is 30 feet.")]),_c('li',[_vm._v("Darkvision")]),_c('p',[_vm._v("Accustomed to twilit forests and the night sky, you have superior vision in dark and dim conditions. You can see in dim light within 60 feet of you as if it were bright light, and in darkness as if it were dim light. You cant discern color in darkness, only shades of gray.")]),_c('li',[_vm._v("Keen Senses")]),_c('p',[_vm._v("You have proficiency in the Perception skill.")]),_c('li',[_vm._v("Fey Ancestry")]),_c('p',[_vm._v("You have advantage on saving throws against being charmed, and magic cant put you to sleep.")]),_c('li',[_vm._v("Trance")]),_c('p',[_vm._v("Elves dont need to sleep. Instead, they meditate deeply, remaining semiconscious, for 4 hours a day. (The Common word for such meditation is trance.) While meditating, you can dream after a fashion; such dreams are actually mental exercises that have become reflexive through years of practice. After resting in this way, you gain the same benefit that a human does from 8 hours of sleep.")]),_c('li',[_vm._v("Languages")]),_c('p',[_vm._v("You can speak, read, and write Common and Elvish. Elvish is fluid, with subtle intonations and intricate grammar. Elven literature is rich and varied, and their songs and poems are famous among other races. Many bards learn their language so they can add Elvish ballads to their repertoires.")])]),_c('div',{staticClass:"img-container tiefling-aside"},[_c('img',{staticClass:"img-fluid",attrs:{"src":require("../assets/races/Tiefling.png"),"alt":"Tiefling","aria-label":"Tiefling Features"}})]),_c('h5',[_vm._v("Species Details")]),_c('ul',{staticClass:"links-raza"},[_c('li',[_c('a',{attrs:{"href":"#fiendish-heritage","aria-label":"Link Fiendish Heritage"}},[_vm._v("Fiendish Heritage")])]),_c('li',[_c('a',{attrs:{"href":"#a-devils-deal","aria-label":"Link A Devils Deal"}},[_vm._v("A Devil's Deal")])]),_c('li',[_c('a',{attrs:{"href":"#tiefling-names","aria-label":"Link Tiefling Names"}},[_vm._v("Tiefling Names")])])]),_c('h5',[_vm._v("Traits")]),_c('ul',[_c('li',[_vm._v("Ability Score Increase")]),_c('p',[_vm._v("Your Dexterity score increases by 2.")]),_c('li',[_vm._v("Age")]),_c('p',[_vm._v("Although elves reach physical maturity at about the same age as humans, the elven understanding of adulthood goes beyond physical growth to encompass worldly experience. An elf typically claims adulthood and an adult name around the age of 100 and can live to be 750 years old.")]),_c('li',[_vm._v("Size")]),_c('p',[_vm._v("Elves range from under 5 to over 6 feet tall and have slender builds. Your size is Medium.")]),_c('li',[_vm._v("Speed")]),_c('p',[_vm._v("Your base walking speed is 30 feet.")]),_c('li',[_vm._v("Darkvision")]),_c('p',[_vm._v("Accustomed to twilit forests and the night sky, you have superior vision in dark and dim conditions. You can see in dim light within 60 feet of you as if it were bright light, and in darkness as if it were dim light. You cant discern color in darkness, only shades of gray.")]),_c('li',[_vm._v("Keen Senses")]),_c('p',[_vm._v("You have proficiency in the Perception skill.")]),_c('li',[_vm._v("Fey Ancestry")]),_c('p',[_vm._v("You have advantage on saving throws against being charmed, and magic cant put you to sleep.")]),_c('li',[_vm._v("Trance")]),_c('p',[_vm._v("Elves dont need to sleep. Instead, they meditate deeply, remaining semiconscious, for 4 hours a day. (The Common word for such meditation is trance.) While meditating, you can dream after a fashion; such dreams are actually mental exercises that have become reflexive through years of practice. After resting in this way, you gain the same benefit that a human does from 8 hours of sleep.")]),_c('li',[_vm._v("Languages")]),_c('p',[_vm._v("You can speak, read, and write Common and Elvish. Elvish is fluid, with subtle intonations and intricate grammar. Elven literature is rich and varied, and their songs and poems are famous among other races. Many bards learn their language so they can add Elvish ballads to their repertoires.")])])]),_c('div',{staticClass:"col-12 col-md-9"},[_c('div',{staticClass:"raza-dwarf"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-dwarf"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Dwarf")]),_c('p',[_vm._v(" Kingdoms rich in ancient grandeur, halls carved into the roots of mountains, the echoing of picks and hammers in deep mines and blazing forges, a commitment to clan and tradition, and a burning hatred of goblins and orcsthese common threads unite all dwarves. ")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"short-and-stout"}},[_vm._v("Short and Stout")]),_c('p',[_vm._v("Bold and hardy, dwarves are known as skilled warriors, miners, and workers of stone and metal. Though they stand well under 5 feet tall, dwarves are so broad and compact that they can weigh as much as a human standing nearly two feet taller. Their courage and endurance are also easily a match for any of the larger folk. Dwarven skin ranges from deep brown to a paler hue tinged with red, but the most common shades are light brown or deep tan, like certain tones of earth. Their hair, worn long but in simple styles, is usually black, gray, or brown, though paler dwarves often have red hair. Male dwarves value their beards highly and groom them carefully.")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"long-memory"}},[_vm._v("Long Memory, Long Grudges")]),_c('p',[_vm._v("Dwarves can live to be more than 400 years old, so the oldest living dwarves often remember a very different world. For example, some of the oldest dwarves living in Citadel Felbarr (in the world of the Forgotten Realms) can recall the day, more than three centuries ago, when orcs conquered the fortress and drove them into an exile that lasted over 250 years. This longevity grants them a perspective on the world that shorter-lived races such as humans and halflings lack. Dwarves are solid and enduring like the mountains they love, weathering the passage of centuries with stoic endurance and little change. They respect the traditions of their clans, tracing their ancestry back to the founding of their most ancient strongholds in the youth of the world, and dont abandon those traditions lightly. "),_c('br'),_vm._v(" Part of those traditions is devotion to the gods of the dwarves, who uphold the dwarven ideals of industrious labor, skill in battle, and devotion to the forge. Individual dwarves are determined and loyal, true to their word and decisive in action, sometimes to the point of stubbornness. Many dwarves have a strong sense of justice, and they are slow to forget wrongs they have suffered. A wrong done to one dwarf is a wrong done to the dwarfs entire clan, so what begins as one dwarfs hunt for vengeance can become a full-blown clan feud. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"clans-and-kingdoms"}},[_vm._v("Clans and Kingdoms")]),_c('p',[_vm._v("Dwarven kingdoms stretch deep beneath the mountains where the dwarves mine gems and precious metals and forge items of wonder. They love the beauty and artistry of precious metals and fine jewelry, and in some dwarves this love festers into avarice. Whatever wealth they cant find in their mountains, they gain through trade. They dislike boats, so enterprising humans and halflings frequently handle trade in dwarven goods along water routes. Trustworthy members of other races are welcome in dwarf settlements, though some areas are off limits even to them. The chief unit of dwarven society is the clan, and dwarves highly value social standing. Even dwarves who live far from their own kingdoms cherish their clan identities and affiliations, recognize related dwarves, and invoke their ancestors names in oaths and curses. To be clanless is the worst fate that can befall a dwarf. Dwarves in other lands are typically artisans, especially weaponsmiths, armorers, and jewelers. Some become mercenaries or bodyguards, highly sought after for their courage and loyalty.")])]),_c('div',{staticClass:"apartado-4"},[_c('h3',{attrs:{"id":"gods-and-clan"}},[_vm._v("Gods, Gold, and Clan")]),_c('p',[_vm._v("Dwarves who take up the adventuring life might be motivated by a desire for treasurefor its own sake, for a specific purpose, or even out of an altruistic desire to help others. Other dwarves are driven by the command or inspiration of a deity, a direct calling or simply a desire to bring glory to one of the dwarf gods. Clan and ancestry are also important motivators. A dwarf might seek to restore a clans lost honor, avenge an ancient wrong the clan suffered, or earn a new place within the clan after having been exiled. Or a dwarf might search for the axe wielded by a mighty ancestor, lost on the field of battle centuries ago.")])]),_c('div',{staticClass:"apartado-5"},[_c('h3',{attrs:{"id":"dwarf-names"}},[_vm._v("Dwarf Names")]),_c('p',[_vm._v("A dwarfs name is granted by a clan elder, in accordance with tradition. Every proper dwarven name has been used and reused down through the generations. A dwarfs name belongs to the clan, not to the individual. A dwarf who misuses or brings shame to a clan name is stripped of the name and forbidden by law to use any dwarven name in its place. "),_c('br'),_c('br'),_vm._v("Male Names: Adrik, Alberich, Baern, Barendd, Brottor, Bruenor, Dain, Darrak, Delg, Eberk, Einkil, Fargrim, Flint, Gardain, Harbek, Kildrak, Morgran, Orsik, Oskar, Rangrim, Rurik, Taklinn, Thoradin, Thorin, Tordek, Traubon, Travok, Ulfgar, Veit, Vondal "),_c('br'),_c('br'),_vm._v("Female Names: Amber, Artin, Audhild, Bardryn, Dagnal, Diesa, Eldeth, Falkrunn, Finellen, Gunnloda, Gurdis, Helja, Hlin, Kathra, Kristryd, Ilde, Liftrasa, Mardred, Riswynn, Sannl, Torbera, Torgga, Vistra "),_c('br'),_c('br'),_vm._v("Clan Names: Balderk, Battlehammer, Brawnanvil, Dankil, Fireforge, Frostbeard, Gorunn, Holderhek, Ironfist, Loderr, Lutgehr, Rumnaheim, Strakeln, Torunn, Ungart ")])])]),_c('div',{staticClass:"raza-elf"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-elf"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Elf")]),_c('p',[_vm._v("Elves are a magical people of otherworldly grace, living in the world but not entirely part of it. They live in places of ethereal beauty, in the midst of ancient forests or in silvery spires glittering with faerie light, where soft music drifts through the air and gentle fragrances waft on the breeze. Elves love nature and magic, art and artistry, music and poetry, and the good things of the world.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"slender-and-graceful"}},[_vm._v("Slender and Graceful")]),_c('p',[_vm._v("With their unearthly grace and fine features, elves appear hauntingly beautiful to humans and members of many other races. They are slightly shorter than humans on average, ranging from well under 5 feet tall to just over 6 feet. They are more slender than humans, weighing only 100 to 145 pounds. Males and females are about the same height, and males are only marginally heavier than females. Elves coloration encompasses the normal human range and also includes skin in shades of copper, bronze, and almost bluish-white, hair of green or blue, and eyes like pools of liquid gold or silver. Elves have no facial and little body hair. They favor elegant clothing in bright colors, and they enjoy simple yet lovely jewelry.")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"a-timeless-perspective"}},[_vm._v("A Timeless Perspective")]),_c('p',[_vm._v("Elves can live well over 700 years, giving them a broad perspective on events that might trouble the shorter-lived races more deeply. They are more often amused than excited, and more likely to be curious than greedy. They tend to remain aloof and unfazed by petty happenstance. When pursuing a goal, however, whether adventuring on a mission or learning a new skill or art, elves can be focused and relentless. They are slow to make friends and enemies, and even slower to forget them. They reply to petty insults with disdain and to serious insults with vengeance. "),_c('br'),_c('br'),_vm._v("Like the branches of a young tree, elves are flexible in the face of danger. They trust in diplomacy and compromise to resolve differences before they escalate to violence. They have been known to retreat from intrusions into their woodland homes, confident that they can simply wait the invaders out. But when the need arises, elves reveal a stern martial side, demonstrating skill with sword, bow, and strategy. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"hidden-woodland-realms"}},[_vm._v("Hidden Woodland Realms")]),_c('p',[_vm._v("Most elves dwell in small forest villages hidden among the trees. Elves hunt game, gather food, and grow vegetables, and their skill and magic allow them to support themselves without the need for clearing and plowing land. They are talented artisans, crafting finely worked clothes and art objects. Their contact with outsiders is usually limited, though a few elves make a good living by trading crafted items for metals (which they have no interest in mining). "),_c('br'),_c('br'),_vm._v("Elves encountered outside their own lands are commonly traveling minstrels, artists, or sages. Human nobles compete for the services of elf instructors to teach swordplay or magic to their children. ")])]),_c('div',{staticClass:"apartado-4"},[_c('h3',{attrs:{"id":"exploration-and-adventure"}},[_vm._v("Exploration and Adventure")]),_c('p',[_vm._v("Elves take up adventuring out of wanderlust. Since they are so long-lived, they can enjoy centuries of exploration and discovery. They dislike the pace of human society, which is regimented from day to day but constantly changing over decades, so they find careers that let them travel freely and set their own pace. Elves also enjoy exercising their martial prowess or gaining greater magical power, and adventuring allows them to do so. Some might join with rebels fighting against oppression, and others might become champions of moral causes.")])]),_c('div',{staticClass:"apartado-5"},[_c('h3',{attrs:{"id":"elf-names"}},[_vm._v("Elf Names")]),_c('p',[_vm._v(" Elves are considered children until they declare themselves adults, some time after the hundredth birthday, and before this period they are called by child names. On declaring adulthood, an elf selects an adult name, although those who knew him or her as a youngster might continue to use the child name. Each elfs adult name is a unique creation, though it might reflect the names of respected individuals or other family members. Little distinction exists between male names and female names; the groupings here reflect only general tendencies. In addition, every elf bears a family name, typically a combination of other Elvish words. Some elves traveling among humans translate their family names into Common, but others retain the Elvish version. "),_c('br'),_c('br'),_vm._v("Child Names: Ara, Bryn, Del, Eryn, Faen, Innil, Lael, Mella, Naill, Naeris, Phann, Rael, Rinn, Sai, Syllin, Thia, Vall "),_c('br'),_c('br'),_vm._v("Male Adult Names: Adran, Aelar, Aramil, Arannis, Aust, Beiro, Berrian, Carric, Enialis, Erdan, Erevan, Galinndan, Hadarai, Heian, Himo, Immeral, Ivellios, Laucian, Mindartis, Paelias, Peren, Quarion, Riardon, Rolen, Soveliss, Thamior, Tharivol, Theren, Varis "),_c('br'),_c('br'),_vm._v("Female Adult Names: Adrie, Althaea, Anastrianna, Andraste, Antinua, Bethrynna, Birel, Caelynn, Drusilia, Enna, Felosial, Ielenia, Jelenneth, Keyleth, Leshanna, Lia, Meriele, Mialee, Naivara, Quelenna, Quillathe, Sariel, Shanairra, Shava, Silaqui, Theirastra, Thia, Vadania, Valanthe, Xanaphia "),_c('br'),_c('br'),_vm._v("Clan Names: Amakiir (Gemflower), Amastacia (Starflower), Galanodel (Moonwhisper), Holimion (Diamonddew), Ilphelkiir (Gemblossom), Liadon (Silverfrond), Meliamne (Oakenheel), Nalo (Nightbreeze), Siannodel (Moonbrook), Xiloscient (Goldpetal) ")])])])]),_c('div',{staticClass:"raza-halfling"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-halfling"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Halfling")]),_c('p',[_vm._v("The comforts of home are the goals of most halflings lives: a place to settle in peace and quiet, far from marauding monsters and clashing armies; a blazing fire and a generous meal; fine drink and fine conversation. Though some halflings live out their days in remote agricultural communities, others form nomadic bands that travel constantly, lured by the open road and the wide horizon to discover the wonders of new lands and peoples. But even these wanderers love peace, food, hearth, and home, though home might be a wagon jostling along a dirt road or a raft floating downriver.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"small-and-practical"}},[_vm._v("Small and Practical")]),_c('p',[_vm._v("The diminutive halflings survive in a world full of larger creatures by avoiding notice or, barring that, avoiding offense. Standing about 3 feet tall, they appear relatively harmless and so have managed to survive for centuries in the shadow of empires and on the edges of wars and political strife. They are inclined to be stout, weighing between 40 and 45 pounds. "),_c('br'),_c('br'),_vm._v("Halflings skin ranges from tan to pale with a ruddy cast, and their hair is usually brown or sandy brown and wavy. They have brown or hazel eyes. Halfling men often sport long sideburns, but beards are rare among them and mustaches even more so. They like to wear simple, comfortable, and practical clothes, favoring bright colors. "),_c('br'),_c('br'),_vm._v("Halfling practicality extends beyond their clothing. Theyre concerned with basic needs and simple pleasures and have little use for ostentation. Even the wealthiest of halflings keep their treasures locked in a cellar rather than on display for all to see. They have a knack for finding the most straightforward solution to a problem, and have little patience for dithering. ")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"kind-and-curious"}},[_vm._v("Kind and Curious")]),_c('p',[_vm._v("Halflings are an affable and cheerful people. They cherish the bonds of family and friendship as well as the comforts of hearth and home, harboring few dreams of gold or glory. Even adventurers among them usually venture into the world for reasons of community, friendship, wanderlust, or curiosity. They love discovering new things, even simple things, such as an exotic food or an unfamiliar style of clothing. "),_c('br'),_c('br'),_vm._v("Halflings are easily moved to pity and hate to see any living thing suffer. They are generous, happily sharing what they have even in lean times. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"blend-into-the-crowd"}},[_vm._v("Blend into the Crowd")]),_c('p',[_vm._v("Halflings are adept at fitting into a community of humans, dwarves, or elves, making themselves valuable and welcome. The combination of their inherent stealth and their unassuming nature helps halflings to avoid unwanted attention. "),_c('br'),_c('br'),_vm._v("Halflings work readily with others, and they are loyal to their friends, whether halfling or otherwise. They can display remarkable ferocity when their friends, families, or communities are threatened. ")])]),_c('div',{staticClass:"apartado-4"},[_c('h3',{attrs:{"id":"pastoral-pleasantries"}},[_vm._v("Pastoral Pleasantries")]),_c('p',[_vm._v("Most halflings live in small, peaceful communities with large farms and well-kept groves. They rarely build kingdoms of their own or even hold much land beyond their quiet shires. They typically dont recognize any sort of halfling nobility or royalty, instead looking to family elders to guide them. Families preserve their traditional ways despite the rise and fall of empires. "),_c('br'),_c('br'),_vm._v("Many halflings live among other races, where the halflings hard work and loyal outlook offer them abundant rewards and creature comforts. Some halfling communities travel as a way of life, driving wagons or guiding boats from place to place and maintaining no permanent home. ")])]),_c('div',{staticClass:"apartado-5"},[_c('h3',{attrs:{"id":"exploring-opportunities"}},[_vm._v("Exploring Opportunities")]),_c('p',[_vm._v("Halflings usually set out on the adventurers path to defend their communities, support their friends, or explore a wide and wonder-filled world. For them, adventuring is less a career than an opportunity or sometimes a necessity. ")])]),_c('div',{staticClass:"apartado-6"},[_c('h3',{attrs:{"id":"halfling-names"}},[_vm._v("Halfling Names")]),_c('p',[_vm._v("A halfling has a given name, a family name, and possibly a nickname. Family names are often nicknames that stuck so tenaciously they have been passed down through the generations. "),_c('br'),_c('br'),_vm._v("Male Names: Alton, Ander, Cade, Corrin, Eldon, Errich, Finnan, Garret, Lindal, Lyle, Merric, Milo, Osborn, Perrin, Reed, Roscoe, Wellby "),_c('br'),_c('br'),_vm._v("Female Names: Andry, Bree, Callie, Cora, Euphemia, Jillian, Kithri, Lavinia, Lidda, Merla, Nedda, Paela, Portia, Seraphina, Shaena, Trym, Vani, Verna "),_c('br'),_c('br'),_vm._v("Clan Names: Brushgather, Goodbarrel, Greenbottle, High-hill, Hilltopple, Leagallow, Tealeaf, Thorngage, Tosscobble, Underbough ")])])])]),_c('div',{staticClass:"raza-human"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-human"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Human")]),_c('p',[_vm._v("In the reckonings of most worlds, humans are the youngest of the common races, late to arrive on the world scene and short-lived in comparison to dwarves, elves, and dragons. Perhaps it is because of their shorter lives that they strive to achieve as much as they can in the years they are given. Or maybe they feel they have something to prove to the elder races, and thats why they build their mighty empires on the foundation of conquest and trade. Whatever drives them, humans are the innovators, the achievers, and the pioneers of the worlds.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"a-broad-spectrum"}},[_vm._v("A Broad Spectrum")]),_c('p',[_vm._v("With their penchant for migration and conquest, humans are more physically diverse than other common races. There is no typical human. An individual can stand from 5 feet to a little over 6 feet tall and weigh from 125 to 250 pounds. Human skin shades range from nearly black to very pale, and hair colors from black to blond (curly, kinky, or straight); males might sport facial hair that is sparse or thick. A lot of humans have a dash of nonhuman blood, revealing hints of elf, orc, or other lineages. Humans reach adulthood in their late teens and rarely live even a single century.")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"variety-in-all-things"}},[_vm._v("Variety in All Things")]),_c('p',[_vm._v("Humans are the most adaptable and ambitious people among the common races. They have widely varying tastes, morals, and customs in the many different lands where they have settled. When they settle, though, they stay: they build cities to last for the ages, and great kingdoms that can persist for long centuries. An individual human might have a relatively short life span, but a human nation or culture preserves traditions with origins far beyond the reach of any single humans memory. They live fully in the presentmaking them well suited to the adventuring lifebut also plan for the future, striving to leave a lasting legacy. Individually and as a group, humans are adaptable opportunists, and they stay alert to changing political and social dynamics.")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"lasting-institutions"}},[_vm._v("Lasting Institutions")]),_c('p',[_vm._v("Where a single elf or dwarf might take on the responsibility of guarding a special location or a powerful secret, humans found sacred orders and institutions for such purposes. While dwarf clans and halfling elders pass on the ancient traditions to each new generation, human temples, governments, libraries, and codes of law fix their traditions in the bedrock of history. Humans dream of immortality, but (except for those few who seek undeath or divine ascension to escape deaths clutches) they achieve it by ensuring that they will be remembered when they are gone. "),_c('br'),_c('br'),_vm._v("Although some humans can be xenophobic, in general their societies are inclusive. Human lands welcome large numbers of nonhumans compared to the proportion of humans who live in nonhuman lands. ")])]),_c('div',{staticClass:"apartado-4"},[_c('h3',{attrs:{"id":"exemplars-of-ambition"}},[_vm._v("Exemplars of Ambition")]),_c('p',[_vm._v("Humans who seek adventure are the most daring and ambitious members of a daring and ambitious race. They seek to earn glory in the eyes of their fellows by amassing power, wealth, and fame. More than other people, humans champion causes rather than territories or groups.")])]),_c('div',{staticClass:"apartado-5"},[_c('h3',{attrs:{"id":"human-names-and-ethnicities"}},[_vm._v("Human Names and Ethnicities")]),_c('p',[_vm._v("Having so much more variety than other cultures, humans as a whole have no typical names. Some human parents give their children names from other languages, such as Dwarvish or Elvish (pronounced more or less correctly), but most parents give names that are linked to their regions culture or to the naming traditions of their ancestors. "),_c('br'),_c('br'),_vm._v("The material culture and physical characteristics of humans can change wildly from region to region. In the Forgotten Realms, for example, the clothing, architecture, cuisine, music, and literature are different in the northwestern lands of the Silver Marches than in distant Turmish or Impiltur to the eastand even more distinctive in far-off Kara-Tur. Human physical characteristics, though, vary according to the ancient migrations of the earliest humans, so that the humans of the Silver Marches have every possible variation of coloration and features. "),_c('br'),_c('br'),_vm._v("In the Forgotten Realms, nine human ethnic groups are widely recognized, though over a dozen others are found in more localized areas of Faern. These groups, and the typical names of their members, can be used as inspiration no matter which world your human is in. ")])]),_c('div',{staticClass:"apartado-6"},[_c('h3',{attrs:{"id":"human-names"}},[_vm._v("Human Names")]),_c('p',[_c('br'),_c('br'),_vm._v("Male Names: Aseir, Bardeid, Haseid, Khemed, Mehmen, Sudeiman, Zasheir; Darvin, Dorn, Evendur, Gorstag, Grim, Helm, Malark, Morn, Randal, Stedd; Bor, Fodel, Glar, Grigor, Igan, Ivor, Kosef, Mival, Orel, Pavel, Sergor; Ander, Blath, Bran, Frath, Geth, Lander, Luth, Malcer, Stor, Taman, Urth; Aoth, Bareris, Ehput-Ki, Kethoth, Mumed, Ramas, So-Kehur, Thazar-De, Urhur; "),_c('br'),_c('br'),_vm._v("Female Names: Atala, Ceidil, Hama, Jasmal, Meilil, Seipora, Yasheira, Zasheida; (surnames) Basha, Dumein, Jassan, Khalid, Mostana, Pashar, Rein; Arveene, Esvele, Jhessail, Kerri, Lureene, Miri, Rowan, Shandri, Tessele; Alethra, Kara, Katernin, Mara, Natali, Olma, Tana, Zora;Amafrey, Betha, Cefrey, Kethra, Mara, Olga, Silifrey, Westra; rizima, Chathi, Nephis, Nulara, Murithi, Sefris, Thola, Umara, Zolis; "),_c('br'),_c('br'),_vm._v("Clan Names: Turami, Tethyrian, Shou, Rashemi, Mulan, Illuskan, Damaran, Chondathan, Calishite. ")])])])]),_c('div',{staticClass:"raza-dragonborn"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-dragonborn"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Dragonborn")]),_c('p',[_vm._v("Born of dragons, as their name proclaims, the dragonborn walk proudly through a world that greets them with fearful incomprehension. Shaped by draconic gods or the dragons themselves, dragonborn originally hatched from dragon eggs as a unique race, combining the best attributes of dragons and humanoids. Some dragonborn are faithful servants to true dragons, others form the ranks of soldiers in great wars, and still others find themselves adrift, with no clear calling in life.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"proud-dragon-kin"}},[_vm._v("Proud Dragon Kin")]),_c('p',[_vm._v("Dragonborn look very much like dragons standing erect in humanoid form, though they lack wings or a tail. The first dragonborn had scales of vibrant hues matching the colors of their dragon kin, but generations of interbreeding have created a more uniform appearance. Their small, fine scales are usually brass or bronze in color, sometimes ranging to scarlet, rust, gold, or copper-green. They are tall and strongly built, often standing close to 6 feet tall and weighing 300 pounds or more. Their hands and feet are strong, talonlike claws with three fingers and a thumb on each hand. "),_c('br'),_c('br'),_vm._v("The blood of a particular type of dragon runs very strong through some dragonborn clans. These dragonborn often boast scales that more closely match those of their dragon ancestorbright red, green, blue, or white, lustrous black, or gleaming metallic gold, silver, brass, copper, or bronze. ")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"self-sufficient-clans"}},[_vm._v("Self-Sufficient Clans")]),_c('p',[_vm._v("To any dragonborn, the clan is more important than life itself. Dragonborn owe their devotion and respect to their clan above all else, even the gods. Each dragonborns conduct reflects on the honor of his or her clan, and bringing dishonor to the clan can result in expulsion and exile. Each dragonborn knows his or her station and duties within the clan, and honor demands maintaining the bounds of that position. "),_c('br'),_c('br'),_vm._v("A continual drive for self-improvement reflects the self-sufficiency of the race as a whole. Dragonborn value skill and excellence in all endeavors. They hate to fail, and they push themselves to extreme efforts before they give up on something. A dragonborn holds mastery of a particular skill as a lifetime goal. Members of other races who share the same commitment find it easy to earn the respect of a dragonborn. "),_c('br'),_c('br'),_vm._v("Though all dragonborn strive to be self-sufficient, they recognize that help is sometimes needed in difficult situations. But the best source for such help is the clan, and when a clan needs help, it turns to another dragonborn clan before seeking aid from other racesor even from the gods. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"dragonborn-names"}},[_vm._v("Dragonborn Names")]),_c('p',[_vm._v(" Dragonborn have personal names given at birth, but they put their clan names first as a mark of honor. A childhood name or nickname is often used among clutchmates as a descriptive term or a term of endearment. The name might recall an event or center on a habit. "),_c('br'),_c('br'),_vm._v("Male Names: Arjhan, Balasar, Bharash, Donaar, Ghesh, Heskan, Kriv, Medrash, Mehen, Nadarr, Pandjed, Patrin, Rhogar, Shamash, Shedinn, Tarhun, Torinn "),_c('br'),_c('br'),_vm._v("Female Names: Akra, Biri, Daar, Farideh, Harann, Havilar, Jheri, Kava, Korinn, Mishann, Nala, Perra, Raiann, Sora, Surina, Thava, Uadjit "),_c('br'),_c('br'),_vm._v("Clan Names: Clethtinthiallor, Daardendrian, Delmirev, Drachedandion, Fenkenkabradon, Kepeshkmolik, Kerrhylon, Kimbatuul, Linxakasendalor, Myastan, Nemmonis, Norixius, Ophinshtalajiir, Prexijandilin, Shestendeliath, Turnuroth, Verthisathurgiesh, Yarjerit ")])])])]),_c('div',{staticClass:"raza-gnome"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-gnome"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Gnome")]),_c('p',[_vm._v("A constant hum of busy activity pervades the warrens and neighborhoods where gnomes form their close-knit communities. Louder sounds punctuate the hum: a crunch of grinding gears here, a minor explosion there, a yelp of surprise or triumph, and especially bursts of laughter. Gnomes take delight in life, enjoying every moment of invention, exploration, investigation, creation, and play.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"vibrant-expression"}},[_vm._v("Vibrant Expression")]),_c('p',[_vm._v("A gnomes energy and enthusiasm for living shines through every inch of his or her tiny body. Gnomes average slightly over 3 feet tall and weigh 40 to 45 pounds. Their tan or brown faces are usually adorned with broad smiles (beneath their prodigious noses), and their bright eyes shine with excitement. Their fair hair has a tendency to stick out in every direction, as if expressing the gnomes insatiable interest in everything around. "),_c('br'),_c('br'),_vm._v(" A gnomes personality is writ large in his or her appearance. A male gnomes beard, in contrast to his wild hair, is kept carefully trimmed but often styled into curious forks or neat points. A gnomes clothing, though usually made in modest earth tones, is elaborately decorated with embroidery, embossing, or gleaming jewels. ")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"delighted-dedication"}},[_vm._v("Delighted Dedication")]),_c('p',[_vm._v("As far as gnomes are concerned, being alive is a wonderful thing, and they squeeze every ounce of enjoyment out of their three to five centuries of life. Humans might wonder about getting bored over the course of such a long life, and elves take plenty of time to savor the beauties of the world in their long years, but gnomes seem to worry that even with all that time, they cant get in enough of the things they want to do and see. "),_c('br'),_c('br'),_vm._v("Gnomes speak as if they cant get the thoughts out of their heads fast enough. Even as they offer ideas and opinions on a range of subjects, they still manage to listen carefully to others, adding the appropriate exclamations of surprise and appreciation along the way. "),_c('br'),_c('br'),_vm._v("Though gnomes love jokes of all kinds, particularly puns and pranks, theyre just as dedicated to the more serious tasks they undertake. Many gnomes are skilled engineers, alchemists, tinkers, and inventors. Theyre willing to make mistakes and laugh at themselves in the process of perfecting what they do, taking bold (sometimes foolhardy) risks and dreaming large. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"bright-burrows"}},[_vm._v("Bright Burrows")]),_c('p',[_vm._v("Gnomes make their homes in hilly, wooded lands. They live underground but get more fresh air than dwarves do, enjoying the natural, living world on the surface whenever they can. Their homes are well hidden by both clever construction and simple illusions. Welcome visitors are quickly ushered into the bright, warm burrows. Those who are not welcome are unlikely to find the burrows in the first place. "),_c('br'),_c('br'),_vm._v("Gnomes who settle in human lands are commonly gemcutters, engineers, sages, or tinkers. Some human families retain gnome tutors, ensuring that their pupils enjoy a mix of serious learning and delighted enjoyment. A gnome might tutor several generations of a single human family over the course of his or her long life. ")])]),_c('div',{staticClass:"apartado-4"},[_c('h3',{attrs:{"id":"seeing-the-world"}},[_vm._v("Seeing the World")]),_c('p',[_vm._v("Curious and impulsive, gnomes might take up adventuring as a way to see the world or for the love of exploring. As lovers of gems and other fine items, some gnomes take to adventuring as a quick, if dangerous, path to wealth. Regardless of what spurs them to adventure, gnomes who adopt this way of life eke as much enjoyment out of it as they do out of any other activity they undertake, sometimes to the great annoyance of their adventuring companions.")])]),_c('div',{staticClass:"apartado-5"},[_c('h3',{attrs:{"id":"gnome-names"}},[_vm._v("Gnome Names")]),_c('p',[_vm._v("Gnomes love names, and most have half a dozen or so. A gnomes mother, father, clan elder, aunts, and uncles each give the gnome a name, and various nicknames from just about everyone else might or might not stick over time. Gnome names are typically variants on the names of ancestors or distant relatives, though some are purely new inventions. When dealing with humans and others who are stuffy about names, a gnome learns to use no more than three names: a personal name, a clan name, and a nickname, choosing the one in each category thats the most fun to say. "),_c('br'),_c('br'),_vm._v("Male Names: Alston, Alvyn, Boddynock, Brocc, Burgell, Dimble, Eldon, Erky, Fonkin, Frug, Gerbo, Gimble, Glim, Jebeddo, Kellen, Namfoodle, Orryn, Roondar, Seebo, Sindri, Warryn, Wrenn, Zook "),_c('br'),_c('br'),_vm._v("Female Names: Bimpnottin, Breena, Caramip, Carlin, Donella, Duvamil, Ella, Ellyjobell, Ellywick, Lilli, Loopmottin, Lorilla, Mardnab, Nissa, Nyx, Oda, Orla, Roywyn, Shamil, Tana, Waywocket, Zanna "),_c('br'),_c('br'),_vm._v("Clan Names:Beren, Daergel, Folkor, Garrick, Nackle, Murnig, Ningel, Raulnor, Scheppen, Timbers, Turen ")])])])]),_c('div',{staticClass:"raza-halfelf"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-halfelf"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Half-Elf")]),_c('p',[_vm._v("To humans, half-elves look like elves, and to elves, they look human. In height, theyre on par with both parents, though theyre neither as slender as elves nor as broad as humans. They range from under 5 feet to about 6 feet tall, and from 100 to 180 pounds, with men only slightly taller and heavier than women. Half-elf men do have facial hair, and sometimes grow beards to mask their elven ancestry. Half-elven coloration and features lie somewhere between their human and elf parents, and thus show a variety even more pronounced than that found among either race. They tend to have the eyes of their elven parents.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"of-two-worlds"}},[_vm._v("Of Two Worlds")]),_c('p',[_vm._v("To humans, half-elves look like elves, and to elves, they look human. In height, theyre on par with both parents, though theyre neither as slender as elves nor as broad as humans. They range from under 5 feet to about 6 feet tall, and from 100 to 180 pounds, with men only slightly taller and heavier than women. Half-elf men do have facial hair, and sometimes grow beards to mask their elven ancestry. Half-elven coloration and features lie somewhere between their human and elf parents, and thus show a variety even more pronounced than that found among either race. They tend to have the eyes of their elven parents. ")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"diplomats-or-wanderers"}},[_vm._v("Diplomats or Wanderers")]),_c('p',[_vm._v("Half-elves have no lands of their own, though they are welcome in human cities and somewhat less welcome in elven forests. In large cities in regions where elves and humans interact often, half-elves are sometimes numerous enough to form small communities of their own. They enjoy the company of other half-elves, the only people who truly understand what it is to live between these two worlds. "),_c('br'),_c('br'),_vm._v("In most parts of the world, though, half-elves are uncommon enough that one might live for years without meeting another. Some half-elves prefer to avoid company altogether, wandering the wilds as trappers, foresters, hunters, or adventurers and visiting civilization only rarely. Like elves, they are driven by the wanderlust that comes of their longevity. Others, in contrast, throw themselves into the thick of society, putting their charisma and social skills to great use in diplomatic roles or as swindlers. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"half-elf-variants"}},[_vm._v("Half-Elf Variants")]),_c('p',[_vm._v("Some half-elves in Faern have a racial trait in place of the Skill Versatility trait. If your DM allows it, your half-elf character can forgo Skill Versatility and instead take the elf trait Keen Senses or a trait based on your elf parentage")])]),_c('div',{staticClass:"apartado-4"},[_c('h3',{attrs:{"id":"half-elf-names"}},[_vm._v("Half-Elf Names")]),_c('p',[_vm._v("Half-elves use either human or elven naming conventions. As if to emphasize that they dont really fit in to either society, half-elves raised among humans are often given elven names, and those raised among elves often take human names.")])])])]),_c('div',{staticClass:"raza-halforc",attrs:{"id":"raza-halforc"}},[_c('div',{staticClass:"apartados"},[_c('h2',{staticClass:"races-title"},[_vm._v("Half Orc")]),_c('p',[_vm._v("Whether united under the leadership of a mighty warlock or having fought to a standstill after years of conflict, orc and human communities, sometimes form alliances. When these alliances are sealed by marriages, half-orcs are born. Some half-orcs rise to become proud leaders of orc communities. Some venture into the world to prove their worth. Many of these become adventurers, achieving greatness for their mighty deeds.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"scarred-and-strong"}},[_vm._v("Scarred and Strong")]),_c('p',[_vm._v("Half-orcs exhibit a blend of orcish and human characteristics, and their appearance varies widely. Grayish skin tones and prominent teeth are the most common shared elements among these folk. "),_c('br'),_c('br'),_vm._v("Orcs regard battle scars as tokens of pride and ornamental scars as things of beauty. Other scars, though, mark an orc or half-orc as a former prisoner or a disgraced exile. Any half-orc who has lived among or near orcs has scars, whether they are marks of humiliation or of pride, recounting their past exploits and injuries. ")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"the-mark-of-gruumsh"}},[_vm._v("The Mark of Gruumsh")]),_c('p',[_vm._v("The one-eyed god Gruumshlord of war and furycreated the first orcs, and even those orcs who turn away from his worship carry his blessings of might and endurance. The same is true of half-orcs. Some half-orcs hear the whispers of Gruumsh in their dreams, calling them to unleash the rage that simmers within them. Others feel Gruumshs exultation when they join in melee combat  and either exult along with him or shiver with fear and loathing. "),_c('br'),_c('br'),_vm._v("Beyond the rage of Gruumsh, half-orcs feel emotion powerfully. Rage doesnt just quicken their pulse, it makes their bodies burn. An insult stings like acid, and sadness saps their strength. But they laugh loudly and heartily, and simple pleasures  feasting, drinking, wrestling, drumming, and wild dancing  fill their hearts with joy. They tend to be short-tempered and sometimes sullen, more inclined to action than contemplation and to fighting than arguing. And when their hearts swell with love, they leap to perform acts of great kindness and compassion. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"half-orc-names"}},[_vm._v("Half-Orc Names")]),_c('p',[_vm._v("Half-orcs usually have names appropriate to the culture in which they were raised. A half-orc who wants to fit in among humans might trade an orc name for a human name. Some half-orcs with human names decide to adopt a guttural orc name because they think it makes them more intimidating. "),_c('br'),_c('br'),_vm._v("Male Orc Names: Dench, Feng, Gell, Henk, Holg, Imsh, Keth, Krusk, Mhurren, Ront, Shump, Thokk "),_c('br'),_c('br'),_vm._v("Female Orc Names: Baggi, Emen, Engong, Kansif, Myev, Neega, Ovak, Ownka, Shautha, Sutha, Vola, Volen, Yevelda ")])])])]),_c('div',{staticClass:"raza-tiefling"},[_c('div',{staticClass:"apartados",attrs:{"id":"raza-tiefling"}},[_c('h2',{staticClass:"races-title"},[_vm._v("Tiefling")]),_c('p',[_vm._v("To be greeted with stares and whispers, to suffer violence and insult on the street, to see mistrust and fear in every eye: this is the lot of the tiefling. And to twist the knife, tieflings know that this is because a pact struck generations ago infused the essence of Asmodeusoverlord of the Nine Hellsinto their bloodline. Their appearance and their nature are not their fault but the result of an ancient sin, for which they and their children and their childrens children will always be held accountable.")]),_c('div',{staticClass:"apartado-1"},[_c('h3',{attrs:{"id":"infernal-bloodline"}},[_vm._v("Infernal Bloodline")]),_c('p',[_vm._v("Tieflings are derived from human bloodlines, and in the broadest possible sense, they still look human. However, their infernal heritage has left a clear imprint on their appearance. Tieflings have large horns that take any of a variety of shapes: some have curling horns like a ram, others have straight and tall horns like a gazelles, and some spiral upward like an antelopes horns. They have thick tails, four to five feet long, which lash or coil around their legs when they get upset or nervous. Their canine teeth are sharply pointed, and their eyes are solid colorsblack, red, white, silver, or goldwith no visible sclera or pupil. Their skin tones cover the full range of human coloration, but also include various shades of red. Their hair, cascading down from behind their horns, is usually dark, from black or brown to dark red, blue, or purple.")])]),_c('div',{staticClass:"apartado-2"},[_c('h3',{attrs:{"id":"self-reliant-and-suspicious"}},[_vm._v("Self-Reliant and Suspicious")]),_c('p',[_vm._v("Tieflings subsist in small minorities found mostly in human cities or towns, often in the roughest quarters of those places, where they grow up to be swindlers, thieves, or crime lords. Sometimes they live among other minority populations in enclaves where they are treated with more respect. "),_c('br'),_c('br'),_vm._v("Lacking a homeland, tieflings know that they have to make their own way in the world and that they have to be strong to survive. They are not quick to trust anyone who claims to be a friend, but when a tieflings companions demonstrate that they trust him or her, the tiefling learns to extend the same trust to them. And once a tiefling gives someone loyalty, the tiefling is a firm friend or ally for life. ")])]),_c('div',{staticClass:"apartado-3"},[_c('h3',{attrs:{"id":"tiefling-names"}},[_vm._v("Tiefling Names")]),_c('p',[_vm._v("Tiefling names fall into three broad categories. Tieflings born into another culture typically have names reflective of that culture. Some have names derived from the Infernal language, passed down through generations, that reflect their fiendish heritage. And some younger tieflings, striving to find a place in the world, adopt a name that signifies a virtue or other concept and then try to embody that concept. For some, the chosen name is a noble quest. For others, its a grim destiny. "),_c('br'),_c('br'),_vm._v("Male Infernal Names: Akmenos, Amnon, Barakas, Damakos, Ekemon, Iados, Kairon, Leucis, Melech, Mordai, Morthos, Pelaios, Skamos, Therai "),_c('br'),_c('br'),_vm._v("Female Infernal Names: Akta, Anakis, Bryseis, Criella, Damaia, Ea, Kallista, Lerissa, Makaria, Nemeia, Orianna, Phelaia, Rieta "),_c('br'),_c('br'),_vm._v("Virtue Names: Art, Carrion, Chant, Creed, Despair, Excellence, Fear, Glory, Hope, Ideal, Music, Nowhere, Open, Poetry, Quest, Random, Reverence, Sorrow, Temerity, Torment, Weary ")])])])])])])])])
