cil. "),_c('strong',[_vm._v("Disfruta de las imgenes:")]),_vm._v(" Las imgenes te ayudarn a visualizar mejor las clases y a entender ms rpido. Si una clase te interesa mucho, puedes fijarte ms en su imagen. "),_c('p',[_c('strong',[_vm._v("Ejemplo visual (Si fuera una pgina real):")])]),_c('strong',[_vm._v("Fighter:")]),_vm._v(" [Informacin sobre qu hace un luchador] (Imagen de un luchador con espada y escudo). "),_c('strong',[_vm._v("Wizard:")]),_vm._v(" [Informacin sobre qu hace un mago] (Imagen de un mago con varita). "),_c('strong',[_vm._v("Cleric:")]),_vm._v(" [Informacin sobre qu hace un clrigo] (Imagen de un clrigo con tnica y smbolo divino). ")])])
},function (){var _vm=this,_c=_vm._self._c;return _c('section',{staticClass:"faq section",attrs:{"id":"faq"}},[_c('div',{staticClass:"container"},[_c('h1',[_vm._v("Classes")]),_c('p',[_vm._v("Class is the primary definition of what your character can do. It's more than a profession; it's "),_c('strong',[_vm._v("your character's calling")]),_vm._v(". Class shapes the way you think about the world and interact with it and your relationship with other people and powers in the multiverse. "),_c('br'),_c('br'),_vm._v(" A fighter, for example, might view the world in pragmatic terms of strategy and maneuvering, and see herself as just a pawn in a much larger game. A cleric, by contrast, might see himself as a willing servant in a god's unfolding plan or a conflict brewing among various deities. While the fighter has contacts in a mercenary company or army, the cleric might know a number of priests, paladins, and devotees who share his faith. "),_c('br'),_c('br'),_vm._v(" Your class gives you a variety of "),_c('strong',[_vm._v("special features")]),_vm._v(", such as a fighter's mastery of weapons and armor, and a wizard's spells. At low levels, your class gives you only two or three features, but as you advance in level you gain more and your existing features often improve. "),_c('br'),_c('br')]),_c('div',{staticClass:"row gy-4"},[_c('div',{staticClass:"col-lg-8",attrs:{"data-aos":"fade-up","data-aos-delay":"200"}},[_c('div',{staticClass:"faq-container"},[_c('div',{staticClass:"faq-item",attrs:{"id":"barbarian-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/barbarian.svg"),"aria-label":"Barbarian Icon","alt":"Barbarian Icon"}}),_vm._v(" Barbarian ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/barbarian.png"),"aria-label":"Barbarian Image","alt":"Barbarian Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v(" 1d12 per barbarian level ")])]),_c('li',[_c('strong',[_vm._v("HP at 1st Level:")]),_c('p',[_vm._v(" 12 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("HP at Higher Levels:")]),_c('p',[_vm._v(" 1d12 (or 7) + your Constitution modifier per barbarian level after 1st")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" Light armor, medium armor, shields | "),_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, martial weapons | "),_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Strength, Constitution | "),_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two from Animal Handling, Athletics, Intimidation, Nature, Perception, and Survival ")])]),_c('h5',[_vm._v("Equipment")]),_vm._v(" You start with the following equipment, in addition to the equipment granted by your background: "),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a greataxe or (b) any martial melee weapon")]),_c('li',[_vm._v("(a) two handaxes or (b) any simple weapon")]),_c('li',[_vm._v("An explorer's pack and four javelins")])])])]),_c('div',{staticClass:"drtxt"},[_c('h1',[_vm._v("Barbarian")]),_c('p',[_c('em',[_vm._v("A tall human tribesman strides through a blizzard, draped in fur and hefting his axe. He laughs as he charges toward the frost giant who dared poach his people's elk herd. A half-orc snarls at the latest challenger to her authority over their savage tribe, ready to break his neck with her bare hands as she did to the last six rivals. Frothing at the mouth, a dwarf slams his helmet into the face of his drow foe, then turns to drive his armored elbow into the gut of another.")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("These barbarians, different as they might be, are defined by their rage: unbridled, unquenchable, and unthinking fury. More than a mere emotion, their anger is the ferocity of a cornered predator, the unrelenting assault of a storm, the churning turmoil of the sea. For some, their rage springs from a communion with fierce animal spirits. Others draw from a roiling reservoir of anger at a world full of pain. For every barbarian, rage is a power that fuels not just a battle frenzy but also uncanny reflexes, resilience, and feats of strength. ")]),_c('h4',[_vm._v("Primal Instinct")]),_c('p',[_vm._v(" People of towns and cities take pride in how their civilized ways set them apart from animals, as if denying ones own nature was a mark of superiority. To a barbarian, though, civilization is no virtue, but a sign of weakness. The strong embrace their animal nature keen instincts, primal physicality, and ferocious rage. Barbarians are uncomfortable when hedged in by walls and crowds. They thrive in the wilds of their homelands: the tundra, jungle, or grasslands where their tribes live and hunt. Barbarians come alive in the chaos of combat. They can enter a berserk state where rage takes over, giving them superhuman strength and resilience. A barbarian can draw on this reservoir of fury only a few times without resting, but those few rages are usually sufficient to defeat whatever threats arise. ")]),_c('h4',[_vm._v("A Life of Danger")]),_c('p',[_vm._v(" Not every member of the tribes deemed barbarians by scions of civilized society has the barbarian class. A true barbarian among these people is as uncommon as a skilled fighter in a town, and he or she plays a similar role as a protector of the people and a leader in times of war. Life in the wild places of the world is fraught with peril: rival tribes, deadly weather, and terrifying monsters. Barbarians charge headlong into that danger so that their people dont have to. Their courage in the face of danger makes barbarians perfectly suited for adventuring, Wandering is often a way of life for their native tribes, and the rootless life of the adventurer is little hardship for a barbarian. Some barbarians miss the close-knit family structures of the tribe, but eventually find them replaced by the bonds formed among the members of their adventuring parties. ")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Rage")]),_c('p',[_vm._v("In battle, you fight with primal ferocity. On your turn, you can enter a rage as a bonus action. While raging, you gain the following benefits if you aren't wearing heavy armor: ")]),_c('ul',{staticClass:"skills"},[_c('li',[_vm._v("You have advantage on Strength checks and Strength saving throws. ")]),_c('li',[_vm._v("When you make a melee weapon attack using Strength, you gain a bonus to the damage roll that increases as you gain levels as a barbarian, as shown in the Rage Damage column of the Barbarian table. ")]),_c('li',[_vm._v("You have resistance to bludgeoning, piercing, and slashing damage. ")])]),_c('p',[_vm._v("If you are able to cast spells, you can't cast them or concentrate on them while raging. Your rage lasts for 1 minute. It ends early if you are knocked unconscious or if your turn ends and you haven't attacked a hostile creature since your last turn or taken damage since then. You can also end your rage on your turn as a bonus action. Once you have raged the number of times shown for your barbarian level in the Rages column of the Barbarian table, you must finish a long rest before you can rage again. ")]),_c('h4',[_vm._v("Unarmored Defense")]),_c('p',[_vm._v("While you are not wearing any armor, your Armor Class equals 10 + your Dexterity modifier + your Constitution modifier. You can use a shield and still gain this benefit. ")]),_c('h4',[_vm._v("Reckless Attack")]),_c('p',[_vm._v(" 2nd level: You can throw aside all concern for defense to attack with fierce desperation. When you make your first attack on your turn, you can decide to attack recklessly. Doing so gives you advantage on melee weapon attack rolls using Strength during this turn, but attack rolls against you have advantage until your next turn. ")]),_c('h4',[_vm._v("Danger Sense")]),_c('p',[_vm._v("2nd level: You gain an uncanny sense of when things nearby aren't as they should be, giving you an edge when you dodge away from danger. You have advantage on Dexterity saving throws against effects that you can see, such as traps and spells. To gain this benefit, you cant be blinded, deafened, or incapacitated.")]),_c('h4',[_vm._v("Primal Path")]),_c('p',[_vm._v("3rd level: You choose a path that shapes the nature of your rage. Choose the Path of the Berserker or the Path of the Totem Warrior, both detailed at the end of the class description. Your choice grants you features at 3rd level and again at 6th, 10th, and 14th levels. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Extra Attack")]),_c('p',[_vm._v("5th level: You can attack twice, instead of once, whenever you take the Attack action on your turn.")]),_c('h4',[_vm._v("Fast Movemement")]),_c('p',[_vm._v("5th level: Your speed increases by 10 feet while you aren't wearing heavy armor.")]),_c('h4',[_vm._v("Feral Instinct")]),_c('p',[_vm._v("7th level: Your instincts are so honed that you have advantage on initiative rolls. Additionally, if you are surprised at the beginning of combat and aren't incapacitated, you can act normally on your first turn, but only if you enter your rage before doing anything else on that turn. ")]),_c('h4',[_vm._v("Brutal Critical")]),_c('p',[_vm._v("9th level: You can roll one additional weapon damage die when determining the extra damage for a critical hit with a melee attack. This increases to two additional dice at 13th level and three additional dice at 17th level.")]),_c('h4',[_vm._v("Relentless Rage")]),_c('p',[_vm._v("11th level: Your rage can keep you fighting despite grievous wounds. If you drop to 0 hit points while you're raging and dont die outright, you can make a DC 10 Constitution saving throw. If you succeed, you drop to 1 hit point instead. Each time you use this feature after the first, the DC increases by 5. When you finish a short or long rest, the DC resets to 10.")]),_c('h4',[_vm._v("Persistent Rage")]),_c('p',[_vm._v("15th level: Your rage is so fierce that it ends early only if you fall unconscious or if you choose to end it.")]),_c('h4',[_vm._v("Indomitable Might")]),_c('p',[_vm._v("18th level: If your total for a Strength check is less than your Strength score, you can use that score in place of the total.")]),_c('h4',[_vm._v("Primal Champion")]),_c('p',[_vm._v("20th level: You embody the power of the wilds. Your Strength and Constitution scores increase by 4. Your maximum for those scores is now 24.")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Primal Paths")]),_c('p',[_vm._v("Rage burns in every barbarians heart, a furnace that drives him or her toward greatness. Different barbarians attribute their rage to different sources, however. For some, it is an internal reservoir where pain, grief, and anger are forged into a fury hard as steel. Others see it as a spiritual blessing, a gift of a totem animal.")]),_c('h4',[_vm._v("Path of the Berserker")]),_c('p',[_vm._v("For some barbarians, rage is a means to an endthat end being violence. The Path of the Berserker is a path of untrammeled fury, slick with blood. As you enter the berserkers rage, you thrill in the chaos of battle, heedless of your own health or well-being.")]),_c('h5',[_vm._v("Frenzy")]),_c('p',[_vm._v("Starting when you choose this path at 3rd level, you can go into a frenzy when you rage. If you do so, for the duration of your rage you can make a single melee weapon attack as a bonus action on each of your turns after this one.")]),_c('h5',[_vm._v("Mindless Rage")]),_c('p',[_vm._v("Beginning at 6th level, you cant be charmed or frightened while raging. If you are charmed or frightened when you enter your rage, the effect is suspended for the duration of the rage.")]),_c('h5',[_vm._v("intimidating Presence")]),_c('p',[_vm._v("Beginning at 10th level, you can use your action to frighten someone with your menacing presence. When you do so, choose one creature that you can see within 30 feet of you. If the creature can see or hear you, it must succeed on a Wisdom saving throw (DC equal to 8 + your proficiency bonus + your Charisma modifier) or be frightened of you until the end of your next turn. On subsequent turns, you can use your action to extend the duration of this effect on the frightened creature until the end of your next turn. This effect ends if the creature ends its turn out of line of sight or more than 60 feet away from you. If the creature succeeds on its saving throw, you can't use this feature on that creature again for 24 hours.")]),_c('h5',[_vm._v("Retaliation")]),_c('p',[_vm._v("Starting at 14th level, when you take damage from a creature that is within 5 feet of you. you can use your reaction to make a melee weapon attack against that creature. ")]),_c('h4',[_vm._v("Path of the Totem Warrior")]),_c('p',[_vm._v("The Path of the Totem Warrior is a spiritual journey, as the barbarian accepts a spirit animal as guide, protector. and inspiration. In battle, your totem spirit fills you with supernatural might, adding magical fuel to your barbarian rage. Most barbarian tribes consider a totem animal to be kin to a particular clan. In such cases, it is unusual for an individual to have more than one totem animal spirit, though exceptions exist.")]),_c('h5',[_vm._v("Spirit Seeker")]),_c('p',[_vm._v("Yours is a path that seeks attunement with the natural world, giving you a kinship with beasts. At 3rd level when you adopt this path, you gain the ability to cast the beast sense and speak with animals spells. but only as rituals.")]),_c('h5',[_vm._v("Totem Spirit")]),_c('p',[_vm._v("Yours is a path that seeks attunement with the natural world, giving you a kinship with beasts. At 3rd level when you adopt this path, you gain the ability to cast the beast sense and speak with animals spells. but only as rituals.")]),_c('h5',[_vm._v("Spirit Seeker")]),_c('p',[_vm._v("At 3rd level, when you adopt this path, you choose a totem spirit and gain its feature. You must make or acquire a physical totem object- an amulet or similar adornmentthat incorporates fur or feathers, claws. teeth, or bones of the totem animal. At your option, you also gain minor physical attributes that are reminiscent of your totem spirit. For example, if you have a bear totem spirit, you might be unusually hairy and thickskinned, or if your totem is the eagle, your eyes turn bright yellow. Your totem animal might be an animal related to those listed here but more appropriate to your homeland. For example. you could choose a hawk or vulture in place of an eagle. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Bear ")]),_vm._v(". While raging. you have resistance to all damage except psychic damage. The spirit of the bear makes you tough enough to stand up to any punishment.")]),_c('li',[_c('strong',[_vm._v("Eagle ")]),_vm._v(". While you're raging and aren't wearing heavy armor, other creatures have disadvantage on opportunity attack rolls against you, and you can use the Dash action as a bonus action on your turn. The spirit of the eagle makes you into a predator who can weave through the fray with ease.")]),_c('li',[_c('strong',[_vm._v("Wolf ")]),_vm._v(". While you're raging, your friends have advantage on melee attack rolls against any creature within 5 feet of you that is hostile to you. The spirit of the wolf makes you a leader of hunters.")])])]),_c('h5',[_vm._v("Aspect of the Beast")]),_c('p',[_vm._v("At 6th level, you gain a magical benefit based on the totem animal of your choice. You can choose the same animal you selected at 3rd level or a different one. "),_c('strong',[_vm._v("Bear ")]),_vm._v(". You gain the might of a bear. Your carrying capacity (including maximum load and maximum lift) is doubled, and you have advantage on Strength checks made to push, pull, lift, or break objects. "),_c('strong',[_vm._v("Eagle ")]),_vm._v(". You gain the eyesight of an eagle. You can see up to 1 mile away with no difficulty, able to discern even fine details as though looking at something no more than 100 feet away from you. Additionally, dim light doesn't impose disadvantage on your Wisdom (Perception) checks. "),_c('strong',[_vm._v("Wolf ")]),_vm._v(". You gain the hunting sensibilities of a wolf. You can track other creatures while traveling at a fast pace, and you can move stealthily while traveling at a normal pace (see chapter 8 for rules on travel pace). ")]),_c('h5',[_vm._v("Spirit Walker")]),_c('p',[_vm._v("At 10th level, you can cast the commune with nature spell, but only as a ritual. When you do so, a spiritual version of one of the animals you chose for Totem Spirit or Aspect of the Beast appears to you to convey the information you seek. ")]),_c('h5',[_vm._v("Totemic Attunement")]),_c('p',[_vm._v("At 14th level, you gain a magical benefit based on a totem animal of your choice. You can choose the same animal you selected previously or a different one. "),_c('strong',[_vm._v("Bear ")]),_vm._v(". While you're raging, any creature within 5 feet of you thats hostile to you has disadvantage on attack rolls against targets other than you or another character with this feature. An enemy is immune to this effect if it can't see or hear you or if it cant be frightened. "),_c('strong',[_vm._v("Eagle ")]),_vm._v(". While raging, you have a flying speed equal to your current walking speed. This benefit works only in short bursts; you fall if you end your turn in the air and nothing else is holding you aloft. "),_c('strong',[_vm._v("Wolf ")]),_vm._v(". While you're raging, you can use a bonus action on your turn to knock a Large or smaller creature prone when you hit it with melee weapon attack. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"bard-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/bard.svg"),"aria-label":"Bard Icon","alt":"Bard Icon"}}),_c('h2',[_vm._v("Bard")])])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/bard.png"),"aria-label":"Bard Image","alt":"Bard Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d8 per bard level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("8 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d8 (or 5) + your Constitution modifier per bard level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" Light armor | "),_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, hand crossbows, longswords, rapiers, shortswords | "),_c('strong',[_vm._v("Tools:")]),_vm._v(" Three musical instruments of your choice ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Dexterity, Charisma | "),_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose any three ")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a rapier, (b) a longsword, or (c) any simple weapon")]),_c('li',[_vm._v("(a) a diplomats pack or (b) an entertainer's pack")]),_c('li',[_vm._v("(a) a lute or (b) any other musical instrument")]),_c('li',[_vm._v("Leather armor and a dagger")])])])]),_c('div',{staticClass:"drtxt"},[_c('h1',[_vm._v("Bard")]),_c('p',[_c('em',[_vm._v("Humming as she traces her fingers over an ancient monument in a long-forgotten ruin, a half-elf in rugged leathers finds knowledge springing into her mind, conjured forth by the magic of her songknowledge of the people who constructed the monument and the mythic saga it depicts. A stern human warrior bangs his sword rhythmically against his scale mail, setting the tempo for his war chant and exhorting his companions to bravery and heroism. The magic of his song fortifies and emboldens them. Laughing as she tunes her cittern, a gnome weaves her subtle magic over the assembled nobles, ensuring that her companions words will be well received. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Whether scholar, skald, or scoundrel, a bard weaves magic through words and music to inspire allies, demoralize foes, manipulate minds, create illusions, and even heal wounds. ")]),_c('h4',[_vm._v("Music and Magic")]),_c('p',[_vm._v("In the worlds of D&D, words and music are not just vibrations of air, but vocalizations with power all their own. The bard is a master of song, speech, and the magic they contain. Bards say that the multiverse was spoken into existence, that the words of the gods gave it shape, and that echoes of these primordial Words of Creation still resound throughout the cosmos. The music of bards is an attempt to snatch and harness those echoes, subtly woven into their spells and powers.")]),_c('p',[_vm._v("The greatest strength of bards is their sheer versatility. Many bards prefer to stick to the sidelines in combat, using their magic to inspire their allies and hinder their foes from a distance. But bards are capable of defending themselves in melee if necessary. using their magic to bolster their swords and armor. Their spells lean toward charms and illusions rather than blatantly destructive spells. They have a wide-ranging knowledge of many subjects and a natural aptitude that lets them do almost anything well. Bards become masters of the talents they set their minds to perfecting, from musical performance to esoteric knowledge.")]),_c('h4',[_vm._v("Learning from Experience")]),_c('p',[_vm._v("True bards are not common in the world. Not every minstrel singing in a tavern or jester cavorting in a royal court is a bard. Discovering the magic hidden in music requires hard study and some measure of natural talent that most troubadours and jongleurs lack. It can be hard to spot the difference between these performers and true bards, though. A bards life is spent wandering across the land gathering lore, telling stories, and living on the gratitude of audiences, much like any other entertainer. But a depth of knowledge, a level of musical skill, and a touch of magic set bards apart from their fellows.")]),_c('p',[_vm._v("Only rarely do bards settle in one place for long, and their natural desire to travelto find new tales to tell, new skills to learn, and new discoveries beyond the horizonmakes an adventuring career a natural calling. Every adventure is an opportunity to learn, practice a variety of skills, enter long-forgotten tombs, discover lost works of magic, decipher old tomes, travel to strange places, or encounter exotic creatures. Bards love to accompany heroes to witness their deeds firsthand. A bard who can tell an awe-inspiring story from personal experience earns renown among other bards. Indeed, after telling so many stories about heroes accomplishing mighty deeds, many bards take these themes to heart and assume heroic roles themselves.")])])]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Spellcasting")]),_c('p',[_vm._v("You have learned to untangle and reshape the fabric of reality in harmony with your wishes and music. Your spells are part of your vast repertoire, magic that you can tune to different situations.")]),_c('h4',[_vm._v("Bardic Inspiration")]),_c('p',[_vm._v("You can inspire others through stirring words or music. To do so, you use a bonus action on your turn to choose one creature other than yourself within 60 feet of you who can hear you. That creature gains one Bardic Inspiration die, a d6. Once within the next 10 minutes, the creature can roll the die and add the number rolled to one ability check, attack roll, or saving throw it makes. The creature can wait until after it rolls the d20 before deciding to use the Bardic Inspiration die, but must decide before the DM says whether the roli succeeds or fails. Once the Bardic Inspiration die is rolled, it is lost. A creature can have only one Bardic Inspiration die at a time.")]),_c('p',[_vm._v("You can use this feature a number of times equal to your Charisma modifier (a minimum of once). You regain any expended uses when you finish a long rest. Your Bardic Inspiration die changes when you reach certain levels in this class. The die becomes a d8 at 5th level, a d10 at 10th level, and a d12 at 15th level.")]),_c('h4',[_vm._v("Jack of All Trades")]),_c('p',[_vm._v("2nd level: You can add half your proficiency bonus, rounded down, to any ability check you make that doesnt already include your proficiency bonus. ")]),_c('h4',[_vm._v("Song of rest")]),_c('p',[_vm._v("2nd level: You can use soothing music or oration to help revitalize your wounded allies during a short rest. If you or any friendly creatures who can hear your performance regain hit points at the end of the short rest, each of those creatures regains an extra 1d6 hit points. The extra hit points increase when you reach certain levels in this class: to 1d8 at 9th level, to 1d10 at 13th level, and to 1d12 at 17th level.")]),_c('h4',[_vm._v("Bard College")]),_c('p',[_vm._v("3rd level: You delve into the advanced techniques of a bard college of your choice: the College of Lore or the College of Valor, both detailed at the end of the class description. Your choice grants you features at 3rd level and again at 6th and 14th level. ")]),_c('h4',[_vm._v("Expertise")]),_c('p',[_vm._v("3rd level: Choose two of your skill proficiencies. Your proficiency bonus is doubled for any ability check you make that uses either of the chosen proficiencies. 10th level: You can choose another two skill proficiencies to gain this benefit.")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("4th , 8th, 12th, 16th, and 19th level: You can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Font of Inspiration")]),_c('p',[_vm._v(" 5th level: You regain all of your expended uses of Bardic Inspiration when you finish a short or long rest.")]),_c('h4',[_vm._v("Countercharm")]),_c('p',[_vm._v(" 6th level: You gain the ability to use musical notes or words of power to disrupt mind-influencing effects. As an action, you can start a performance that lasts until the end of your next turn. During that time, you and any friendly creatures within 30 feet of you have advantage on saving throws against being frightened or charmed. Acreature must be able to hear you to gain this benefit. The performance ends early if you are incapacitated or silenced or if you voluntarily end it (no action required). ")]),_c('h4',[_vm._v("Magical Secrets")]),_c('p',[_vm._v(" 10th level: You have plundered magical knowledge from a wide spectrum of disciplines. Choose two spells from any class, including this one. A spell you choose must be of a level you can cast, as shown on the Bard table, or a cantrip. The chosen spells count as bard spells for you and are included in the number in the Spells Known column of the Bard table. You learn two additional spells from any class at 14th level and again at 18th level. ")]),_c('h4',[_vm._v("Superior Inspiration")]),_c('p',[_vm._v(" 20th level: When you roll initiative and have no uses of Bardic Inspiration left, you regain one use.")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Bard Colleges")]),_c('p',[_vm._v("The way of a bard is gregarious. Bards seek each other out to swap songs and stories, boast of their accomplishments, and share their knowledge. Bards form loose associations, which they call colleges, to facilitate their gatherings and preserve their traditions")]),_c('h4',[_vm._v("College of Lore")]),_c('p',[_vm._v("Bards of the College of Lore know something about most things, collecting bits of knowledge from sources as diverse as scholarly tomes and peasant tales. Whether singing folk ballads in taverns or elaborate compositions in royal courts, these bards use their gifts to hold audiences spellbound. When the applause dies down, the audience members might find themselves questioning everything they held to be true, from their faith in the priesthood of the local temple to their loyalty to the king. The loyalty of these bards lies in the pursuit of beauty and truth, not in fealty to a monarch or following the tenets of a deity. A noble who keeps such a bard asa herald or advisor knows that the bard would rather be honest than politic. The colleges members gather in libraries and sometimes in actual colleges, complete with classrooms and dormitories, to share their lore with one another. They also meet at festivals or affairs of state, where they can expose corruption, unravel lies, and poke fun at selfimportant figures of authority.")]),_c('h5',[_vm._v("Bonus Proficiencies")]),_c('p',[_vm._v("When you join the College of Lore at 3rd level, you gain proficiency with three skills of your choice.")]),_c('h5',[_vm._v("Cutting Words")]),_c('p',[_vm._v("Also at 3rd level, you learn how to use your wit to distract, confuse, and otherwise sap the confidence and competence of others. When a creature that you can see within 60 feet of you makes an attack roll, an ability check, or a damage roll, you can use your reaction to expend one of your uses of Bardic Inspiration, rolling a Bardic Inspiration die and subtracting the number rolled from the creatures roll. You can choose to use this feature after the creature makes its roll, but before the DM determines whether the attack roll or ability. check succeeds or fails, or before the creature deals its damage. The creature is immune if it cant hear you or if its immune to being charmed. ")]),_c('h5',[_vm._v("Additional Magical Secrets")]),_c('p',[_vm._v("At 6th level, you learn two spells of your choice from any class. A spell you choose must be of a level you can cast. as shown on the Bard table, or a cantrip. The chosen spells count as bard spells for you but dont count against the number of bard spells you know.")]),_c('h5',[_vm._v("Peerless Skill")]),_c('p',[_vm._v("Starting at 14th level, when you make an ability check. you can expend one use of Bardic Inspiration. Roll a Bardic Inspiration die and add the number rolled to your ability check. You can choose to do so after you roil the die for the ability check, but before the DM tells you whether you succeed or fail. ")]),_c('h4',[_vm._v("College of Valor")]),_c('p',[_vm._v("Bards of the College of Valor are daring skalds whose tales keep alive the memory of the great heroes of the past, and thereby inspire a new generation of heroes. These bards gather in mead halls or around great bonfires to sing the deeds of the mighty, both past and present. They travel the land to witness great events firsthand and to ensure that the memory of those events doesnt pass from the world. With their songs. they inspire others to reach the same heights of accomplishment as the heroes of old.")]),_c('h5',[_vm._v("Bonus Proficiencies")]),_c('p',[_vm._v("When you join the College of Valor at 3rd level, you gain proficiency with medium armor, shields, and martial weapons.")]),_c('h5',[_vm._v("Combat Inspiration")]),_c('p',[_vm._v("Also at 3rd level, you learn to inspire others in battle. A creature that has a Bardic Inspiration die from you can roll that die and add the number rolled to a weapon damage roll it just made. Alternatively, when an attack roll s made against the creature, it can use its reaction to roll the Bardic Inspiration die and add the number rolled to its AC against that attack, after seeing the roll but before knowing whether it hits or misses.")]),_c('h5',[_vm._v("Extra Attack")]),_c('p',[_vm._v("Starting at 6th level, you can attack twice, instead of once, whenever you take the Attack action on your turn. ")]),_c('h5',[_vm._v("Battle Magic")]),_c('p',[_vm._v("At 14th level, you have mastered the art of weaving spellcasting and weapon use into a single harmonious act. When you use your action to cast a bard spell, you can make one weapon attack as a bonus action. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"cleric-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/cleric.svg"),"aria-label":"Cleric Icon","alt":"Cleric Icon"}}),_vm._v(" Cleric ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/cleric.png"),"aria-label":"Cleric Image","alt":"Cleric Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v(" 1d8 per cleric level ")])]),_c('li',[_c('strong',[_vm._v("HP at 1st Level:")]),_c('p',[_vm._v(" 8 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("HP at Higher Levels:")]),_c('p',[_vm._v(" 1d8 (or 5) + your Constitution modifier per cleric level after 1st")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" Light armor, medium armor, shields | "),_c('strong',[_vm._v("Weapons:")]),_vm._v(" All simple weapons | "),_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Wisdom, Charisma | "),_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two from History, Insight, Medicine, Persuasion, and Religion ")])]),_c('h5',[_vm._v("Equipment")]),_vm._v(" You start with the following equipment, in addition to the equipment granted by your background: "),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a mace or (b) a warhammer (if proficient)")]),_c('li',[_vm._v("(a) scale mail, (b) leather armor, or (c) chain mail (if proficient) ")]),_c('li',[_vm._v("(a) a light crossbow and 20 bolts or (b) any simple weapon")]),_c('li',[_vm._v("(a) a priests pack or (b) an explorers pack")]),_c('li',[_vm._v("A shield and a holy symbol")])])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Arms and eyes upraised toward the sun and a prayer on his lips, an elf begins to glow with an inner light that spills out to heal his battle-worn companions. Chanting a song of glory, a dwarf swings his axe in wide swaths to cut through the ranks of orcs arrayed against him, shouting praise to the gods with every foe's fall. Calling down a curse upon the forces of undeath, a human lifts her holy symbol as light pours from it to drive back the zombies crowding in on her companions.")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Clerics are intermediaries between the mortal world and the distant planes of the gods. As varied as the gods they serve, clerics strive to embody the handiwork of their deities. No ordinary priest, a cleric is imbued with divine magic. ")]),_c('h4',[_vm._v("Healers and Warriors")]),_c('p',[_vm._v(" Divine magic, as the name suggests, is the power of the gods, flowing from them into the world. Clerics are conduits for that power, manifesting it as miraculous effects. The gods dont grant this power to everyone who seeks it, but only to those chosen to fulfill a high calling. Harnessing divine magic doesnt rely on study or training. A cleric might learn formulaic prayers and ancient rites, but the ability to cast cleric spells relies on devotion and an intuitive sense of a deitys wishes. ")]),_c('p',[_vm._v("Clerics combine the helpful magic of healing and inspiring their allies with spells that harm and hinder foes. They can provoke awe and dread, lay curses of plague or poison, and even call down flames from heaven to consume their enemies. For those evildoers who will benefit most from a mace to the head, clerics depend on their combat training to let them wade into melee with the power of the gods on their side. ")]),_c('h4',[_vm._v("Divine Agents")]),_c('p',[_vm._v(" Not every acolyte or officiant at a temple or shrine is a cleric. Some priests are called to a simple life of temple service, carrying out their gods will through prayer and sacrifice, not by magic and strength of arms. In some cities, priesthood amounts to a political office, viewed as a stepping stone to higher positions of authority and involving no communion with a god at all. True clerics are rare in most hierarchies. ")]),_c('p',[_vm._v(" When a cleric takes up an adventuring life, it is usually because his or her god demands it. Pursuing the goals of the gods often involves braving dangers beyond the walls of civilization, smiting evil or seeking holy relics in ancient tombs. Many clerics are also expected to protect their deities worshipers, which can mean fighting rampaging orcs, negotiating peace between warring nations, or sealing a portal that would allow a demon prince to enter the world. Most adventuring clerics maintain some connection to established temples and orders of their faiths. A temple might ask for a clerics aid, or a high priest might be ina position to demand it. ")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Spellcasting")]),_c('p',[_vm._v("As a conduit for divine power, you can cast cleric spells. ")]),_c('h4',[_vm._v("Divine Domain")]),_c('p',[_vm._v("Choose one domain related to your deity: Knowledge, Life, Light, Nature, Tempest, Trickery, or War. Each domain is detailed at the end of the class description, and each one provides examples of gods associated with it. Your choice grants you domain spells and other features when you choose it at 1st level. It also grants you additional ways to use Channel Divinity when you gain that feature at 2nd level, and additional benefits at 6th, 8th, and 17th levels. ")]),_c('h4',[_vm._v("Channel Divinity")]),_c('p',[_vm._v("At 2nd level, you gain the ability to channel divine energy directly from your deity, using that energy to fuel magical effects. You start with two such effects: Turn Undead and an effect determined by your domain. Some domains grant you additional effects as you advance in levels, as noted in the domain description. When you use your Channel Divinity, you choose which effect to create. You must then finish a short or long rest to use your Channel Divinity again. Some Channel Divinity effects require saving throws. When you use such an effect from this class, the DC equals your cleric spell save DC. Beginning at 6th level, you can use your Channel Divinity twice between rests, and beginning at 18th level, you can use it three times between rests. When you finish a short or long rest, you regain your expended uses.")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Destroy Undead")]),_c('p',[_vm._v("Starting at 5th level, when an undead fails its saving throw against your Turn Undead feature, the creature is instantly destroyed if its challenge rating is at or below a certain threshold, as shown in the Destroy Undead table.")]),_c('h4',[_vm._v("Divine Intervention")]),_c('p',[_vm._v(" Beginning at 10th level, you can call on your deity to intervene on your behalf when your need is great. Imploring your deitys aid requires you to use your action. Describe the assistance you seek, and roll percentile dice. If you roll a number equal to or lower than your cleric level, your deity intervenes. The DM chooses the nature of the intervention; the effect of any cleric spell or cleric domain spell would be appropriate. If your deity intervenes, you cant use this feature again for 7 days. Otherwise, you can use it again after you finish a long rest. At 20th level, your call for intervention succeeds automatically, no roll required. ")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Divine Domains")]),_c('p',[_vm._v("In a pantheon, every deity has influence over different aspects of mortal life and civilization, called a deitys domain. All the domains over which a deity has influence are called the deitys portfolio. For example, the portfolio of the Greek god Apollo includes the domains of Knowledge, Life, and Light. As a cleric, you choose one aspect of your deitys portfolio to emphasize, and you are granted powers related to that domain.")]),_c('p',[_vm._v(" Your choice might correspond to a particular sect dedicated to your deity. Apollo, for example, could be worshiped in one region as Phoebus (radiant) Apollo, emphasizing his influence over the Light domain, and in a different place as Apollo Acesius (healing), emphasizing his association with the Life domain. Alternatively, your choice of domain could simply be a matter of personal preference, the aspect of the deity that appeals to you most. Each domains description gives examples of deities who have influence over that domain. Gods are included from the worlds of the Forgotten Realms, Greyhawk, Dragonlance, and Eberron campaign settings, as well as from the Celtic, Greek, Norse, and Egyptian pantheons of antiquity. ")]),_c('h4',[_vm._v("Knowledge Domain")]),_c('p',[_vm._v("The gods of knowledgeincluding Oghma, Boccob, Gilean, Aureon, and Thothvalue learning and understanding above all. Some teach that knowledge is to be gathered and shared in libraries and universities, or promote the practical knowledge of craft and invention. Some deities hoard knowledge and keep its secrets to themselves. And some promise their followers that they will gain tremendous power if they unlock the secrets of the multiverse. Followers of these gods study esoteric lore, collect old tomes, delve into the secret places of the earth, and learn all they can. Some gods of knowledge promote the practical knowledge of craft and invention, including smith deities like Gond, Reorx, Onatar, Moradin, Hephaestus, and Goibhniu. ")]),_c('h5',[_vm._v("Blessings of Knowledge")]),_c('p',[_vm._v("At 1st level, you learn two languages of your choice. You also become proficient in your choice of two of the following skills: Arcana, History, Nature, or Religion. Your proficiency bonus is doubled for any ability check you make that uses either of those skills.")]),_c('h5',[_vm._v("Channel Divinity: Knowledge of the Ages")]),_c('p',[_vm._v("BStarting at 2nd level, you can use your Channel Divinity to tap into a divine well of knowledge. As an action, you choose one skill or tool. For 10 minutes, you have proficiency with the chosen skill or tool.")]),_c('h5',[_vm._v("Channel Divinity: Read Thoughts")]),_c('p',[_vm._v(" At 6th level, you can use your Channel Divinity to read a creatures thoughts. You can then use your access to the creatures mind to command it. ")]),_c('h5',[_vm._v("Potent Spellcasting")]),_c('p',[_vm._v("Starting at 8th level, you add your Wisdom modifier to the damage you deal with any cleric cantrip. ")]),_c('h5',[_vm._v("Visions of the Past")]),_c('p',[_vm._v(" Starting at 17th level, you can call up visions of the past that relate to an object you hold or your immediate surroundings. You spend at least 1 minute in meditation and prayer, then receive dreamlike, shadowy glimpses of recent events. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Object Reading ")]),_vm._v(". Holding an object as you meditate, you can see visions of the objects previous owner. After meditating for 1 minute, you learn how the owner acquired and lost the object, as well as the most recent significant event involving the object and that owner. If the object was owned by another creature in the recent past (within a number of days equal to your Wisdom score), you can spend 1 additional minute for each owner to learn the same information about that creature..")]),_c('li',[_c('strong',[_vm._v("Area Reading ")]),_vm._v(". As you meditate, you see visions of recent events in your immediate vicinity (a room, street, tunnel, clearing, or the like, up to a 50-foot cube), going back a number of days equal to your Wisdom score. For each minute you meditate, you learn about one significant event, beginning with the most recent. Significant events typically involve powerful emotions, such as battles and betrayals, marriages and murders, births and funerals. However, they might also include more mundane events that are nevertheless important in your current situation.")])])]),_c('h4',[_vm._v("Life Domain")]),_c('p',[_vm._v(" The Life domain focuses on the vibrant positive energyone of the fundamental forces of the universe that sustains all life. The gods of life promote vitality and health through healing the sick and wounded, caring for those in need, and driving away the forces of death and undeath. Almost any non-evil deity can claim influence over this domain, particularly agricultural deities (such as Chauntea, Arawai, and Demeter), sun gods (such as Lathander, Pelor, and Re-Horakhty), gods of healing or endurance (such as Ilmater, Mishakal, Apollo, and Diancecht), and gods of home and community (such as Hestia, Hathor, and Boldrei). ")]),_c('h5',[_vm._v("Bonus Proficiency")]),_c('p',[_vm._v("When you choose this domain at 1st level, you gain proficiency with heavy armor.")]),_c('h5',[_vm._v("Disciple of Life")]),_c('p',[_vm._v("Starting at 1st level, your healing spells are more effective. Whenever you use a spell of 1st level or higher to restore hit points to a creature, the creature regains additional hit points equal to 2 + the spells level.")]),_c('h5',[_vm._v("Channel Divinity: Preserve Life")]),_c('p',[_vm._v(" Starting at 2nd level, you can use your Channel Divinity to heal the badly injured. As an action, you present your holy symbol and evoke healing energy that can restore a number of hit points equal to five times your cleric level. Choose any creatures within 30 feet of you, and divide those hit points among them. This feature can restore a creature to no more than half of its hit point maximum. You cant use this feature on an undead or a construct. ")]),_c('h5',[_vm._v("Blessed Healer")]),_c('p',[_vm._v("Beginning at 6th level, the healing spells you cast on others heal you as well. When you cast a spell of 1st level or higher that restores hit points to a creature other than you, you regain hit points equal to 2 + the spells level. ")]),_c('h5',[_vm._v("Divine Strike")]),_c('p',[_vm._v("At 8th level, you gain the ability to infuse your weapon strikes with divine energy. Once on each of your turns when you hit a creature with a weapon attack, you can cause the attack to deal an extra 1d8 radiant damage to the target. When you reach 14th level, the extra damage increases to 2d8. ")]),_c('h5',[_vm._v("Supreme Healing")]),_c('p',[_vm._v("Starting at 17th level, when you would normally roll one or more dice to restore hit points with a spell,you instead use the highest number possible for each die. For example, instead of restoring 2d6 hit points toa creature, you restore 12. ")]),_c('h4',[_vm._v("Light Domain")]),_c('p',[_vm._v(" Gods of lightincluding Helm, Lathander, Pholtus, Branchala, the Silver Flame, Belenus, Apollo, and Re-Horakhtypromote the ideals of rebirth and renewal, truth, vigilance, and beauty, often using the symbol of the sun. Some of these gods are portrayed as the sun itself or as a charioteer who guides the sun across the sky. Others are tireless sentinels whose eyes pierce every shadow and see through every deception. Some are deities of beauty and artistry, who teach that art is a vehicle for the soul's improvement. Clerics of a god of light are enlightened souls infused with radiance and the power of their gods discerning vision, charged with chasing away lies and burning away darkness. ")]),_c('h5',[_vm._v("Bonus Cantrip")]),_c('p',[_vm._v("When you choose this domain at 1st level, you gain the light cantrip if you dont already know it.")]),_c('h5',[_vm._v("Warding Flare")]),_c('p',[_vm._v(" 1st level: You can interpose divine light between yourself and an attacking enemy. When you are attacked by a creature within 30 feet of you that you can see, you can use your reaction to impose disadvantage on the attack roll, causing light to flare before the attacker before it hits or misses. An attacker that cant be blinded is immune to this feature. You can use this feature a number of times equal to your Wisdom modifier (a minimum of once). You regain all expended uses when you finish a long rest. ")]),_c('h5',[_vm._v("Channel Divinity: Radiance of the Dawn")]),_c('p',[_vm._v(" 2nd level: You can use your Channel Divinity to harness sunlight, banishing darkness and dealing radiant damage to your foes. As an action, you present your holy symbol, and any magical darkness within 30 feet of you is dispelled. Additionally, each hostile creature within 30 feet of you must make a Constitution saving throw. A creature takes radiant damage equal to 2d10 + your cleric level on a failed saving throw, and half as much damage on a successful one. A creature that has total cover from you is not affected.")]),_c('h5',[_vm._v("Improved Flare")]),_c('p',[_vm._v("6th level: You can also use your Warding Flare feature when a creature that you can see within 30 feet of you attacks a creature other than you.")]),_c('h5',[_vm._v("Potent Spellcasting")]),_c('p',[_vm._v("8th level: You add your Wisdom modifier to the damage you deal with any cleric cantrip")]),_c('h5',[_vm._v("Corona of Light")]),_c('p',[_vm._v("17th level: You can use your action to activate an aura of sunlight that lasts for 1 minute or until you dismiss it using another action. You emit bright light in a 60-foot radius and dim light 30 feet beyond that. Your enemies in the bright light have disadvantage on saving throws against any spell that deals fire or radiant damage.")]),_c('h4',[_vm._v("Nature Domain")]),_c('p',[_vm._v(" Gods of nature are as varied as the natural world itself, from inscrutable gods of the deep forests (such as Silvanus, Obad-Hai, Chislev, Balinor, and Pan) to friendly deities associated with particular springs and groves (such as Eldath). Druids revere nature as a whole and might serve one of these deities, practicing mysterious rites and reciting all-but-forgotten prayers in their own secret tongue. But many of these gods have clerics as well, champions who take a more active role in advancing the interests of a particular nature god. These clerics might hunt the evil monstrosities that despoil the woodlands, bless the harvest of the faithful, or wither the crops of those who anger their gods. ")]),_c('h5',[_vm._v("Acolyte of Nature")]),_c('p',[_vm._v("1st level: You learn one druid cantrip of your choice. You also gain proficiency in one of the following skills of your choice: Animal Handling, Nature, or Survival.")]),_c('h5',[_vm._v("Bonus Proficiency")]),_c('p',[_vm._v(" 1st level: You gain proficiency with heavy armor. ")]),_c('h5',[_vm._v("Channel Divinity: Charm Animals and Plants")]),_c('p',[_vm._v(" 2nd level: You can use your Channel Divinity to charm animals and plants. As an action, you present your holy symbol and invoke the name of your deity. Each beast or plant creature that can see you within 30 feet of you must make a Wisdom saving throw. If the creature fails its saving throw, it is charmed by you for 1 minute or until it takes damage. While it is charmed by you, it is friendly to you and other creatures you designate.")]),_c('h5',[_vm._v("Dampen Elements")]),_c('p',[_vm._v("6th level: When you or a creature within 30 feet of you takes acid, cold, fire, lightning, or thunder damage, you can use your reaction to grant resistance to the creature against that instance of the damage.")]),_c('h5',[_vm._v("Divine Strike")]),_c('p',[_vm._v("8th level: You gain the ability to infuse your weapon strikes with divine energy. Once on each of your turns when you hit a creature with a weapon attack, you can cause the attack to deal an extra 1d8 cold, fire, or lightning damage (your choice) to the target. When you reach 14th level, the extra damage increases to 2d8.")]),_c('h5',[_vm._v("Master of Nature")]),_c('p',[_vm._v("17th level: You gain the ability to command animals and plant creatures. While creatures are charmed by your Charm Animals and Plants feature, you can take a bonus action on your turn to verbally command what each of those creatures will do on its next turn.. ")]),_c('h4',[_vm._v("Tempest Domain")]),_c('p',[_vm._v(" Gods whose portfolios include the Tempest domain including Talos, Umberlee, Kord, Zeboim, the Devourer, Zeus, and Thorgovern storms, sea, and sky. They include gods of lightning and thunder, gods of earthquakes, some fire gods, and certain gods of violence, physical strength, and courage. In some pantheons, a god of this domain rules over other deities and is known for swift justice delivered by thunderbolts. In the pantheons of seafaring people, gods of this domain are ocean deities and the patrons of sailors. Tempest gods send their clerics to inspire fear in the common folk, either to keep those folk on the path of righteousness or to encourage them to offer sacrifices of propitiation to ward off divine wrath.")]),_c('h5',[_vm._v("Wrath of the Storm")]),_c('p',[_vm._v("1st level: You can thunderously rebuke attackers. When a creature within 5 feet of you that you can see hits you with an attack, you can use your reaction to cause the creature to make a Dexterity saving throw. The creature takes 2d8 lightning or thunder damage (your choice) on a failed saving throw, and half as much damage on a successful one. You can use this feature a number of times equal to your Wisdom modifier (a minimum of once). You regain all expended uses when you finish a long rest. ")]),_c('h5',[_vm._v("Bonus Proficiencies")]),_c('p',[_vm._v(" 1st level: You gain proficiency with martial weapons and heavy armor. ")]),_c('h5',[_vm._v("Channel Divinity: Destructive Wrath")]),_c('p',[_vm._v(" 2nd level: You can use your Channel Divinity to wield the power of the storm with unchecked ferocity. When you roll lightning or thunder damage, you can use your Channel Divinity to deal maximum damage, instead of rolling. ")]),_c('h5',[_vm._v("Thunderbolt Strike")]),_c('p',[_vm._v("6th level: when you deal lightning damage to a Large or smaller creature, you can also push it up to 10 feet away from you. ")]),_c('h5',[_vm._v("Divine Strike")]),_c('p',[_vm._v("8th level: You gain the ability to infuse your weapon strikes with divine energy. Once on each of your turns when you hit a creature with a weapon attack, you can cause the attack to deal an extra 1d8 thunder damage to the target. When you reach 14th level, the extra damage increases to 2d8.")]),_c('h5',[_vm._v("Master of Nature")]),_c('p',[_vm._v("17th level: You have a flying speed equal to your current walking speed whenever you are not underground or indoors ")]),_c('h4',[_vm._v("Trickery Domain")]),_c('p',[_vm._v(" Gods of trickerysuch as Tymora, Beshaba, Olidammara, the Traveler, Garl Glittergold, and Lokiare mischief-makers and instigators who stand as a constant challenge to the accepted order among both gods and mortals. Theyre patrons of thieves, scoundrels, gamblers, rebels, and liberators. Their clerics are a disruptive force in the world, puncturing pride, mocking tyrants, stealing from the rich, freeing captives, and flouting hollow traditions. They prefer subterfuge, pranks, deception, and theft rather than direct confrontation. ")]),_c('h5',[_vm._v("Blessing of the Trickster")]),_c('p',[_vm._v("1st level: Starting when you choose this domain at 1st level, you can use your action to touch a willing creature other than yourself to give it advantage on Dexterity (Stealth) checks. This blessing lasts for 1 hour or until you use this feature again. ")]),_c('h5',[_vm._v("Channel Divinity: Invoke Duplicity")]),_c('p',[_vm._v(" 2nd level: You can use your Channel Divinity to create an illusory duplicate of yourself. As an action, you create a perfect illusion of yourself that lasts for 1 minute, or until you lose your concentration (as if you were concentrating on a spell). The illusion appears in an unoccupied space that you can see within 30 feet of you. As a bonus action on your turn, you can move the illusion up to 30 feet to a space you can see, but it must remain within 120 feet of you. For the duration, you can cast spells as though you were in the illusions space, but you must use your own senses. Additionally, when both you and your illusion are within 5 feet of a creature that can see the illusion, you have advantage on attack rolls against that creature, given how distracting the illusion is to the target. ")]),_c('h5',[_vm._v("Channel Divinity: Cloak of Shadows")]),_c('p',[_vm._v(" 6th level: You can use your Channel Divinity to vanish. As an action, you become invisible until the end of your next turn. You become visible if you attack or cast a spell.")]),_c('h5',[_vm._v("Divine Strike")]),_c('p',[_vm._v("8th level: You gain the ability to infuse your weapon strikes with poisona gift from your deity. Once on each of your turns when you hit a creature with a weapon attack, you can cause the attack to deal an extra 1d8 poison damage to the target. When you reach 14th level, the extra damage increases to 2d8. ")]),_c('h5',[_vm._v("Improved Duplicity")]),_c('p',[_vm._v("17th level: You can create up to four duplicates of yourself, instead of one, when you use Invoke Duplicity. As a bonus action on your turn, you can move any number of them up to 30 feet, to a maximum range of 120 feet. ")]),_c('h4',[_vm._v("War Domain")]),_c('p',[_vm._v(" War has many manifestations. It can make heroes of ordinary people. It can be desperate and horrific, with acts of cruelty and cowardice eclipsing instances of excellence and courage. In either case, the gods of war watch over warriors and reward them for their great deeds. The clerics of such gods excel in battle, inspiring others to fight the good fight or offering acts of violence as prayers. Gods of war include champions of honor and chivalry (such as Torm, Heironeous, and KiriJolith) as well as gods of destruction and pillage (such as Erythnul, the Fury, Gruumsh, and Ares) and gods of conquest and domination (such as Bane, Hextor, and Maglubiyet). Other war gods (such as Tempus, Nike, and Nuada) take a more neutral stance, promoting war in all its manifestations and supporting warriors in any circumstance. ")]),_c('h5',[_vm._v("Bonus Proficiencies")]),_c('p',[_vm._v("1st level: You gain proficiency with martial weapons and heavy armor. ")]),_c('h5',[_vm._v("War Priest")]),_c('p',[_vm._v(" 1st level: Your god delivers bolts of inspiration to you while you are engaged in battle. When you use the Attack action, you can make one weapon attack as a bonus action. You can use this feature a number of times equal to your Wisdom modifier (a minimum of once). You regain all expended uses when you finish a long rest. ")]),_c('h5',[_vm._v("Channel Divinity: Guided Strike")]),_c('p',[_vm._v(" 2nd level: You can use your Channel Divinity to strike with supernatural accuracy. When you make an attack roll, you can use your Channel Divinity to gain a +10 bonus to the roll. You make this choice after you see the roll, but before the DM says whether the attack hits or misses. ")]),_c('h5',[_vm._v("Channel Divinity: War God's Blessing")]),_c('p',[_vm._v(" 6th level: when a creature within 30 feet of you makes an attack roll, you can use your reaction to grant that creature a +10 bonus to the roll, using your Channel Divinity. You make this choice after you see the roll, but before the DM says whether the attack hits or misses. ")]),_c('h5',[_vm._v("Divine Strike")]),_c('p',[_vm._v("8th level: You gain the ability to infuse your weapon strikes with divine energy. Once on each of your turns when you hit a creature with a weapon attack, you can cause the attack to deal an extra 1d8 damage of the same type dealt by the weapon to the target. When you reach 14th level, the extra damage increases to 2d8. ")]),_c('h5',[_vm._v("Avatar of Battle")]),_c('p',[_vm._v("17th level: You gain resistance to bludgeoning, piercing, and slashing damage from nonmagical weapons. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"druid-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/druid.svg"),"aria-label":"Druid Icon","alt":"druid Icon"}}),_vm._v(" Druid ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/druid.png"),"aria-label":"Druid Image","alt":"Druid Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d8 per druid level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("8 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d8 (or 5) + your Constitution modifier per druid level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" Light armor")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, hand crossbows, longswords, rapiers, shortswords")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" Three musical instruments of your choice ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Dexterity, Charisma")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose any three")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a rapier, (b) a longsword, or (c) any simple weapon")]),_c('li',[_vm._v("(a) a diplomats pack or (b) an entertainer's pack")]),_c('li',[_vm._v("(a) a lute or (b) any other musical instrument")]),_c('li',[_vm._v("Leather armor and a dagger")])])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Holding high a gnarled staff wreathed with holly, an elf summons the fury of the storm and calls down explosive bolts of lightning to smite the torch-carrying orcs who threaten her forest. Crouching out of sight on a high tree branch in the form of a leopard, a human peers out of the jungle at the strange construction of a temple of Evil Elemental Air, keeping a close eye on the cultists activities. Swinging a blade formed of pure fire, a half-elf charges into a mass of skeletal soldiers, sundering the unnatural magic that gives the foul creatures the mocking semblance of life. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Whether calling on the elemental forces of nature or emulating the creatures of the animal! world, druids are an embodiment of nature's resilience, cunning, and fury. They claim no mastery over nature. Instead, they see themselves as extensions of natures indomitable will. ")]),_c('h4',[_vm._v("Power of Nature")]),_c('p',[_vm._v("Druids revere nature above all, gaining their spells and other magical powers either from the force of nature itself or from a nature deity. Many druids pursue a mystic spirituality of transcendent union with nature rather than devotion to a divine entity, while others serve gods of wild nature, animals, or elemental forces. The ancient druidic traditions are sometimes called the Old Faith, in contrast to the worship of gods in temples and shrines. ")]),_c('p',[_vm._v("Druid spells are oriented toward nature and animals the power of tooth and claw, of sun and moon, of fire and storm. Druids also gain the ability to take on animal forms, and some druids make a particular study of this practice, even to the point where they prefer animal form to their natural form.")]),_c('h4',[_vm._v("Preserve the Balance")]),_c('p',[_vm._v("For druids, nature exists in a precarious balance. The four elements that make up a worldair, earth, fire, and watermust remain in equilibrium. If one element were to gain power over the others, the world could be destroyed, drawn into one of the elemental planes and broken apart into its component elements. Thus, druids oppose cults of Elemental Evil and others who promote one element to the exclusion of others. ")]),_c('p',[_vm._v("Druids are also concerned with the delicate ecological balance that sustains plant and animal life, and the need for civilized folk to live in harmony with nature, not in opposition to it. Druids accept that which is cruel in nature, and they hate that which is unnatural, including aberrations (such as beholders and mind flayers) and undead (such as zombies and vampires). Druids sometimes lead raids against such creatures, especially when the monsters encroach on the druids territory. Druids are often found guarding sacred sites or watching over regions of unspoiled nature. But when a significant danger arises, threatening natures balance or the lands they protect, druids take on a more active role in combating the threat, as adventurers.")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Spellcasting")]),_c('p',[_vm._v("Drawing on the divine essence of nature itself, you can cast spells to shape that essence to your will. ")]),_c('h4',[_vm._v("Wild Shape")]),_c('p',[_vm._v("2nd Level: You can use your action to magically assume the shape of a beast that you have seen before. You can use this feature twice. You regain expended uses when you finish a short or long rest. Your druid level determines the beasts you can transform into, as shown in the Beast Shapes table. At 2nd level, for example, you can transform into any beast that has a challenge rating of 1/4 or lower that doesnt have a flying or swimming speed.")]),_c('p',[_c('ul',[_c('li',[_vm._v("Your game statistics are replaced by the statistics of the beast, but you retain your alignment, personality, and Intelligence, Wisdom, and Charisma scores. You also retain all of your skill and saving throw proficiencies, in addition to gaining those of the creature. If the creature has the same proficiency as you and the bonus in its stat block is higher than yours, use the creatures bonus instead of yours. If the creature has any legendary or lair actions, you cant use them. ")]),_c('li',[_vm._v("When you transform, you assume the beasts hit points and Hit Dice. When you revert to your normal form, you return to the number of hit points you had before you transformed. However, if you revert as a result of dropping to 0 hit points, any excess damage carries over to your normal form. ")]),_c('li',[_vm._v(" You cant cast spells, and your ability to speak or take any action that requires hands is limited to the capabilities of your beast form. Transforming doesnt break your concentration on a spell you've already cast, however, or prevent you from taking actions that are part of a cast spell. ")]),_c('li',[_vm._v(" You retain the benefit of any features from your class, race, or other source and can use them if the new form is physically capable of doing so. ")]),_c('li',[_vm._v("You choose whether your equipment falls to the ground in your space, merges into your new form, or is worn by it.")])])]),_c('h4',[_vm._v("Druid Circle")]),_c('p',[_vm._v("2nd Level: you choose to identify with a circle of druids: the Circle of the Land or the Circle of the Moon, both detailed at the end of the class description. Your choice grants you features at 2nd level and again at 6th, 10th, and 14th level. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Timeless Body")]),_c('p',[_vm._v("18th Level: The primal magic that you wield causes you to age more slowly. For every 10 years that pass, your body ages only 1 year. ")]),_c('h4',[_vm._v("Beast Spells")]),_c('p',[_vm._v("18th Level: You can cast many of your druid spells in any shape you assume using Wild Shape. You can perform the somatic and verbal components of a druid spell while in a beast shape, but you arent able to provide material components. ")]),_c('h4',[_vm._v("Archdruid")]),_c('p',[_vm._v("20th Level: You can use your Wild Shape an unlimited number of times.")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Druid Circles")]),_c('p',[_vm._v("Though their organization is invisible to most outsiders, druids are part of a society that spans the land, ignoring political borders. Ali druids are nominally members of this druidic society, though some individuals are so isolated that they have never seen any high-ranking members of the society or participated in druidic gatherings. Druids recognize each other as brothers and sisters. Like creatures of the wilderness, however, druids sometimes compete with or even prey on each other. At a local scale, druids are organized into circles that share certain perspectives on nature, balance, and the way of the druid.")]),_c('h4',[_vm._v("Circle of the Land")]),_c('p',[_vm._v("The Circle of the Land is made up of mystics and sages who safeguard ancient knowledge and rites through a vast oral tradition. These druids meet within sacred circles of trees or standing stones to whisper primal secrets in Druidic. The circles wisest members preside as the chief priests of communities that hold to the Old Faith and serve as advisors to the rulers of those folk. As a member of this circle, your magic is influenced by the land where you were initiated into the circles mysterious rites.")]),_c('h5',[_vm._v("Bonus Cantrip")]),_c('p',[_vm._v("2nd level: You learn one additional druid cantrip of your choice.")]),_c('h5',[_vm._v("Natural Recovery")]),_c('p',[_vm._v("2nd level: You can regain some of your magical energy by sitting in meditation and communing with nature. During a short rest, you choose expended spell slots to recover. The spell slots can have a combined level that is equal to or less than half your druid level (rounded up), and none of the slots can be 6th level or higher. You can't use this feature again until you finish a long rest For example, when you are a 4th-level druid, you can recover up to two levels worth of spell slots. You can recover either a 2nd-level slot or two 1st-level slots. ")]),_c('h5',[_vm._v("Circle Spells")]),_c('p',[_vm._v("Your mystical connection to the land infuses you with the ability to cast certain spells. At 3rd, 5th, 7th, and 9th level you gain access to circle spells connected to the land where you became a druid. Choose that landarctic, coast, desert, forest, grassland, mountain, swamp, or Underdarkand consult the associated list of spells. Once you gain access to a circle spell, you always have it prepared, and it doesnt count against the number of spells you can prepare each day. If you gain access toa spell that doesnt appear on the druid spell list, the spell is nonetheless a druid spell for you.")]),_c('h5',[_vm._v("Land's Stride")]),_c('p',[_vm._v("6th level: Moving through nonmagical difficult terrain costs you no extra movement. You can also pass through nonmagical plants without being slowed by them and without taking damage from them if they have thorns, spines, or a similar hazard. In addition, you have advantage on saving throws against plants that are magically created or manipulated to impede movement, such those created by the entangle spell. ")]),_c('h5',[_vm._v("Nature's Ward")]),_c('p',[_vm._v("14th level: Creatures of the natural world sense your connection to nature and become hesitant to attack you. When a beast or plant creature attacks you, that creature must make a Wisdom saving throw against your druid spell save DC. On a failed save, the creature must choose a different target, or the attack automatically misses. On a successful save, the creature is immune to this effect for 24 hours. The creature is aware of this effect before it makes its attack against you. ")]),_c('h5',[_vm._v("Nature's Sanctuary")]),_c('p',[_vm._v("10th level: You cant be charmed or frightened by elementals or fey, and you are immune to poison and disease. ")]),_c('h4',[_vm._v("Circle of the Moon")]),_c('p',[_vm._v("Druids of the Circle of the Moon are fierce guardians of the wilds. Their order gathers under the full moon to share news and trade warnings. They haunt the deepest parts of the wilderness, where they might go for weeks on end before crossing paths with another humanoid creature, let alone another druid. Changeable as the moon, a druid of this circle might prowl as a great cat one night, soar over the treetops as an eagle the next day, and crash through the undergrowth in bear form to drive off a trespassing monster. The wild is in the druids blood.")]),_c('h5',[_vm._v("Combat Wild Shape")]),_c('p',[_vm._v("2nd level: You gain the ability to use Wild Shape on your turn as a bonus action, rather than as an action. Additionally, while you are transformed by Wild Shape, you can use a bonus action to expend one spell slot to regain 1d8 hit points per level of the spell slot expended.")]),_c('h5',[_vm._v("Circle Forms")]),_c('p',[_vm._v("2nd level: You can use your Wild Shape to transform into a beast with a challenge rating as high as 1 (you ignore the Max. CR column of the Beast Shapes table, but must abide by the other limitations there). 6th level: You can transform into a beast with a challenge rating as high as your druid level divided by 3, rounded down. ")]),_c('h5',[_vm._v("Primal Strike")]),_c('p',[_vm._v("6th level: Your attacks in beast form count as magical for the purpose of overcoming resistance and immunity to nonmagical attacks and damage")]),_c('h5',[_vm._v("Elemental Wild Shape")]),_c('p',[_vm._v("10th level: You can expend two uses of Wild Shape at the same time to transform into an air elemental, an earth elemental, a fire elemental, or a water elemental. ")]),_c('h5',[_vm._v("Thousand Forms")]),_c('p',[_vm._v("10th level: You have learned to use magic to alter your physical form in more subtle ways. You can cast the alter self spell at will. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"fighter-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/fighter.svg"),"aria-label":"Fighter Icon","alt":"fighter Icon"}}),_vm._v(" Fighter ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('div',{staticClass:"hp"},[_c('img',{attrs:{"src":require("../assets/classes/fighter.png"),"aria-label":"Fighter Image","alt":"Fighter Image"}}),_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d10 per fighter level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("10 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d10 (or 6) + your Constitution modifier per fighter level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" All armor, shields | "),_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, martial weapons | "),_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Strength, Constitution | "),_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two from Acrobatics, Animal Handling, Athletics, History, Insight, Intimidation, Perception, and Survival. ")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) chain mail or (b) leather, longbow, and 20 arrows")]),_c('li',[_vm._v("(a) a martial weapon and a shield or (b) two martial weapons ")]),_c('li',[_vm._v("(a) a light crossbow and 20 bolts or (b) two handaxes")]),_c('li',[_vm._v("(a) a dungeoneers pack or (b) an explorers pack")])])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v(" human in clanging plate armor holds her shield before her as she runs toward the massed goblins. An elf behind her, clad in studded leather armor, peppers the goblins with arrows loosed from his exquisite bow. The half-orc nearby shouts orders, helping the two combatants coordinate their assault to the best advantage. A dwarf in chain mail interposes his shield between the ogres club and his companion, knocking the deadly blow aside. His companion, a half-elf in scale armor, swings two scimitars in a blinding whirl as she circles the ogre, looking for a blind spot in its defenses. A gladiator fights for sport in an arena, a master with his trident and net, skilled at toppling foes and moving them around for the crowd's delightand his own tactical advantage. His opponents sword flares with blue light an instant before she sends lightning flashing forth to smite him. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("All of these heroes are fighters, perhaps the most diverse class of characters in the worlds of DUNGEONS & DRAGONS. Questing knights, conquering overlords, royal champions, elite foot soldiers, hardened mercenaries, and bandit kingsas fighters, they all share an unparalleled mastery with weapons and armor, and a thorough knowledge of the skills of combat. And they are well acquainted with death, both meting it out and staring it defiantly in the face. ")]),_c('h4',[_vm._v("Well-Rounded Specialists")]),_c('p',[_vm._v("Fighters learn the basics of all combat styles. Every fighter can swing an axe, fence with a rapier, wield a longsword or a greatsword, use a bow, and even trap foes in a net with some degree of skill. Likewise, a fighter is adept with shields and every form of armor. Beyond that basic degree of familiarity, each fighter specializes in a certain style of combat. Some concentrate on archery, some on fighting with two weapons at once, and some on augmenting their martial skills with magic. This combination of broad general ability and extensive specialization makes fighters superior combatants on battlefields and in dungeons alike. ")]),_c('h4',[_vm._v("Trained for Danger")]),_c('p',[_vm._v("Not every member of the city watch, the village militia, or the queens army is a fighter. Most of these troops are relatively untrained soldiers with only the most basic combat knowledge. Veteran soldiers, military officers, trained bodyguards, dedicated knights, and similar figures are fighters. ")]),_c('p',[_vm._v("Some fighters feel drawn to use their training as adventurers. The dungeon delving. monster slaying, and other dangerous work common among adventurers is second nature for a fighter, not all that different from the life he or she left behind. There are greater risks, perhaps, but also much greater rewardsfew fighters in the city watch have the opportunity to discover a magic flame tongue sword, for example.")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Fighting Style")]),_c('p',[_vm._v("You adopt a particular style of fighting as your specialty. Choose one of the following options. You cant take a Fighting Style option more than once, even if you later get to choose again. ")]),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Archery")]),_c('li',[_vm._v("Defense")]),_c('li',[_vm._v("Dueling")]),_vm._v(" "),_c('li',[_vm._v("Great Weapon Fighting")]),_c('li',[_vm._v("Protection")]),_c('li',[_vm._v("Two-Weapon Fighting")])]),_c('h4',[_vm._v("Second Wind")]),_c('p',[_vm._v("You have a limited well of stamina that you can draw on to protect yourself from harm. On your turn, you can use a bonus action to regain hit points equal to 1d10 + your fighter level. Once you use this feature, you must finish a short or long rest before you can use it again.")]),_c('h4',[_vm._v("Action Surge")]),_c('p',[_vm._v("2nd level: You can push yourself beyond your normal limits for a moment. On your turn, you can take one additional action on top of your regular action and a possible bonus action. ")]),_c('h4',[_vm._v("Martial Archetype")]),_c('p',[_vm._v("3rd level: You choose an archetype that you strive to emulate in your combat styles and techniques. Choose Champion, Battle Master, or Eldritch Knight, all detailed at the end of the class description. The archetype you choose grants you features at 3rd level and again at 7th, 10th, 15th, and 18th level. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("4th, 6th, 8th, 12th, 14th, 16th, and 19th level: You can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Extra Attack")]),_c('p',[_vm._v("5th level: You can attack twice, instead of once, whenever you take the Attack action on your turn. The number of attacks increases to three when you 2 reach 11th level in this class and to four when you reach 20th level in this class.")]),_c('h4',[_vm._v("Indomitable")]),_c('p',[_vm._v("9th level: You can reroll a saving throw that you fail. If you do so, you must use the new roll, and you cant use this feature again until you finish a long rest. You can use this feature twice between long rests starting at 13th level and three times between long rests starting at 17th level.")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Martial Archetypes")]),_c('p',[_vm._v("Different fighters choose different approaches to perfecting their fighting prowess. The martial archetype you choose to emulate reflects your approach. ")]),_c('h4',[_vm._v("Champion")]),_c('p',[_vm._v("The archetypal Champion focuses on the development of raw physical power honed to deadly perfection. Those who model themselves on this archetype combine rigorous training with physical excellence to deal devastating blows.")]),_c('h5',[_vm._v("Improved Critical")]),_c('p',[_vm._v("3rd level: Your weapon attacks score a critical hit ona roll of 19 or 20.")]),_c('h5',[_vm._v("Remarkable Athlete")]),_c('p',[_vm._v("7th level: You can add half your proficiency bonus (round up) to any Strength, Dexterity, or Constitution check you make that doesnt already use your proficiency bonus. In addition, when you make a running long jump, the distance you can cover increases by a number of feet equal to your Strength modifier. ")]),_c('h5',[_vm._v("Additional Fighting Style")]),_c('p',[_vm._v("10th level: You can choose a second option from the Fighting Style class feature.")]),_c('h5',[_vm._v("Superior Critical")]),_c('p',[_vm._v("15th level: Your weapon attacks score a critical hit on a roll of 1820. ")]),_c('h5',[_vm._v("Survivor")]),_c('p',[_vm._v("18th level: You attain the pinnacle of resilience in battle. At the start of each of your turns, you regain hit points equal to 5 + your Constitution modifier if you have no more than half of your hit points left. You dont gain this benefit if you have 0 hit points. ")]),_c('h4',[_vm._v("Battle Master")]),_c('p',[_vm._v(" Those who emulate the archetypal Battle Master employ martial techniques passed down through generations. To a Battle Master, combat is an academic field, sometimes including subjects beyond battle such as weaponsmithing and calligraphy. Not every fighter absorbs the lessons of history, theory, and artistry that are reflected in the Battle Master archetype, but those who do are well-rounded fighters of great skill and knowledge. ")]),_c('h5',[_vm._v("Combat Superiority")]),_c('p',[_vm._v("3rd level: You learn maneuvers that are fueled by special dice called superiority dice. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Maneuvers ")]),_vm._v(". You learn three maneuvers of your choice, which are detailed under Maneuvers below. Many maneuvers enhance an attack in some way. You can use only one maneuver per attack. You learn two additional maneuvers of your choice at 7th, 10th, and 15th level. Each time you learn new maneuvers, you can also replace one maneuver you know with a different one.")]),_c('li',[_c('strong',[_vm._v("Superiority Dice ")]),_vm._v(". You have four superiority dice, which are d8s. A superiority die is expended when you use it. You regain all of your expended superiority dice when you finish a short or long rest. You gain another superiority die at 7th level and one more at 15th level.")]),_c('li',[_c('strong',[_vm._v("Saving Throws ")]),_vm._v(". Some of your maneuvers require your target to make a saving throw to resist the maneuvers effects. The saving throw DC is calculated as follows: Maneuver save DC = 8 + your proficiency bonus + your Strength or Dexterity modifier (your choice)")])])]),_c('h5',[_vm._v("Student of War")]),_c('p',[_vm._v("3rd level: You gain proficiency with one type of artisans tools of your choice. ")]),_c('h5',[_vm._v("Know your Enemy")]),_c('p',[_vm._v("7th level: If you spend at least 1 minute observing or interacting with another creature outside combat, you can learn certain information about its capabilities compared to your own. The DM tells you if the creature is your equal, superior, or inferior in regard to two of the following characteristics of your choice. "),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Strength core")]),_c('li',[_vm._v("Dexterity score")]),_c('li',[_vm._v("Constitution score")]),_c('li',[_vm._v("Armor Class")]),_c('li',[_vm._v("Current Hit Points")]),_c('li',[_vm._v("Total class levels")]),_c('li',[_vm._v("Fighter class levels")])])]),_c('h5',[_vm._v("Improved Combat Superiority")]),_c('p',[_vm._v("10th level: Your superiority dice turn into d10s. 18th level: They turn into d12s.")]),_c('h5',[_vm._v("Relentless")]),_c('p',[_vm._v("15th level: When you roll initiative and have no superiority dice remaining, you regain 1 superiority die. ")]),_c('h5',[_vm._v("Maneuvers")]),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Commander's Strike")]),_c('li',[_vm._v("Disarming Attack")]),_c('li',[_vm._v("Distracting Strike")]),_c('li',[_vm._v("Evasive Footwork")]),_c('li',[_vm._v("Feinting Attack")]),_c('li',[_vm._v("Goading Attack")]),_c('li',[_vm._v("Lunging Attack")]),_c('li',[_vm._v("Maneuvering Attack")]),_c('li',[_vm._v("Menacing Attack")]),_c('li',[_vm._v("Parry")]),_c('li',[_vm._v("Precision Attack")]),_c('li',[_vm._v("Pushing Attack")]),_c('li',[_vm._v("Rally")]),_c('li',[_vm._v("Riposte")]),_c('li',[_vm._v("Sweeping Attack")]),_c('li',[_vm._v("Trip Attack")])]),_c('h4',[_vm._v("Eldritch Knight")]),_c('p',[_vm._v(" The archetypal Eldritch Knight combines the martial mastery common to all fighters with a careful study of magic. Eldritch Knights use magical techniques similar to those practiced by wizards. They focus their study on two of the eight schools of magic: abjuration and evocation. Abjuration spells grant an Eldritch Knight additional protection in battle, and evocation spells deal damage to many foes at once, extending the fighters reach in combat. These knights learn a comparatively small number of spells, committing them to memory instead of keeping them in a spellbook. ")]),_c('h5',[_vm._v("Spellcasting")]),_c('p',[_vm._v("3rd level: You augment your martial prowess with the ability to cast spells. See chapter 10 for the general rules of spellcasting and chapter 11 for the wizard spell list. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Cantrips ")]),_vm._v(". You learn two cantrips of your choice from the wizard spell list. You learn an additional wizard cantrip of your choice at 10th level. ")]),_c('li',[_c('strong',[_vm._v("Spell Slots")]),_vm._v(". The Eldritch Knight Spellcasting table shows how many spell slots you have to cast your spells of 1st level and higher. To cast one of these spells, you must expend a slot of the spells level or higher. You regain all expended spell slots when you finish a long rest. ")]),_c('li',[_c('strong',[_vm._v("Spells Known of 1st Level or Higher")]),_vm._v(". You know three 1st-level wizard spells of your choice, two of which you must choose from the abjuration and evocation spells on the wizard spell list. The Spells Known column of the Eldritch Knight Spellcasting table shows when you learn more wizard spells of 1st level or higher. Each of these spells must be an abjuration or evocation spell of your choice, and must be of a level for which you have spell slots. For instance, when you reach 7th level in this class, you can learn one new spell of 1st or 2nd level. The spells you learn at 8th, 14th, and 20th level can come from any school of magic. Whenever you gain a level in this class, you can replace one of the wizard spells you know with another spell of your choice from the wizard spell list. The new spell must be of a level for which you have spell slots, and it must be an abjuration or evocation spell, unless you're replacing the spell you gained at 8th, 14th. or 20th level.")]),_c('li',[_c('strong',[_vm._v("Spellcasting Ability")]),_vm._v(". Intelligence is your spellcasting ability for your wizard spells, since you learn your spells through study and memorization. You use your Intelligence whenever a spell refers to your spellcasting ability. In addition, you use your Intelligence modifier when setting the saving throw DC for a wizard spell you cast and when making an attack roll with one. ")])])]),_c('h5',[_vm._v("Weapon Bond")]),_c('p',[_vm._v("3rd level: You learn a ritual that creates a magical bond between yourself and one weapon. You perform the ritual over the course of 1 hour, which can be done during a short rest. The weapon must be within your reach throughout the ritual, at the conclusion of which you touch the weapon and forge the bond. Once you have bonded a weapon to yourself, you can't be disarmed of that weapon unless you are incapacitated. If it is on the same plane of existence, you can summon that weapon as a bonus action on your turn, causing it to teleport instantly to your hand. You can have up to two bonded weapons, but can summon only one at a time with your bonus action. If you attempt to bond with a third weapon, you must break the bond with one of the other two. ")]),_c('h5',[_vm._v("War Magic")]),_c('p',[_vm._v("7th level: When you use your action to cast a cantrip, you can make one weapon attack as a bonus action. ")]),_c('h5',[_vm._v("Eldritch Strike")]),_c('p',[_vm._v("10th level: You learn how to make your weapon strikes undercut a creatures resistance to your spells. When you hit a creature with a weapon attack, that creature has disadvantage on the next saving throw it makes against a spell you cast before the end of your next turn. ")]),_c('h5',[_vm._v("Arcane Charge")]),_c('p',[_vm._v("15th level: You gain the ability to teleport up to 30 feet to an unoccupied space you can see when you use your Action Surge. You can teleport before or after the additional action. ")]),_c('h5',[_vm._v("Improved War Magic")]),_c('p',[_vm._v("18th level: When you use your action to cast a spell, you can make one weapon attack as a bonus action. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"monk-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/monk.svg"),"aria-label":"Monk Icon","alt":"Monk Icon"}}),_vm._v(" Monk ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/monk.png"),"aria-label":"Monk Image","alt":"Monk Image"}}),_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d8 per monk level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("8 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d8 (or 5) + your Constitution modifier per monk level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" None")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, shortswords, longswords, rapiers, shortswords")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" Choose one type of artisans tools or one musical instrument ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Dexterity, Strength")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two from Acrobatics, Athletics, History, Insight, Religion, and Stealth")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a shortsword or (b) any simple weapon")]),_c('li',[_vm._v("(a) a dungeoneers pack or (b) an explorers pack")]),_c('li',[_vm._v("10 darts")])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Her fists a blur as they deflect an incoming hail of arrows, a half-elf springs over a barricade and throws herself into the massed ranks of hobgoblins on the other side. She whirls among them, knocking their blows aside and sending them reeling, until at last she stands alone. Taking a deep breath, a human covered in tattoos settles into a battle stance. As the first charging orcs reach him, he exhales and a blast of fire roars from his mouth, engulfing his foes. Moving with the silence of the night, a black-clad halfling steps into a shadow beneath an arch and emerges from another inky shadow on a balcony a f stones throw away. She slides her blade free of its clothwrapped scabbard and peers through the open window at the tyrant prince, so vulnerable in the grip of sleep. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Whatever their discipline, monks are united in their ability to magically harness the energy that flows in their bodies. Whether channeled as a striking display of combat prowess or a subtler focus of defensive ability and speed, this energy infuses all that a monk does. ")]),_c('h4',[_vm._v("The Magic of Ki")]),_c('p',[_vm._v("Monks make careful study of a magical energy that most monastic traditions call ki. This energy is an element of the magic that suffuses the multiversespecifically, the element that flows through living bodies. Monks harness this power within themselves to create magical effects and exceed their bodies physical capabilities, and some of their special attacks can hinder the flow of ki in their opponents. Using this energy, monks channel uncanny speed and strength into their unarmed strikes. As they gain experience, their martial training and their mastery of ki gives them more power over their bodies and the bodies of their foes. ")]),_c('h4',[_vm._v("Training and Asceticism")]),_c('p',[_vm._v("Small walled cloisters dot the landscapes of the worlds of D&D, tiny refuges from the flow of ordinary life, where time seems to stand still. The monks who live there seek personal perfection through contemplation and rigorous training. Many entered the monastery as children, sent to live there when their parents died, when food couldnt be found to support them, or in return for some kindness that the monks had performed for their families. Some monks live entirely apart from the surrounding population, secluded from anything that might impede their spiritual progress. Others are sworn to isolation, emerging only to serve as spies or assassins at the command of their leader, a noble patron, or some other mortal or divine power. The majority of monks dont shun their neighbors, making frequent visits to nearby towns or villages and exchanging their service for food and other goods. As versatile warriors, monks often end up protecting their neighbors from monsters or tyrants. ")]),_c('p',[_vm._v("For a monk, becoming an adventurer means leaving a structured, communal lifestyle to become a wanderer. This can be a harsh transition, and monks dont undertake it lightly. Those who leave their cloisters take their work seriously, approaching their adventures as personal tests of their physical and spiritual growth. As arule, monks care little for material wealth and are driven by a desire to accomplish a greater mission than merely slaying monsters and plundering their treasure")]),_c('h4',[_vm._v("Preserve the Balance")]),_c('p',[_vm._v("For druids, nature exists in a precarious balance. The four elements that make up a worldair, earth, fire, and watermust remain in equilibrium. If one element were to gain power over the others, the world could be destroyed, drawn into one of the elemental planes and broken apart into its component elements. Thus, druids oppose cults of Elemental Evil and others who promote one element to the exclusion of others. ")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Martial Arts")]),_c('p',[_vm._v("1st level: Your practice of martial arts gives you mastery of combat styles that use unarmed strikes and monk weapons, which are shortswords and any simple melee weapons that dont have the two-handed or heavy property. Certain monasteries use specialized forms of the monk weapons. ")]),_c('ul',{staticClass:"skills"},[_c('li',[_vm._v("You can use Dexterity instead of Strength for the attack and damage rolls of your unarmed strikes and monk weapons. ")]),_c('li',[_vm._v("You can roll a d4 in place of the normal damage of your unarmed strike or monk weapon. This die changes as you gain monk levels. ")]),_c('li',[_vm._v("When you use the Attack action with an unarmed strike or a monk weapon on your turn, you can make one unarmed strike as a bonus action.")])]),_c('h4',[_vm._v("Ki")]),_c('p',[_vm._v("2nd level, your training allows you to harness the mystic energy of ki. Your access to this energy is represented by a number of ki points. Your monk level determines the number of points you have. You start knowing three such features: Flurry of Blows, Patient Defense, and Step of the Wind. You learn more ki features as you gain levels in this class. ")]),_c('h5',[_vm._v("Flurry of Blows")]),_c('p',[_vm._v("Immediately after you take the Attack action on your turn, you can spend 1 ki point to make two unarmed strikes as a bonus action.")]),_c('h5',[_vm._v("Patient Defense")]),_c('p',[_vm._v("You can spend 1 ki point to take the Dodge action asa bonus action on your turn.")]),_c('h5',[_vm._v("Step of the Wind")]),_c('p',[_vm._v("You can spend 1 ki point to take the Disengage or Dash action as a bonus action on your turn, and your jump distance is doubled for the turn.")]),_c('h4',[_vm._v("Unarmored Movement")]),_c('p',[_vm._v("2nd level: You can push yourself beyond your normal limits for a moment. On your turn, you can take one additional action on top of your regular action and a possible bonus action. ")]),_c('h4',[_vm._v("Martial Archetype")]),_c('p',[_vm._v(" 2nd level: Your speed increases by 10 feet while you are not wearing armor or wielding a shield. This bonus increases when you reach certain monk levels, as shown in the Monk table. ")]),_c('p',[_vm._v("9th level: You gain the ability to move along vertical surfaces and across liquids on your turn without falling during the move. ")]),_c('h4',[_vm._v("Monastic Traditions")]),_c('p',[_vm._v("3rd level: You commit yourself to a monastic tradition: the Way of the Open Hand, the Way of Shadow, or the Way of the Four Elements, all detailed at the end of the class description. Your tradition grants you features at 3rd level and again at 6th, 11th, and 17th level.")]),_c('h4',[_vm._v("Deflect Missiles")]),_c('p',[_vm._v(" 3rd level: You can use your reaction to deflect or catch the missile when you are hit by a ranged weapon attack. When you do so, the damage you take from the attack is reduced by 1d10 + your Dexterity modifier + your monk level. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("4th, 6th, 8th, 12th, 14th, 16th, and 19th level: You can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Slow Fall")]),_c('p',[_vm._v("5th level: You can attack twice, instead of once, whenever you take the Attack action on your turn. The number of attacks increases to three when you 2 reach 11th level in this class and to four when you reach 20th level in this class. ")]),_c('h4',[_vm._v("Extra Attack")]),_c('p',[_vm._v("5th level: You can attack twice, instead of once, whenever you take the Attack action on your turn. The number of attacks increases to three when you 2 reach 11th level in this class and to four when you reach 20th level in this class. ")]),_c('h4',[_vm._v("Stunning Strike")]),_c('p',[_vm._v(" 5th level: You can interfere with the flow of ki in an opponents body. When you hit another creature with a melee weapon attack, you can spend 1 ki point to attempt a stunning strike. The target must succeed on a Constitution saving throw or be stunned until the end of your next turn. ")]),_c('h4',[_vm._v("Ki-Empowered Strikes")]),_c('p',[_vm._v("6th level: Your unarmed strikes count as magical for the purpose of overcoming resistance and immunity to nonmagical attacks and damage. ")]),_c('h4',[_vm._v("Evasion")]),_c('p',[_vm._v("7th level: Your instinctive agility lets you dodge out of the way of certain area effects, such as a blue dragon's lightning breath or a fireball spell. When you are subjected to an effect that allows you to make a Dexterity saving throw to take only half damage, you instead take no damage if you succeed on the saving throw, and only half damage if you fail. ")]),_c('h4',[_vm._v("Stillness of Mind")]),_c('p',[_vm._v("7th level: You can use your action to end one effect on yourself that is causing you to be charmed or frightened. ")]),_c('h4',[_vm._v("Purity of Body")]),_c('p',[_vm._v("10th level: Your mastery of the ki flowing through you makes you immune to disease and poison. ")]),_c('h4',[_vm._v("Tongue of the Sun and Moon")]),_c('p',[_vm._v(" 13th level: You learn to touch the ki of other minds so that you understand all spoken languages. Moreover, any creature that can understand a language can understand what you say. ")]),_c('h4',[_vm._v("Diamond Soul")]),_c('p',[_vm._v("14th level: Your mastery of ki grants you proficiency in all saving throws. Additionally, whenever you make a saving throw and fail, you can spend 1 ki point to reroll it and take the second result. ")]),_c('h4',[_vm._v("Timeless Body")]),_c('p',[_vm._v("15th level: Your ki sustains you so that you suffer none of the frailty of old age, and you cant be aged magically. You can still die of old age, however. In addition, you no longer need food or water. ")]),_c('h4',[_vm._v("Empty Body")]),_c('p',[_vm._v("18th level: You can use your action to spend 4 ki points to become invisible for 1 minute. During that time, you also have resistance to all damage but force damage. Additionally, you can spend 8 ki points to cast the astral projection spell, without needing material components. When you do so, you can't take any other creatures with you. ")]),_c('h4',[_vm._v("Perfect Self")]),_c('p',[_vm._v("20th level: When you roll for initiative and have no ki points remaining, you regain 4 ki points. ")]),_c('h4',[_vm._v("Indomitable")]),_c('p',[_vm._v("9th level: You can reroll a saving throw that you fail. If you do so, you must use the new roll, and you cant use this feature again until you finish a long rest. You can use this feature twice between long rests starting at 13th level and three times between long rests starting at 17th level.")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Monastic Traditions")]),_c('p',[_vm._v("Three traditions of monastic pursuit are common in the monasteries scattered across the multiverse. Most monasteries practice one tradition exclusively, but a few honor the three traditions and instruct each monk according to his or her aptitude and interest. All three traditions rely on the same basic techniques, diverging as the student grows more adept. Thus, a monk need choose a tradition only upon reaching 3rd level. ")]),_c('h4',[_vm._v("Way of the Open Hand")]),_c('p',[_vm._v("Monks of the Way of the Open Hand are the ultimate masters of martial arts combat, whether armed or unarmed. They learn techniques to push and trip their opponents, manipulate ki to heal damage to their bodies, and practice advanced meditation that can protect them from harm. ")]),_c('h5',[_vm._v("Open Hand Technique")]),_c('p',[_vm._v("Starting when you choose this tradition at 3rd level, you can manipulate your enemys ki when you harness your own. Whenever you hit a creature with one of the attacks granted by your Flurry of Blows, you can impose one of the following effects on that target:")]),_c('ul',[_c('li',[_vm._v(" It must succeed on a Dexterity saving throw or be knocked prone. ")]),_c('li',[_vm._v("It must make a Strength saving throw. If it fails, you can push it up to 15 feet away from you. ")]),_c('li',[_vm._v(" It cant take reactions until the end of your next turn.")])]),_c('h5',[_vm._v("Wholeness of Body")]),_c('p',[_vm._v("6th level: You gain the ability to heal yourself. As an action, you can regain hit points equal to three times your monk level. You must finish a long rest before you can use this feature again. ")]),_c('h5',[_vm._v("Tranquility")]),_c('p',[_vm._v("11th level: You can enter a special meditation that surrounds you with an aura of peace. At the end of a long rest, you gain the effect of a sanctuary spell that lasts until the start of your next long rest (the spell can end early as normal). The saving throw DC for the spell equals 8 + your Wisdom modifier + your proficiency bonus. ")]),_c('h5',[_vm._v("Quivering Palm")]),_c('p',[_vm._v("17th level: You gain the ability to set up lethal vibrations in someones body. When you hit a creature with an unarmed strike, you can spend 3 ki points to start these imperceptible vibrations, which last for a number of days equal to your monk level. The vibrations are harmless unless you use your action to end them. To do so, you and the target must be on the same plane of existence. When you use this action, the creature must make a Constitution saving throw. If it fails, it is reduced to 0 hit points. If it succeeds, it takes 10d10 necrotic damage. You can have only one creature under the effect of this feature at a time. You can choose to end the vibrations harmlessly without using an action. ")]),_c('h4',[_vm._v("Way of Shadow")]),_c('p',[_vm._v("Monks of the Way of Shadow follow a tradition that values stealth and subterfuge. These monks might be called ninjas or shadowdancers, and they serve as spies and assassins. Sometimes the members of a ninja monastery are family members, forming a clan sworn to secrecy about their arts and missions. Other monasteries are more like thieves guilds, hiring out their services to nobles, rich merchants, or anyone else who can pay their fees. Regardless of their methods, the heads of these monasteries expect the unquestioning obedience of their students. ")]),_c('h5',[_vm._v("Shadow Arts")]),_c('p',[_vm._v("3rd level: You can use your ki to duplicate the effects of certain spells. As an action, you can spend 2 ki points to cast darkness, darkvision, pass without trace, or silence, without providing material components. Additionally, you gain the minor illusion cantrip if you dont already know it. ")]),_c('h5',[_vm._v("Shadow Step")]),_c('p',[_vm._v("6th level: You gain the ability to step from one shadow into another. When you are in dim light or darkness, as a bonus action you can teleport up to 60 feet to an unoccupied space you can see that is also in dim light or darkness. You then have advantage on the first melee attack you make before the end of the turn. ")]),_c('h5',[_vm._v("Cloak of Shadows")]),_c('p',[_vm._v("11th level: You have learned to become one with the shadows. When you are in an area of dim light or darkness, you can use your action to become invisible. You remain invisible until you make an attack, cast a spell, or are in an area of bright light. ")]),_c('h5',[_vm._v("Opportunist")]),_c('p',[_vm._v("17th level: You can exploit a creature's momentary distraction when it is hit by an attack. Whenever a creature within 5 feet of you is hit by an attack made by a creature other than you, you can use your reaction to make a melee attack against that creature. ")]),_c('h4',[_vm._v("Way of the Four Elements")]),_c('p',[_vm._v("You follow a monastic tradition that teaches you to harness the elements. When you focus your ki, you can align yourself with the forces of creation and bend the four elements to your will, using them as an extension of your body. Some members of this tradition dedicate themselves to a single element, but others weave the elements together. Many monks of this tradition tattoo their bodies with representations of their ki powers, commonly imagined as coiling dragons, but also as phoenixes, fish, plants, mountains, and cresting waves. ")]),_c('h5',[_vm._v("Disciple of the Elements")]),_c('p',[_vm._v(" 3rd level: You learn magical disciplines that harness the power of the four elements. A discipline requires you to spend ki points each time you use it. You know the Elemental Attunement discipline and one other elemental discipline of your choice, which are detailed in the Elemental Disciplines section below. You learn one additional elemental discipline of your choice at 6th, 11th, and 17th level. Whenever you learn a new elemental discipline, you can also replace one elemental discipline that you already know with a different discipline. ")]),_c('h5',[_vm._v("Elemental Disciplines")]),_c('p',[_vm._v("The elemental disciplines are presented in alphabetical order. If a discipline requires a level, you must be that level in this class to learn the discipline.")]),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Breath of Winter (17th Level)")]),_c('li',[_vm._v("Clench of the North Wind (6th Level)")]),_c('li',[_vm._v("Elemental Attunement")]),_c('li',[_vm._v("Eternal Mountain Defense (11th Level)")]),_c('li',[_vm._v("Fangs of the Fire Snake")]),_c('li',[_vm._v("Fist of Four Thunders")]),_c('li',[_vm._v("Fist of Unbroken Air.")]),_c('li',[_vm._v("Flames of the Phoenix (11th Level)")]),_c('li',[_vm._v("Gong of the Summit (6th Level)")]),_c('li',[_vm._v("Mist Stance (11th Level)")]),_c('li',[_vm._v("Ride the Wind (11th Level)")]),_c('li',[_vm._v("River of Hungry Flame (17th Level)")]),_c('li',[_vm._v("Rush of the Gale Spirits")]),_c('li',[_vm._v("Shape the Flowing River")]),_c('li',[_vm._v("Sweeping Cinder Strike")]),_c('li',[_vm._v("Water Whip")]),_c('li',[_vm._v("Wave of Rolling Earth (17th Level)")])])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"paladin-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/paladin.svg"),"aria-label":"Paladin Icon","alt":"Paladin Icon"}}),_vm._v(" Paladin ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/paladin.png"),"aria-label":"Paladin Image","alt":"Paladin Image"}}),_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d10 per paladin level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("10 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d10 (or 6) + your Constitution modifier per paladin level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" All armor, shields")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, martial weapons")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Wisdom, Charisma")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two from Athletics, Insight, Intimidation, Medicine, Persuasion, and Religion")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a martial weapon and a shield or (b) two martial weapons")]),_c('li',[_vm._v("(a) five javelins or (b) any simple melee weapon")]),_c('li',[_vm._v("(a) a priests pack or (b) an explorers pack")]),_c('li',[_vm._v("Chain mail and a holy symbol")])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Clad in plate armor that gleams in the sunlight despite the dust and grime of long travel, a human lays down her sword and shield and places her hands on a mortally wounded man. Divine radiance shines from her hands, the mans wounds knit closed, and his eyes open wide with amazement. A dwarf crouches behind an outcrop, his black cloak making him nearly invisible in the night, and watches an orc war band celebrating its recent victory. Silently, he stalks into their midst and whispers an oath, and two orcs are dead before they even realize he is there. Silver hair shining in a shaft of light that seems to illuminate only him, an elf laughs with exultation. His spear flashes like his eyes as he jabs again and again at a twisted giant, until at last his light overcomes its hideous darkness. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Whatever their origin and their mission, paladins are united by their oaths to stand against the forces of evil. Whether sworn before a gods altar and the witness of a priest, in a sacred glade before nature spirits and fey beings, or in a moment of desperation and grief with the dead as the only witness, a paladins oath is a powerful bond. It is a source of power that turns a devout warrior into a blessed champion. ")]),_c('h4',[_vm._v("The Cause of Righteousness")]),_c('p',[_vm._v("A paladin swears to uphold justice and righteousness, to stand with the good things of the world against the encroaching darkness, and to hunt the forces of evil wherever they lurk. Different paladins focus on various aspects of the cause of righteousness, but all are bound by the oaths that grant them power to do their sacred work. Although many paladins are devoted to gods of good, a paladins power comes as much from a commitment to justice itself as it does from a god. ")]),_c('p',[_vm._v("Paladins train for years to learn the skills of combat, mastering a variety of weapons and armor. Even so, their martial skills are secondary to the magical power they wield: power to heal the sick and injured, to smite the wicked and the undead, and to protect the innocent and those who join them in the fight for justice.")]),_c('h4',[_vm._v("Beyond the Mundane Life")]),_c('p',[_vm._v("Almost by definition, the life of a paladin is an adventuring life. Unless a lasting injury has taken him or her away from adventuring for a time, every paladin lives on the front lines of the cosmic struggle against evil. Fighters are rare enough among the ranks of the militias and armies of the world, but even fewer people can claim the true calling of a paladin. When they do receive the call, these warriors turn from their former occupations and take up arms to fight evil. Sometimes their oaths lead them into the service of the crown as leaders of elite groups of knights, but even then their loyalty is first to the cause of righteousness, not to crown and country. ")]),_c('p',[_vm._v("Adventuring paladins take their work seriously. A delve into an ancient ruin or dusty crypt can be a quest driven by a higher purpose than the acquisition of treasure. Evil lurks in dungeons and primeval forests, and even the smallest victory against it can tilt the cosmic balance away from oblivion.")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Divine Sense")]),_c('p',[_vm._v(" The presence of strong evil registers on your senses like a noxious odor, and powerful good rings like heavenly music in your ears. As an action, you can open your awareness to detect such forces. Until the end of your next turn, you know the location of any celestial, fiend, or undead within 60 feet of you that is not behind total cover. You know the type (celestial, fiend, or undead) of any being whose presence you sense, but not its identity. Within the same radius, you also detect the presence of any place or object that has been consecrated or desecrated, as with the hallow spell. You can use this feature a number of times equal to 1 + your Charisma modifier. When you finish a long rest, you regain all expended uses. ")]),_c('h4',[_vm._v("Lay on Hands")]),_c('p',[_vm._v("Your blessed touch can heal wounds. You have a pool of healing power that replenishes when you take a long rest. With that pool, you can restore a total number of hit points equal to your paladin level x 5. As an action, you can touch a creature and draw power from the pool to restore a number of hit points to that creature, up to the maximum amount remaining in your pool. ")]),_c('p',[_vm._v("Alternatively, you can expend 5 hit points from your pool of healing to cure the target of one disease or neutralize one poison affecting it. You can cure multiple diseases and neutralize multiple poisons with a single use of Lay on Hands, expending hit points separately for each one. This feature has no effect on undead and constructs. ")]),_c('h4',[_vm._v("Fighting Style")]),_c('p',[_vm._v(" 2nd level, you adopt a style of fighting as your specialty. Choose one of the following options. You cant take a Fighting Style option more than once, even if you later get to choose again. ")]),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Defense")]),_c('li',[_vm._v("Dueling")]),_c('li',[_vm._v("Great Weapon Fighting")]),_c('li',[_vm._v("Protection")])]),_c('h4',[_vm._v("Spellcasting")]),_c('p',[_vm._v("2nd level: You have learned to draw on divine magic through meditation and prayer to cast spells as a cleric does. ")]),_c('h4',[_vm._v("Divine Smite")]),_c('p',[_vm._v("2nd level: When you hit a creature with a melee weapon attack, you can expend one paladin spell slot to deal radiant damage to the target, in addition to the weapons damage. The extra damage is 2d8 for a 1st-level spell slot, plus 1d8 for each spell level higher than 1st, to a maximum of 5d8. The damage increases by 1d8 if the target is an undead or a fiend. ")]),_c('h4',[_vm._v("Divine Health")]),_c('p',[_vm._v("3rd level: The divine magic flowing through you makes you immune to disease. ")]),_c('h4',[_vm._v("Sacred Oath")]),_c('p',[_vm._v("3rd level: You swear the oath that binds you as a paladin forever. Up to this time you have been in a preparatory stage, committed to the path but not yet sworn to it. Now you choose the Oath of Devotion, the Oath of the Ancients, or the Oath of Vengeance, all detailed at the end of the class description. ")]),_c('p',[_vm._v("Your choice grants you features at 3rd level and again at 7th, 15th, and 20th level. Those features include oath spells and the Channel Divinity feature ")]),_c('h5',[_vm._v("Oath Spells")]),_c('p',[_vm._v("Each oath has a list of associated spells. You gain access to these spells at the levels specified in the oath description.")]),_c('h5',[_vm._v("Channel Divinity")]),_c('p',[_vm._v("Your oath allows you to channel divine energy to fuel magical effects. Each Channel Divinity option provided by your oath explains how to use it. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Extra Attack")]),_c('p',[_vm._v("5th level: You can attack twice, instead of once, whenever you take the Attack action on your turn. ")]),_c('h4',[_vm._v("Aura of Protection")]),_c('p',[_vm._v("6th level: Whenever you or a friendly creature within 10 feet of you must make a saving throw, the creature gains a bonus to the saving throw equal to your Charisma modifier (with a minimum bonus of +1). You must be conscious to grant this bonus. At 18th level, the range of this aura increases to 30 feet. ")]),_c('h4',[_vm._v("Aura of Courage")]),_c('p',[_vm._v("10th level, you and friendly creatures within 10 feet of you cant be frightened while you are conscious. At 18th level, the range of this aura increases to 30 feet. ")]),_c('h4',[_vm._v("Improved Divine Smite")]),_c('p',[_vm._v("11th level: You are so suffused with righteous might that all your melee weapon strikes carry divine power with them. Whenever you hit a creature with a melee weapon, the creature takes an extra 1d8 radiant damage. If you also use your Divine Smite with an attack, you add this damage to the extra damage of your Divine Smite. ")]),_c('h4',[_vm._v("Cleansing Touch")]),_c('p',[_vm._v("14th level: You can use your action to end one spell on yourself or on one willing creature that you touch. ")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Sacred Oaths")]),_c('p',[_vm._v("Becoming a paladin involves taking vows that commit the paladin to the cause of righteousness, an active path of fighting wickedness. The final oath, taken when he or she reaches 3rd level, is the culmination of all the paladins training. Some characters with this class dont consider themselves true paladins until they have reached 3rd level and made this oath. For others, the actual swearing of the oath is a formality, an official stamp on what has always been true in the paladins heart.")]),_c('h4',[_vm._v("Oath of Devotion")]),_c('p',[_vm._v("The Oath of Devotion binds a paladin to the loftiest ideals of justice, virtue, and order. Sometimes called cavaliers, white knights, or holy warriors, these paladins meet the ideal of the knight in shining armor, acting with honor in pursuit of justice and the greater good. They hold themselves to the highest standards of conduct, and some, for better or worse, hold the rest of the world to the same standards. Many who swear this oath are devoted to gods of law and good and use their gods tenets as the measure of their devotion. They hold angelsthe perfect servants of goodas their ideals, and incorporate images of angelic wings into their helmets or coats of arms. ")]),_c('h5',[_vm._v("Tenets of Devotion")]),_c('p',[_vm._v("Though the exact words and strictures of the Oath of Devotion vary, paladins of this oath share these tenets. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Honesty ")]),_vm._v(". Don't lie or cheat. Let your word be your promise. ")]),_c('li',[_c('strong',[_vm._v("Courage")]),_vm._v(". Never fear to act, though caution is wise. ")]),_c('li',[_c('strong',[_vm._v("Compassion")]),_vm._v(". Aid others, protect the weak, and punish those who threaten them. Show mercy to your foes, but temper it with wisdom. ")]),_c('li',[_c('strong',[_vm._v("Honor")]),_vm._v(". Treat others with fairness, and let your honorable deeds be an example to them. Do as much good as possible while causing the least amount of harm. ")]),_c('li',[_c('strong',[_vm._v("Duty")]),_vm._v(". Be responsible for your actions and their consequences, protect those entrusted to your care, and obey those who have just authority over you. ")])])]),_c('h5',[_vm._v("Oath Spells")]),_c('p',[_vm._v("You gain oath spells at the paladin levels listed.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("3rd")]),_vm._v(". "),_c('em',[_vm._v("protection from evil and good, sanctuary")])]),_c('li',[_c('strong',[_vm._v("5th")]),_vm._v(". "),_c('em',[_vm._v("lesser restoration, zone of truth")])]),_c('li',[_c('strong',[_vm._v("9th")]),_vm._v(". "),_c('em',[_vm._v("beacon of hope, dispel magic")])]),_c('li',[_c('strong',[_vm._v("13th")]),_vm._v(". "),_c('em',[_vm._v("freedom of movement, guardian of faith")])]),_c('li',[_c('strong',[_vm._v("17th")]),_vm._v(". "),_c('em',[_vm._v("commune, flame strike")])])]),_c('h5',[_vm._v("Channel Divinity")]),_c('p',[_vm._v("3rd level: You gain the following two Channel Divinity options. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Sacred Weapon.")]),_vm._v(" As an action, you can imbue one weapon that you are holding with positive energy, using your Channel Divinity. For 1 minute, you add your Charisma modifier to attack rolls made with that weapon (with a minimum bonus of +1). The weapon also emits bright light in a 20-foot radius and dim light 20 feet beyond that. If the weapon is not already magical, it becomes magical for the duration. ")]),_c('li',[_c('strong',[_vm._v("Turn the Unholy.")]),_vm._v(". As an action, you present your holy symbol and speak a prayer censuring fiends and undead, using your Channel Divinity. Each fiend or undead that can see or hear you within 30 feet of you must make a Wisdom saving throw. If the creature fails its saving throw, it is turned for 1 minute or until it takes damage. ")])])]),_c('h5',[_vm._v("Aura of Devotion")]),_c('p',[_vm._v("7th level: You and friendly creatures within 10 feet of you cant be charmed while you are conscious. At 18th level, the range of this aura increases to 30 feet ")]),_c('h5',[_vm._v("Purity of Spirit")]),_c('p',[_vm._v("15th level: You are always under the effects of a "),_c('em',[_vm._v("protection from evil and good spell")]),_vm._v(". ")]),_c('h4',[_vm._v("Oath of the Ancients")]),_c('p',[_vm._v("The Oath of the Ancients is as old as the race of elves and the rituals of the druids. Sometimes called fey knights, green knights, or horned knights, paladins who swear this oath cast their lot with the side of the light in the cosmic struggle against darkness because they love the beautiful and life-giving things of the world, not necessarily because they believe in principles of honor, courage, and justice. They adorn their armor and clothing with images of growing thingsleaves, antlers, or flowersto reflect their commitment to preserving life and light in the world. ")]),_c('h5',[_vm._v("Tenets of the Ancients")]),_c('p',[_vm._v("The tenets of the Oath of the Ancients have been preserved for uncounted centuries. This oath emphasizes the principles of good above any concerns of law or chaos. Its four central principles are simple. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Kindle the Light")]),_vm._v(". Through your acts of mercy, kindness, and forgiveness, kindle the light of hope in the world, beating back despair. ")]),_c('li',[_c('strong',[_vm._v("Shelter the Light")]),_vm._v(". Where there is good, beauty, love, and laughter in the world, stand against the wickedness that would swallow it. Where life flourishes, stand against the forces that would render it barren. ")]),_c('li',[_c('strong',[_vm._v("Preserve Your Own Light")]),_vm._v(". Delight in song and laughter, in beauty and art. If you allow the light to die in your own heart, you cant preserve it in the world. ")]),_c('li',[_c('strong',[_vm._v("Be the Light")]),_vm._v(". Be a glorious beacon for all who live in despair. Let the light of your joy and courage shine forth in all your deeds. ")])])]),_c('h5',[_vm._v("Oath Spells")]),_c('p',[_vm._v("You gain oath spells at the paladin levels listed.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("3rd")]),_vm._v(". "),_c('em',[_vm._v("ensnaring strike, speak with animals")])]),_c('li',[_c('strong',[_vm._v("5th")]),_vm._v(". "),_c('em',[_vm._v("moonbeam, misty step")])]),_c('li',[_c('strong',[_vm._v("9th")]),_vm._v(". "),_c('em',[_vm._v("plant growth, protection from energy")])]),_c('li',[_c('strong',[_vm._v("13th")]),_vm._v(". "),_c('em',[_vm._v("ice storm, stoneskin")])]),_c('li',[_c('strong',[_vm._v("17th")]),_vm._v(". "),_c('em',[_vm._v("commune with nature, tree stride")])])]),_c('h5',[_vm._v("Channel Divinity")]),_c('p',[_vm._v("3rd level: You gain the following two Channel Divinity options. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Natures Wrath.")]),_vm._v(" Invoke primeval forces to ensnare a foe. As an action, you can cause spectral vines to spring up and reach for a creature within 10 feet of you that you can see. The creature must succeed on a Strength or Dexterity saving throw (its choice) or be restrained. While restrained by the vines, the creature repeats the saving throw at the end of each of its turns. On a success, it frees itself and the vines vanish. ")]),_c('li',[_c('strong',[_vm._v("Turn the Faithless.")]),_vm._v(". Utter ancient words that are painful for fey and fiends to hear. As an action, you present your holy symbol, and each fey or fiend within 30 feet of you that can hear you must make a Wisdom saving throw. On a failed save, the creature is turned for 1 minute or until it takes damage. ")])])]),_c('h5',[_vm._v("Aura of Warding")]),_c('p',[_vm._v("7th level: Ancient magic lies so heavily upon you that it forms an eldritch ward. You and friendly creatures within 10 feet of you have resistance to damage from spells. At 18th level, the range of this aura increases to 30 feet ")]),_c('h5',[_vm._v("Undying Sentinel")]),_c('p',[_vm._v("15th level: When you are reduced to O hit points and are not killed outright, you can choose to drop to 1 hit point instead. Once you use this ability, you can't use it again until you finish a long rest. Additionally, you suffer none of the drawbacks of old age, and you cant be aged magically. ")]),_c('h5',[_vm._v("Elder Champion")]),_c('p',[_vm._v("20th level: You can assume the form of an ancient force of nature, taking on an appearance you choose. For example, your skin might turn green or take ona bark-like texture, your hair might become leafy or moss- like, or you might sprout antlers or a lion-like mane. Using your action, you undergo a transformation. For 1 minute, you gain the following benefits: "),_c('ul',[_c('li',[_vm._v("At the start of each of your turns, you regain 10 hit points.")]),_c('li',[_vm._v("Whenever you cast a paladin spell that has a casting time of 1 action, you can cast it using a bonus action instead.")]),_c('li',[_vm._v(" Enemy creatures within 10 feet of you have disadvantage on saving throws against your paladin spells and Channel Divinity options. ")])]),_vm._v(" Once you use this feature, you cant use it again until you finish a long rest. ")]),_c('h4',[_vm._v("Oath of Vengeance")]),_c('p',[_vm._v("The Oath of Vengeance is a solemn commitment to punish those who have committed a grievous sin. When evil forces slaughter helpless villagers, when an entire people turns against the will of the gods, when a thieves guild grows too violent and powerful, when a dragon rampages through the countrysideat times like these, paladins arise and swear an Oath of Vengeance to set right that which has gone wrong. To these paladins sometimes called avengers or dark knightstheir own purity is not as important as delivering justice. ")]),_c('h5',[_vm._v("Tenets of Vengeance")]),_c('p',[_vm._v("The tenets of the Oath of Vengeance vary by paladin, but all the tenets revolve around punishing wrongdoers by any means necessary. Paladins who uphold these tenets are willing to sacrifice even their own righteousness to mete out justice upon those who do evil, so the paladins are often neutral or lawful neutral in alignment. The core principles of the tenets are brutally simple. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Fight the Greater Evil")]),_vm._v(". Faced with a choice of fighting my sworn foes or combating a lesser evil. I choose the greater evil. ")]),_c('li',[_c('strong',[_vm._v("No Mercy for the Wicked")]),_vm._v(". Ordinary foes might win my mercy, but my sworn enemies do not. ")]),_c('li',[_c('strong',[_vm._v("By Any Means Necessary")]),_vm._v(". My qualms can't get in the way of exterminating my foes. ")]),_c('li',[_c('strong',[_vm._v("Restitution")]),_vm._v(". If my foes wreak ruin on the world, it is because I failed to stop them. I must heip those harmed by their misdeeds. ")])])]),_c('h5',[_vm._v("Oath Spells")]),_c('p',[_vm._v("You gain oath spells at the paladin levels listed.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("3rd")]),_vm._v(". "),_c('em',[_vm._v("bane, hunters mark")])]),_c('li',[_c('strong',[_vm._v("5th")]),_vm._v(". "),_c('em',[_vm._v("hold person, misty step")])]),_c('li',[_c('strong',[_vm._v("9th")]),_vm._v(". "),_c('em',[_vm._v("haste, protection from energy")])]),_c('li',[_c('strong',[_vm._v("13th")]),_vm._v(". "),_c('em',[_vm._v("banishment, dimension door")])]),_c('li',[_c('strong',[_vm._v("17th")]),_vm._v(". "),_c('em',[_vm._v("hold monster, scrying")])])]),_c('h5',[_vm._v("Channel Divinity")]),_c('p',[_vm._v("3rd level: You gain the following two Channel Divinity options. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Abjure Enemy.")]),_vm._v(" As an action, you present your holy symbol and speak a prayer of denunciation, using your Channel Divinity. Choose one creature within 60 feet of you that you can see. That creature must make a Wisdom saving throw, unless it is immune to being frightened. Fiends and undead have disadvantage on this saving throw. ")]),_c('li',[_c('strong',[_vm._v("Vow of Enmity.")]),_vm._v(". As a bonus action, you can utter a vow of enmity against a creature you can see within 10 feet of you, using your Channel Divinity. You gain advantage on attack rolls against the creature for 1 minute or until it drops to 0 hit points or falls unconscious. ")])])]),_c('h5',[_vm._v("Relentless Avenger")]),_c('p',[_vm._v("7th level: Your supernatural focus helps you close off a foes retreat. When you hit a creature with an opportunity attack, you can move up to half your speed immediately after the attack and as part of the same reaction. This movement doesnt provoke opportunity attacks. ")]),_c('h5',[_vm._v("Soul of Vengeance")]),_c('p',[_vm._v("15th level: The authority with which you speak your Vow of Enmity gives you greater power over your foe. When a creature under the effect of your Vow of Enmity makes an attack, you can use your reaction to make a melee weapon attack against that creature if it is within range. ")]),_c('h5',[_vm._v("Avenging Angel")]),_c('p',[_vm._v("20th level: You can assume the form of an angelic avenger. Using your action, you undergo a transformation. For 1 hour, you gain the following benefits: "),_c('ul',[_c('li',[_vm._v("Wings sprout from your back and grant you a flying speed of 60 feet. ")]),_c('li',[_vm._v("You emanate an aura of menace in a 30-foot radius. The first time any enemy creature enters the aura or starts its turn there during a battle, the creature must succeed on a Wisdom saving throw or become frightened of you for 1 minute or until it takes any damage. Attack rolls against the frightened creature have advantage. ")])]),_vm._v(" Once you use this feature, you cant use it again until you finish a long rest. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"ranger-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/ranger.svg"),"alt":"Ranger Icon"}}),_vm._v(" Ranger ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/ranger.png"),"aria-label":"Ranger Image","alt":"Ranger Image"}}),_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d10 per ranger level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("10 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d10 (or 6) + your Constitution modifier per ranger level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" Light armor, medium armor, shields")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, martial weapons")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Dexterity, Strength")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose three from Animal Handling, Athletics, Insight, Investigation, Nature, Perception, Stealth, and Survival")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) scale mail or (b) leather armor")]),_c('li',[_vm._v("(a) two shortswords or (b) two simple melee weapons")]),_c('li',[_vm._v("(a) a dungeoneers pack or (b) an explorers pack")]),_c('li',[_vm._v("A longbow and a quiver of 20 arrows")])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Rough and wild looking, a human stalks alone through the shadows of trees, hunting the orcs he knows are planning a raid on a nearby farm. Clutching a shortsword in each hand, he becomes a whirlwind of steel, cutting down one enemy after another. After tumbling away from a cone of freezing air, an elf finds her feet and draws back her bow to loose an arrow at the white dragon. Shrugging off the wave of fear that emanates from the dragon like the cold of its breath, she sends one arrow after another to find the gaps between the  dragons thick scales. Holding his hand high, a half-elf whistles to the hawk that circles high above him, calling the bird back to his side. Whispering instructions in Elvish, he points to the owlbear hes been tracking and sends the hawk to distract the creature while he readies his bow. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Far from the bustle of cities and towns, past the hedges that shelter the most distant farms from the terrors of the wild, amid the dense-packed trees of F trackless forests and across wide and empty plains, rangers keep their unending watch. ")]),_c('h4',[_vm._v("Deadly Hunters")]),_c('p',[_vm._v("Warriors of the wilderness, rangers specialize in hunting the monsters that threaten the edges of civilizationhumanoid raiders, rampaging beasts and monstrosities, terrible giants, and deadly dragons. They learn to track their quarry as a predator does, moving stealthily through the wilds and hiding themselves in brush and rubble. Rangers focus their combat training on techniques that are particularly useful against their specific favored foes. ")]),_c('p',[_vm._v("Thanks to their familiarity with the wilds, rangers acquire the ability to cast spells that harness natures power, much as a druid does. Their spells, like their combat abilities, emphasize speed, stealth, and the hunt. A rangers talents and abilities are honed with deadly focus on the grim task of protecting the borderlands.")]),_c('h4',[_vm._v("Independent Adventurers")]),_c('p',[_vm._v("Though a ranger might make a living as a hunter, a guide, or a tracker, a rangers true calling is to defend the outskirts of civilization from the ravages of monsters and humanoid hordes that press in from the wild. In some places, rangers gather in secretive orders or join forces with druidic circles. Many rangers, though, are independent almost to a fault, knowing that, when a dragon or a band of orcs attacks, a ranger might be the firstand possibly the lastline of defense. ")]),_c('p',[_vm._v("This fierce independence makes rangers well suited to adventuring, since they are accustomed to life far from the comforts of a dry bed and a hot bath. Faced with city-bred adventurers who grouse and whine about the hardships of the wild, rangers respond with some mixture of amusement, frustration, and compassion. But they quickly learn that other adventurers who can carry their own weight in a fight against civilizations foes are worth any extra burden. Coddled city folk might not know how to feed themselves or find fresh water in the wild, but they make up for it in other ways. ")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Favored Enemy")]),_c('p',[_vm._v("1st level: You have significant experience studying, tracking, hunting, and even talking to a certain type of enemy. Choose a type of favored enemy: aberrations, beasts, celestials, constructs, dragons, elementals, fey, fiends, giants, monstrosities, oozes, plants, or undead. Alternatively, you can select two races of humanoid (such as gnolls and orcs) as favored enemies. ")]),_c('p',[_vm._v("You have advantage on Wisdom (Survival) checks to track your favored enemies, as well as on Intelligence checks to recall information about them. When you gain this feature, you also learn one language of your choice that is spoken by your favored enemies, if they speak one at all. You choose one additional favored enemy, as well as an associated language, at 6th and 14th level. As you gain levels, your choices should reflect the types of monsters you have encountered on your adventures. ")]),_c('h4',[_vm._v("Natural Explorer")]),_c('p',[_vm._v("You are particularly familiar with one type of natural environment and are adept at traveling and surviving in such regions. Choose one type of favored terrain: arctic, coast, desert, forest, grassland, mountain, swamp, or the Underdark. While traveling for an hour or more in your favored terrain, you gain the following benefits:")]),_c('ul',[_c('li',[_vm._v("Difficult terrain doesnt slow your groups travel. ")]),_c('li',[_vm._v("Your group cant become lost except by magical means.")]),_c('li',[_vm._v("Even when you are engaged in another activity while traveling (such as foraging, navigating, or tracking), you remain alert to danger. ")]),_c('li',[_vm._v("If you are traveling alone, you can move stealthily at a normal pace. ")]),_c('li',[_vm._v("When you forage, you find twice as much food as you normally would. ")]),_c('li',[_vm._v("While tracking other creatures, you also learn their exact number, their sizes, and how long ago they passed through the area. ")]),_vm._v(" You choose additional favored terrain types at 6th and 10th level. ")]),_c('h4',[_vm._v("Fighting Style")]),_c('p',[_vm._v("2nd level, you adopt a particular style of fighting as your specialty. Choose one of the following options. ")]),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Archery")]),_c('li',[_vm._v("Defense")]),_c('li',[_vm._v("Dueling")]),_c('li',[_vm._v("Two-Weapon Fighting")])]),_c('h4',[_vm._v("Spellcasting")]),_c('p',[_vm._v("2nd level: You have learned to use the magical essence of nature to cast spells, much as a druid does. ")]),_c('h4',[_vm._v("Ranger Archetype")]),_c('p',[_vm._v("3rd level: You choose an archetype that you strive to emulate: Hunter or Beast Master, both detailed at the end of the class description. Your choice grants you features at 3rd level and again at 7th, 11th, and 15th level. ")]),_c('h4',[_vm._v("Primeval Awareness")]),_c('p',[_vm._v("3rd level: You can use your action and expend one ranger spell slot to focus your awareness on the region around you. For 1 minute per level of the spell slot you expend, you can sense whether the following types of creatures are present within 1 mile of you (or within up to 6 miles if you are in your favored terrain): aberrations, celestials, dragons, elementals, fey, fiends, and undead. This feature doesnt reveal the creatures location or number. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Extra Attack")]),_c('p',[_vm._v("5th level, you can attack twice, instead of once, whenever you take the Attack action on your turn. ")]),_c('h4',[_vm._v("Land's Stride")]),_c('p',[_vm._v("8th level: Moving through nonmagical difficult terrain costs you no extra movement. You can also pass through nonmagical plants without being slowed by them and without taking damage from them if they have thorns, spines, or a similar hazard. ")]),_c('h4',[_vm._v("Hide in Plain Sight")]),_c('p',[_vm._v("10th level: You can spend 1 minute creating camouflage for yourself. You must have access to fresh mud, dirt, plants, soot, and other naturally occurring materials with which to create your camouflage. Once you are camouflaged in this way, you can try to hide by pressing yourself up against a solid surface, such as a tree or wall, that is at least as tall and wide as you are. ")]),_c('h4',[_vm._v("Vanish")]),_c('p',[_vm._v("14th level: You can use the Hide action as a bonus action on your turn. Also, you cant be tracked by nonmagical means, unless you choose to leave a trail. ")]),_c('h4',[_vm._v("Feral Senses")]),_c('p',[_vm._v("18th level: You gain preternatural senses that help you fight creatures you cant see. When you attack a creature you can't see, your inability to see it doesnt impose disadvantage on your attack rolls against it. ")]),_c('p',[_vm._v(" You are also aware of the location of any invisible creature within 30 feet of you, provided that the creature isnt hidden from you and you aren't blinded or deafened. ")]),_c('h4',[_vm._v("Foe Slayer")]),_c('p',[_vm._v("20th level: You become an unparalleled hunter of your enemies. Once on each of your turns, you can add your Wisdom modifier to the attack roll or the damage roll of an attack you make against one of your favored enemies. You can choose to use this feature before or after the roll, but before any effects of the roll are applied. ")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Ranger Archetypes")]),_c('p',[_vm._v("The ideal of the ranger has two classic expressions: the Hunter and the Beast Master.")]),_c('h4',[_vm._v("Hunter")]),_c('p',[_vm._v("Emulating the Hunter archetype means accepting your place as a bulwark between civilization and the terrors of the wilderness. As you walk the Hunters path, you learn specialized techniques for fighting the threats you face, from rampaging ogres and hordes of orcs to towering giants and terrifying dragons. ")]),_c('h5',[_vm._v("Hunter's Prey")]),_c('p',[_vm._v("3rd level: You gain one of the following features of your choice.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Colossus Slayer ")]),_vm._v(". Your tenacity can wear down the most potent foes. When you hit a creature with a weapon attack, the creature takes an extra 1d8 damage if its below its hit point maximum. You can deal this extra damage only once per turn. ")]),_c('li',[_c('strong',[_vm._v("Giant Killer")]),_vm._v(". When a Large or larger creature within 5 feet of you hits or misses you with an attack, you can use your reaction to attack that creature immediately after its attack, provided that you can see the creature. ")]),_c('li',[_c('strong',[_vm._v("Horde Breaker")]),_vm._v(". Once on each of your turns when you make a weapon attack, you can make another attack with the same weapon against a different creature that is within 5 feet of the original target and within range of your weapon. ")])]),_c('h5',[_vm._v("Defensive Tactics")]),_c('p',[_vm._v("7th level: You gain one of the following features of your choice.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Escape the Horde")]),_vm._v(". Opportunity attacks against you are made with disadvantage. ")]),_c('li',[_c('strong',[_vm._v("Multiattack Defense")]),_vm._v(". When a creature hits you with an attack, you gain a +4 bonus to AC against all subsequent attacks made by that creature for the rest of the turn. ")]),_c('li',[_c('strong',[_vm._v("Steel Will")]),_vm._v(". You have advantage on saving throws against being frightened.")])]),_c('h5',[_vm._v("Multiattack")]),_c('p',[_vm._v("11th level: You gain one of the following features of your choice.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Volley")]),_vm._v(". You can use your action to make a ranged attack against any number of creatures within 10 feet of a point you can see within your weapons range. You must have ammunition for each target, as normal, and you make a separate attack roll for each target. ")]),_c('li',[_c('strong',[_vm._v("Whirlwind Attack")]),_vm._v(". You can use your action to make a melee attack against any number of creatures within 5 feet of you, with a separate attack roll for each target. ")]),_c('li',[_c('strong',[_vm._v("Steel Will")]),_vm._v(". You have advantage on saving throws against being frightened.")])]),_c('h5',[_vm._v("Superior Hunter's Defense")]),_c('p',[_vm._v("15th level: You gain one of the following features of your choice.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Evasion")]),_vm._v(". You can nimbly dodge out of the way of certain area effects, such as a red dragons fiery breath or a lightning bolt spell. When you are subjected to an effect that allows you to make a Dexterity saving throw to take only half damage, you instead take no damage if you succeed on the saving throw, and only half damage if you fail. ")]),_c('li',[_c('strong',[_vm._v("Stand Against the Tide")]),_vm._v(". When a hostile creature misses you with a melee attack, you can use your reaction to force that creature to repeat the same attack against another creature (other than itself) of your choice. ")]),_c('li',[_c('strong',[_vm._v("Uncanny Dodge")]),_vm._v(". When an attacker that you can see hits you with an attack, you can use your reaction to halve the attacks damage against you.")])]),_c('h4',[_vm._v("Beast Master")]),_c('p',[_vm._v("The Beast Master archetype embodies a friendship between the civilized races and the beasts of the world. United in focus, beast and ranger work as one to fight the monstrous foes that threaten civilization and the wilderness alike. Emulating the Beast Master archetype means committing yourself to this ideal, working in partnership with an animal as its companion and friend. ")]),_c('h5',[_vm._v("Ranger's Companion")]),_c('p',[_vm._v("3rd level: You gain a beast companion that accompanies you on your adventures and is trained to fight alongside you. Choose a beast that is no larger than Medium and that has a challenge rating of 1/4 or lower (appendix D presents statistics for the hawk, mastiff, and panther as examples). Add your proficiency bonus to the beasts AC, attack rolls, and damage rolls, as well as to any saving throws and skills it is proficient in. Its hit point maximum equals its normal maximum or four times your ranger level, whichever is higher. ")]),_c('p',[_vm._v("The beast obeys your commands as best as it can. It takes its turn on your initiative, though it doesnt take an action unless you command it to. On your turn, you can verbally command the beast where to move (no action required by you). You can use your action to verbally command it to take the Attack, Dash, Disengage, Dodge, or Help action. Once you have the Extra Attack feature, you can make one weapon attack yourself when you command the beast to take the Attack action. ")]),_c('p',[_vm._v(" While traveling through your favored terrain with only the beast, you can move stealthily at a normal pace. If the beast dies, you can obtain another one by spending 8 hours magically bonding with another beast that isnt hostile to you, either the same type of beast as before or a different one. ")]),_c('h5',[_vm._v("Exceptional Training")]),_c('p',[_vm._v("7th level: On any of your turns when your beast companion doesnt attack, you can use a bonus action to command the beast to take the Dash, Disengage, Dodge, or Help action on its turn. ")]),_c('h5',[_vm._v("Bestial Fury")]),_c('p',[_vm._v("11th level: Your beast companion can make two attacks when you command it to use the Attack action.")]),_c('h5',[_vm._v("Share Spells")]),_c('p',[_vm._v("15th level: When you cast a spell targeting yourself, you can also affect your beast companion with the spell if the beast is within 30 feet of you. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"rogue-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/rogue.svg"),"aria-label":"Rogue Icon","alt":"rogue Icon"}}),_vm._v(" Rogue ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/rogue.png"),"aria-label":"Rogue Image","alt":"Rogue Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d8 per rogue level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("8 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d8 (or 5) + your Constitution modifier per rogue level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" Light armor")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons, hand crossbows, longswords, rapiers, shortswords")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" Thieves' Tools ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Dexterity, Intelligence")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose four from Acrobatics, Athletics, Deception. Insight, Intimidation, Investigation, Perception. Performance. Persuasion, Sleight of Hand. and Stealth")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a rapier or (b) a shortsword")]),_c('li',[_vm._v("(a) a shortbow and quiver of 20 arrows or (b) a shortsword")]),_c('li',[_vm._v("(a) a burglars pack, (b) a dungeoneers pack, or (c) an explorers pack")]),_c('li',[_vm._v("Leather armor, two daggers, and thieves tools")])])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Signaling for her companions to wait, a halfling creeps forward through the dungeon hall. She presses an ear to the door, then pulls out a set of tools and picks the lock in the blink of an eye. Then she disappears into the shadows as her fighter friend moves forward to kick the door open. A human lurks in the shadows of an alley while his accomplice prepares for her part in the ambush. When their targeta notorious slaverpasses the alleyway, the accomplice cries out, the slaver comes to investigate, and the assassins blade cuts his throat before he can make a sound. Suppressing a giggle, a gnome waggles her fingers and magically lifts the key ring from the guards belt. In a moment, the keys are in her hand, the cell door is open, and she and her companions are free to make their escape. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Rogues rely on skill, stealth, and their foes vulnerabilities to get the upper hand in any situation. They have a knack for finding the solution to just about any problem, demonstrating a resourcefulness and versatility that is the cornerstone of any successful adventuring party. ")]),_c('h4',[_vm._v("Skill and Precision")]),_c('p',[_vm._v("Rogues devote as much effort to mastering the use of a variety of skills as they do to perfecting their combat abilities, giving them a broad expertise that few other characters can match. Many rogues focus on stealth and deception, while others refine the skills that help them in a dungeon environment, such as climbing, finding and disarming traps, and opening locks. ")]),_c('p',[_vm._v("When it comes to combat, rogues prioritize cunning over brute strength. A rogue would rather make one precise strike, placing it exactly where the attack will hurt the target most, than wear an opponent down with a barrage of attacks. Rogues have an almost supernatural knack for avoiding danger, and a few learn magical tricks to supplement their other abilities. ")]),_c('h4',[_vm._v("A Shady Living")]),_c('p',[_vm._v("Every town and city has its share of rogues. Most of them live up to the worst stereotypes of the class, making a living as burglars, assassins, cutpurses, and con artists. Often, these scoundrels are organized into thieves guilds or crime families. Plenty of rogues operate independently, but even they sometimes recruit apprentices to help them in their scams and heists. A few rogues make an honest living as locksmiths, investigators, or exterminators, which can be a dangerous job in a world where dire ratsand wereratshaunt the sewers. ")]),_c('p',[_vm._v("As adventurers, rogues fall on both sides of the law. Some are hardened criminals who decide to seek their fortune in treasure hoards, while others take up a life of adventure to escape from the law. Some have learned and perfected their skills with the explicit purpose of infiltrating ancient ruins and hidden crypts in search of treasure. ")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Expertise")]),_c('p',[_vm._v("1st level: Choose two of your skill proficiencies, or one of your skill proficiencies and your proficiency with thieves tools. Your proficiency bonus is doubled for any ability check you make that uses either of the chosen proficiencies. At 6th level, you can choose two more of your proficiencies (in skills or with thieves tools) to gain this benefit. ")]),_c('h4',[_vm._v("Sneak Attack")]),_c('p',[_vm._v("1st level: You know how to strike subtly and exploit a foes distraction. Once per turn, you can deal an extra 1d6 damage to one creature you hit with an attack if you have advantage on the attack roll. The attack must use a finesse or a ranged weapon. ")]),_c('p',[_vm._v("You dont need advantage on the attack roll if another enemy of the target is within 5 feet of it, that enemy isnt incapacitated, and you don't have disadvantage on the attack roll. ")]),_c('h4',[_vm._v("Thieves' Cant")]),_c('p',[_vm._v("During your rogue training you learned thieves cant, a secret mix of dialect, jargon, and code that allows you to hide messages in seemingly normal conversation. Only another creature that knows thieves cant understands such messages. It takes four times longer to convey such a message than it does to speak the same idea plainly.")]),_c('p',[_vm._v("In addition, you understand a set of secret signs and symbols used to convey short, simple messages, such as whether an area is dangerous or the territory of a thieves guild, whether loot is nearby, or whether the people in an area are easy marks or will provide a safe house for thieves on the run. ")]),_c('h4',[_vm._v("Cunning Action")]),_c('p',[_vm._v("2nd level: Your quick thinking and agility allow you to move and act quickly. You can take a bonus action on each of your turns in combat. This action can be used only to take the Dash, Disengage, or Hide action. ")]),_c('h4',[_vm._v("Roguish Archetype")]),_c('p',[_vm._v("3rd level: You choose an archetype that you emulate in the exercise of your rogue abilities: Thief, Assassin, or Arcane Trickster, all detailed at the end of the class description. Your archetype choice grants you features at 3rd level and then again at 9th, 13th, and 17th level. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Uncanny Dodge")]),_c('p',[_vm._v("5th level: When an attacker that you can see hits you with an attack, you can use your reaction to halve the attacks damage against you. ")]),_c('h4',[_vm._v("Evasion")]),_c('p',[_vm._v("7th level: You can nimbly dodge out of the way of certain area effects, such as a red dragons fiery breath or an ice storm spell. When you are subjected to an effect that allows you to make a Dexterity saving throw to take only half damage, you instead take no damage if you succeed on the saving throw, and only half damage if you fail. ")]),_c('h4',[_vm._v("Reliable Talent")]),_c('p',[_vm._v("11th level: You have refined your chosen skills until they approach perfection. Whenever you make an ability check that lets you add your proficiency bonus, you can treat a d20 roll of 9 or lower as a 10. ")]),_c('h4',[_vm._v("Blindsense")]),_c('p',[_vm._v("14th level: If you are able to hear, you are aware of the location of any hidden or invisible creature within 10 feet of you. ")]),_c('h4',[_vm._v("Slippery Mind")]),_c('p',[_vm._v("15th level: You have acquired greater mental strength. You gain proficiency in Wisdom saving throws. ")]),_c('h4',[_vm._v("Elusive")]),_c('p',[_vm._v("18th level: You are so evasive that attackers rarely gain the upper hand against you. No attack roll has advantage against you while you aren't incapacitated. ")]),_c('h4',[_vm._v("Stroke of Luck")]),_c('p',[_vm._v("20th level: You have an uncanny knack for succeeding when you need to. If your attack misses a target within range, you can turn the miss into a hit. Alternatively, if you fail an ability check, you can treat the d20 roll as a 20. Once you use this feature, you cant use it again until you finish a short or long rest. ")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Roguish Archetypes")]),_c('p',[_vm._v("Rogues have many features in common, including their emphasis on perfecting their skills, their precise and deadly approach to combat, and their increasingly quick reflexes. But different rogues steer those talents in varying directions, embodied by the rogue archetypes. Your choice of archetype is a reflection of your focus not necessarily an indication of your chosen profession, but a description of your preferred techniques. ")]),_c('h4',[_vm._v("Thief")]),_c('p',[_vm._v("You hone your skills in the larcenous arts. Burglars, bandits, cutpurses, and other criminals typically follow this archetype, but so do rogues who prefer to think of themselves as professional treasure seekers, explorers, delvers, and investigators. In addition to improving your agility and stealth, you learn skills useful for delving into ancient ruins, reading unfamiliar languages, and using magic items you normally couldnt employ. ")]),_c('h5',[_vm._v("Fast Hands")]),_c('p',[_vm._v("3rd level: You can use the bonus action granted by your Cunning Action to make a Dexterity (Sleight of Hand) check, use your thieves tools to disarm a trap or open a lock, or take the Use an Object action. ")]),_c('h5',[_vm._v("Second-Story Work")]),_c('p',[_vm._v("3rd level: You gain the ability to climb faster than normal; climbing no longer costs you extra movement. In addition, when you make a running jump, the distance you cover increases by a number of feet equal to your Dexterity modifier. ")]),_c('h5',[_vm._v("Supreme Sneak")]),_c('p',[_vm._v("9th Level: You have advantage on a Dexterity (Stealth) check if you move no more than half your speed on the same turn.")]),_c('h5',[_vm._v("Use Magic Device")]),_c('p',[_vm._v("13th level: You have learned enough about the workings of magic that you can improvise the use of items even when they are not intended for you. You ignore all class, race, and level requirements on the use of magic items. ")]),_c('h5',[_vm._v("Thief's Reflexes")]),_c('p',[_vm._v("17th level: You have become adept at laying ambushes and quickly escaping danger. You can take two turns during the first round of any combat. You take your first turn at your normal initiative and your second turn at your initiative minus 10. You can't use this feature when you are surprised. ")]),_c('h4',[_vm._v("Assassin")]),_c('p',[_vm._v("You focus your training on the grim art of death. Those who adhere to this archetype are diverse: hired killers, spies, bounty hunters, and even specially anointed priests trained to exterminate the enemies of their deity. Stealth, poison, and disguise help you eliminate your foes with deadly efficiency. ")]),_c('h5',[_vm._v("Bonus Proficiencies")]),_c('p',[_vm._v("3rd level: You gain proficiency with the disguise kit and the poisoners kit. ")]),_c('h5',[_vm._v("Assassinate")]),_c('p',[_vm._v("3rd level: You are at your deadliest when you get the drop on your enemies. You have advantage on attack rolls against any creature that hasn't taken a turn in the combat yet. In addition, any hit you score against a creature that is surprised is a critical hit. ")]),_c('h5',[_vm._v("Infiltration Expertise")]),_c('p',[_vm._v("9th level: You can unfailingly create false identities for yourself. You must spend seven days and 25 gp to establish the history, profession, and affiliations for an identity. You cant establish an identity that belongs to someone else. For example, you might acquire appropriate clothing, letters of introduction, and officiallooking certification to establish yourself as a member of a trading house from a remote city so you can insinuate yourself into the company of other wealthy merchants. Thereafter, if you adopt the new identity as a disguise, other creatures believe you to be that person until given an obvious reason not to. ")]),_c('h5',[_vm._v("Impostor")]),_c('p',[_vm._v("13th level: You gain the ability to unerringly mimic another persons speech, writing, and behavior. You must spend at least three hours studying these three components of the person's behavior, listening to speech, examining handwriting, and observing mannerisms. Your ruse is indiscernible to the casual observer. If a wary creature suspects something is amiss, you have advantage on any Charisma (Deception) check you make to avoid detection. ")]),_c('h5',[_vm._v("Death Strike")]),_c('p',[_vm._v("17th level: You become a master of instant death. When you attack and hit a creature that is surprised, it must make a Constitution saving throw (DC 8 + your Dexterity modifier + your proficiency bonus). On a failed save, double the damage of your attack against the creature. ")]),_c('h4',[_vm._v("Arcane Trickster")]),_c('p',[_vm._v("Some rogues enhance their fine-honed skills of stealth and agility with magic, learning tricks of enchantment and illusion. These rogues include pickpockets and burglars, but also pranksters, mischief-makers, and a significant number of adventurers. ")]),_c('h5',[_vm._v("Spellcasting")]),_c('p',[_vm._v("3rd level: You gain the ability to cast spells.")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Cantrips ")]),_vm._v(". You learn three cantrips: mage hand and two other cantrips of your choice from the wizard spell list. You learn another wizard cantrip of your choice at 10th level. ")]),_c('li',[_c('strong',[_vm._v("Spell Slots")]),_vm._v(". The Arcane Trickster Spellcasting table (#) shows how many spell slots you have to cast your spells of 1st level and higher. To cast one of these spells, you must expend a slot of the spells level or higher. You regain all expended spell slots when you finish a long rest. ")]),_c('li',[_c('strong',[_vm._v("Spells Known of 1st Level or Higher")]),_vm._v(". You know three 1st-level wizard spells of your choice, two of which you must choose from the enchantment and illusion spells on the wizard spell list. When you reach 7th level in this class, you can learn one new spell of 1st or 2nd level. The spells you learn at 8th, 14th, and 20th level can come from any school of magic. ")]),_c('li',[_c('strong',[_vm._v("Spellcasting Ability")]),_vm._v(". Intelligence is your spelicasting ability for your wizard spells, since you learn your spells through dedicated study and memorization. You use your Intelligence whenever a spell refers to your spellcasting ability. In addition, you use your Intelligence modifier when setting the saving throw DC for a wizard spell you cast and when making an attack roll with one.")])]),_c('h5',[_vm._v("Mage Hand Legerdemain")]),_c('p',[_vm._v("3rd level: When you cast mage hand, you can make the spectral hand invisible, and you can perform the following additional tasks with it: "),_c('ul',[_c('li',[_vm._v("You can stow one object the hand is holding ina container worn or carried by another creature.")]),_c('li',[_vm._v("You can retrieve an object in a container worn or carried by another creature.")]),_c('li',[_vm._v("You can use thieves tools to pick locks and disarm traps at range.")])])]),_c('h5',[_vm._v("Magical Ambush")]),_c('p',[_vm._v("9th level: If you are hidden from a creature when you cast a spell on it, the creature has disadvantage on any saving throw it makes against the spell this turn. ")]),_c('h5',[_vm._v("Versatile Trickster")]),_c('p',[_vm._v("13th level: You gain the ability to distract targets with your mage hand. As a bonus action on your turn, you can designate a creature within 5 feet of the spectral hand created by the spell. Doing so gives you advantage on attack rolls against that creature until the end of the turn ")]),_c('h5',[_vm._v("Spell Thief")]),_c('p',[_vm._v("17th level: You gain the ability to.magically steal the knowledge of how to cast a spell from another spellcaster. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"sorcerer-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/sorcerer.svg"),"aria-label":"Sorcerer Icon","alt":"Sorcerer Icon"}}),_vm._v(" Sorcerer ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/sorcerer.png"),"aria-label":"Sorcerer Image","alt":"Sorcerer Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d6 per sorcerer level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("6 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d6 (or 4) + your Constitution modifier per sorcerer level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" None")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Daggers, darts, slings, quarterstaffs, light crossbows")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Constitution, Charisma")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two from Arcana, Deception, Insight, Intimidation, Persuasion, and Religion")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a light crossbow and 20 bolts or (b) any simple weapon")]),_c('li',[_vm._v("(a) a component pouch or (b) an arcane focus")]),_c('li',[_vm._v("(a) a dungeoneers pack or (b) an explorers pack")]),_c('li',[_vm._v("Two daggers")])])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Golden eyes flashing, a human stretches out her hand and unleashes the dragonfire that burns in her veins. As an inferno rages around her foes, leathery wings spread from her back and she takes to the air. Long hair whipped by a conjured wind, a half-elf spreads his arms wide and throws his head back. Lifting him momentarily off the ground, a wave of magic surges up in him, through him, and out from him in a mighty blast of lightning. Crouching behind a stalagmite, a halfling points a finger at a charging troglodyte. A blast of fire springs from her finger to strike the creature. She ducks back behind the rock formation with a grin, unaware that her wild magic has turned her skin bright blue. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Sorcerers carry a magical birthright conferred upon them by an exotic bloodline, some otherworldly influence, or exposure to unknown cosmic forces. One cant study sorcery as one learns a language, any more than one can learn to live a legendary life. No one chooses sorcery; the power chooses the sorcerer. ")]),_c('h4',[_vm._v("Raw Magic")]),_c('p',[_vm._v("Magic is a part of every sorcerer, suffusing body, mind, and spirit with a latent power that waits to be tapped. Some sorcerers wield magic that springs from an ancient bloodline infused with the magic of dragons. Others carry a raw, uncontrolled magic within them, a chaotic storm that manifests in unexpected ways. ")]),_c('p',[_vm._v("The appearance of sorcerous powers is wildly unpredictable. Some draconic bloodlines produce exactly one sorcerer in every generation, but in other lines of descent every individual is a sorcerer. Most of the time, the talents of sorcery appear as apparent flukes. Some sorcerers can't name the origin of their power, while others trace it to strange events in their own lives. The touch of a demon, the blessing of a dryad at a babys birth, or a taste of the water from a mysterious spring might spark the gift of sorcery. So too might the gift of a deity of magic, exposure to the elemental forces of the Inner Planes or the maddening chaos of Limbo, or a glimpse into the inner workings of reality. ")]),_c('p',[_vm._v("Sorcerers have no use for the spellbooks and ancient tomes of magic lore that wizards rely on, nor do they rely on a patron to grant their spells as warlocks do. By learning to harness and channel their own inborn magic, they can discover new and staggering ways to unleash that power. ")]),_c('h4',[_vm._v("Unexplained Powers")]),_c('p',[_vm._v("Sorcerers are rare in the world, and its unusual to find a sorcerer who is not involved in the adventuring life in some way. People with magical power seething in their veins soon discover that the power doesnt like to stay quiet. A sorcerers magic wants to be wielded, and it has a tendency to spill out in unpredictable ways if it isnt called on. ")]),_c('p',[_vm._v("Sorcerers often have obscure or quixotic motivations driving them to adventure. Some seek a greater understanding of the magical force that infuses them, or the answer to the mystery of its origin. Others hope to find a way to get rid of it, or to unleash its full potential. Whatever their goals, sorcerers are every bit as useful to an adventuring party as wizards, making up for a comparative lack of breadth in their magical knowledge with enormous flexibility in using the spells they know.")])])]),_c('h3',[_vm._v("Class Skills")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Spellcasting")]),_c('p',[_vm._v("An event in your past, or in the life of a parent or ancestor, left an indelible mark on you, infusing you with arcane magic. This font of magic, whatever its origin, fuels your spells. ")]),_c('h4',[_vm._v("Sorcerous Origin")]),_c('p',[_vm._v("Choose a sorcerous origin, which describes the source of your innate magical power: Draconic Bloodline or Wild Magic. both detailed at the end of the class description. Your choice grants you features when you choose it at 1st level and again at 6th, 14th, and 18th level. ")]),_c('h4',[_vm._v("Font of Magic")]),_c('p',[_vm._v("2nd level: You tap into a deep wellspring of magic within yourself. This wellspring is represented by sorcery points, which allow you to create a variety of magical effects. ")]),_c('h5',[_vm._v("Sorcery Points")]),_c('p',[_vm._v("You have 2 sorcery points, and you gain more as you reach higher levels, as shown in the Sorcery Points column of the Sorcerer table. You can never have more sorcery points than shown on the table for your level. You regain all spent sorcery points when you finish a long rest. ")]),_c('h5',[_vm._v("Flexible Casting")]),_c('p',[_vm._v(" You can use your sorcery points to gain additional spell slots, or sacrifice spell slots to gain additional sorcery points. You learn other ways to use your sorcery points as you reach higher levels. ")]),_c('h4',[_vm._v("Metamagic")]),_c('p',[_vm._v("3rd level, you gain the ability to twist your spells to suit your needs. You gain two of the following Metamagic options of your choice. You gain another one at 10th and 17th level. You can use only one Metamagic option on a spell when you cast it, unless otherwise noted. ")]),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Careful Spell")]),_c('li',[_vm._v("Distant Spell")]),_c('li',[_vm._v("Empowered Spell")]),_c('li',[_vm._v("Extended Spell")]),_c('li',[_vm._v("Heightened Spell")]),_c('li',[_vm._v("Quickened Spell")]),_c('li',[_vm._v("Subtle Spell")]),_c('li',[_vm._v("Twinned Spell")])]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Sorcerous Restoration")]),_c('p',[_vm._v("20th level: You regain 4 expended sorcery points whenever you finish a short rest. ")])]),_c('div',{staticClass:"sectxt"},[_c('h4',[_vm._v("Sorcerous Origins")]),_c('p',[_vm._v("Different sorcerers claim different origins for their innate magic. Although many variations exist, most of these origins fall into two categories: a draconic bloodline and wild magic. ")]),_c('h4',[_vm._v("Draconic BLoodline")]),_c('p',[_vm._v("Your innate magic comes from draconic magic that was mingled with your blood or that of your ancestors. Most often, sorcerers with this origin trace their descent back to a mighty sorcerer of ancient times who made a bargain with a dragon or who might even have claimed a dragon parent. Some of these bloodlines are well established in the world, but most are obscure. Any given sorcerer could be the first of a new bloodline, as a result of a pact or some other exceptional circumstances. ")]),_c('h5',[_vm._v("Draconic Ancestor")]),_c('p',[_vm._v("1st level: You choose one type of dragon as your ancestor. The damage type associated with each dragon is used by features you gain later. You can speak, read, and write Draconic. Additionally, whenever you make a Charisma check when interacting with dragons, your proficiency bonus is doubled if it applies to the check. ")]),_c('h5',[_vm._v("Draconic Resilience")]),_c('p',[_vm._v("As magic flows through your body, it causes physical traits of your dragon ancestors to emerge. At 1st level, your hit point maximum increases by 1 and increases by 1 again whenever you gain a level in this class. Additionally, parts of your skin are covered by a thin sheen of dragon-like scales. When you arent wearing armor, your AC equals 13 + your Dexterity modifier. ")]),_c('h5',[_vm._v("Elemental Affinity")]),_c('p',[_vm._v("6th level: When you cast a spell that deals damage of the type associated with your draconic ancestry, add your Charisma modifier to that damage. At the same time, you can spend 1 sorcery point to gain resistance to that damage type for 1 hour. ")]),_c('h5',[_vm._v("Dragon Wings")]),_c('p',[_vm._v("14th level: You gain the ability to sprout a pair of dragon wings from your back, gaining a flying speed equal to your current speed. You can create these wings as a bonus action on your turn. They last until you dismiss them as a bonus action on your turn. You cant manifest your wings while wearing armor unless the armor is made to accommodate them, and clothing not made to accommodate your wings might be destroyed when you manifest them. ")]),_c('h5',[_vm._v("Draconic Presence")]),_c('p',[_vm._v("18th level: You can channel the dread presence of your dragon ancestor, causing those around you to become awestruck or frightened. As an action, you can spend 5 sorcery points to draw on this power and exude an aura of awe or fear (your choice) to a distance of 60 feet. For 1 minute or until you lose your concentration (as if you were casting a concentration spell), each hostile creature that starts its turn in this aura must succeed on a Wisdom saving throw or be charmed (if you chose awe) or frightened (if you chose fear) until the aura ends. A creature that succeeds on this saving throw is immune to your aura for 24 hours. ")]),_c('h5',[_vm._v("Nature's Sanctuary")]),_c('p',[_vm._v("10th level: You cant be charmed or frightened by elementals or fey, and you are immune to poison and disease. ")]),_c('h4',[_vm._v("Wild Magic")]),_c('p',[_vm._v("Your innate magic comes from the wild forces of chaos that underlie the order of creation. You might have endured exposure to some form of raw magic, perhaps through a planar portal leading to Limbo, the Elemental Planes, or the mysterious Far Realm. Perhaps you were blessed by a powerful fey creature or marked by a demon. Or your magic could be a fluke of your birth, with no apparent cause or reason. However it came to be, this chaotic magic churns within you, waiting for any outlet. ")]),_c('h5',[_vm._v("Wild Magic Surge")]),_c('p',[_vm._v("1st level: Your spellcasting can unleash surges of untamed magic. Immediately after you cast a sorcerer spell of 1st level or higher, the DM can have you roll a d20. If you roll a 1, roll on the Wild Magic Surge table to create a random magical effect.")]),_c('h5',[_vm._v("Tides of Chaos")]),_c('p',[_vm._v("1st level: You can manipulate the forces of chance and chaos to gain advantage on one attack roll, ability check, or saving throw. Once you do so, you must finish a long rest before you can use this feature again. Any time before you regain the use of this feature, the DM can have you roll on the Wild Magic Surge table immediately after you cast a sorcerer spell of 1st level or higher. You then regain the use of this feature. ")]),_c('h5',[_vm._v("Bend Luck")]),_c('p',[_vm._v("6th level: You have the ability to twist fate using your wild magic. When another creature you can see makes an attack roll, an ability check, or a saving throw, you can use your reaction and spend 2 sorcery points to roll 1d4 and apply the number rolled as a bonus or penalty (your choice) to the creatures roll. You can do so after the creature rolls but before any effects of the roll occur.")]),_c('h5',[_vm._v("Controlled Chaos")]),_c('p',[_vm._v("14th level: You gain a modicum of control over the surges of your wild magic. Whenever you roll on the Wild Magic Surge table, you can roll twice and use either number. ")]),_c('h5',[_vm._v("Spell Bombardment")]),_c('p',[_vm._v("18th level: The harmful energy of your spells intensifies. When you roll damage for a spell and roll the highest number possible on any of the dice, choose one of those dice, roll it again and add that roll to the damage. You can use the feature only once per turn. ")])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"warlock-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/warlock.svg"),"aria-label":"Warlock Icon","alt":"Warlock Icon"}}),_vm._v(" Warlock ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/warlock.png"),"aria-label":"Warlock Image","alt":"Warlock Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d8 per warlock level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("8 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d8 (or 5) + your Constitution modifier per warlock level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" Light armor")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Simple weapons")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Wisdom, Charisma")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two skills from Arcana, Deception, History, Intimidation, Investigation, Nature, and Religion")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a light crossbow and 20 bolts or (b) any simple weapon")]),_c('li',[_vm._v("(a) a component pouch or (b) an arcane focus")]),_c('li',[_vm._v("(a) a scholars pack or (b) a dungeoneers pack")]),_c('li',[_vm._v("Leather armor, any simple weapon, and two daggers")])])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("With a pseudodragon curled on his shoulder, a young elf in golden robes smiles warmly, weaving a magical charm into his honeyed words and bending the palace sentinel to his will. As flames spring to life in her hands, a wizened human whispers the secret name of her demonic patron, infusing her spell with fiendish magic. Shifting his gaze between a battered tome and the odd alignment of the stars overhead, a wild-eyed tiefling chants the mystic ritual that will open a doorway to a distant world. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Warlocks are seekers of the knowledge that lies hidden in the fabric of the multiverse. Through pacts made with mysterious beings of supernatural power, warlocks unlock magical effects both subtle and spectacular. Drawing on the ancient knowledge of beings such as fey nobles, demons, devils, hags, and alien entities of the Far Realm, warlocks piece together arcane secrets to bolster their own power. ")]),_c('h4',[_vm._v("Sworn and Beholden")]),_c('p',[_vm._v("A warlock is defined by a pact with an otherworldly being. Sometimes the relationship between warlock and patron is like that of a cleric and a deity, though the beings that serve as patrons for warlocks are not gods. A warlock might lead a cult dedicated to a demon prince, an archdevil, or an utterly alien entitybeings not typically served by clerics. More often, though, the arrangement is similar to that between a master and an apprentice. The warlock learns and grows in power, at the cost of occasional services performed on the patrons behalf. ")]),_c('p',[_vm._v("The magic bestowed on a warlock ranges from minor but lasting alterations to the warlocks being (such as the ability to see in darkness or to read any language) to access to powerful spells. Unlike bookish wizards, warlocks supplement their magic with some facility at hand-to-hand combat. They are comfortable in light armor and know how to use simple weapons. ")]),_c('h4',[_vm._v("Delvers into Secrets")]),_c('p',[_vm._v("Warlocks are driven by an insatiable need for knowledge and power, which compels them into their pacts and shapes their lives. This thirst drives warlocks into their pacts and shapes their later careers as well. Stories of warlocks binding themselves to fiends are widely known. But many warlocks serve patrons that are not fiendish. Sometimes a traveler in the wilds comes to a strangely beautiful tower, meets its fey lord or lady, and stumbles into a pact without being fully aware of it. And sometimes, while poring over tomes of forbidden lore, a brilliant but crazed students mind is opened to realities beyond the material world and to the alien beings that dwell in the outer void. ")]),_c('p',[_vm._v("Once a pact is made, a warlocks thirst for knowledge and power can't be slaked with mere study and research. No one makes a pact with such a mighty patron if he or she doesnt intend to use the power thus gained. Rather, the vast majority of warlocks spend their days in active pursuit of their goals, which typically means some kind of adventuring. Furthermore, the demands of their patrons drive warlocks toward adventure. ")])])]),_c('h3',[_vm._v("Class Skill")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Otherwordly Patron")]),_c('p',[_vm._v("1st level: You have struck a bargain with an otherworldly being of your choice: the Archfey, the Fiend, or the Great Old One, each of which is detailed at the end of the class description. Your choice grants you features at 1st level and again at 6th, 10th, and 14th level. ")]),_c('h4',[_vm._v("Pact Magic")]),_c('p',[_vm._v("Your arcane research and the magic bestowed on you by your patron have given you facility with spells. ")]),_c('h4',[_vm._v("Eldritch Invocations")]),_c('p',[_vm._v("In your study of occult lore, you have unearthed eldritch invocations, fragments of forbidden knowledge that imbue you with an abiding magical ability. ")]),_c('p',[_vm._v("2nd level: You gain two eldritch invocations of your choice. Your invocation options are detailed at the end of the class description. When you gain certain warlock levels, you gain additional invocations of your choice, as shown in the Invocations Known column of the Warlock table. Additionally, when you gain a tevel in this class, you can choose one of the invocations you know and replace it with another invocation that you could learn at that level. ")]),_c('h4',[_vm._v("Pact Boon")]),_c('p',[_vm._v("3rd level: Your otherworldly patron bestows a gift upon you for your loyal service. You gain one of the following features of your choice. ")]),_c('h5',[_vm._v("Pact of the Chain")]),_c('p',[_vm._v("You learn the find familiar spell and can cast it as a ritual. The spell doesnt count against your number of spells known. When you cast the spell, you can choose one of the normal forms for your familiar or one of the following special forms: imp, pseudodragon, quasit, or sprite. Additionally, when you take the Attack action, you can forgo one of your own attacks to allow your familiar to make one attack of its own.")]),_c('h5',[_vm._v("Pact of the Blade")]),_c('p',[_vm._v("You can use your action to create a pact weapon in your empty hand. You can choose the form that this melee weapon takes each time you create it (see chapter 5 for weapon options). You are proficient with it while you wield it. This weapon counts as magical for the purpose of overcoming resistance and immunity to nonmagical attacks and damage.")]),_c('h5',[_vm._v("Pact of the Tome")]),_c('p',[_vm._v("Your patron gives you a grimoire called a Book of Shadows. When you gain this feature, choose three cantrips from any classs spell list. While the book is on your person, you can cast those cantrips at will. They don't count against your number of cantrips known. If you lose your Book of Shadows, you can perform a 1-hour ceremony to receive a replacement from your patron. This ceremony can be performed during a short or long rest, and it destroys the previous book. The book turns to ash when you die. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Mystic Arcanum")]),_c('p',[_vm._v("11th level: Your patron bestows upon you a magical secret called an arcanum. Choose one 6th-level spell from the warlock spell list as this arcanum. You can cast your arcanum spell once without expending a spell slot. You must finish a long rest before you can do so again.")]),_c('p',[_vm._v("At higher levels, you gain more warlock spells of your choice that can be cast in this way: one 7th-level spell at 13th level, one 8th-level spell at 15th level, and one 9th-level spell at 17th level. You regain all uses of your Mystic Arcanum when you finish a long rest. ")]),_c('h4',[_vm._v("Eldritch Master")]),_c('p',[_vm._v("20th level: You can draw on your inner reserve of mystical power while entreating your patron to regain expended spell slots. You can spend 1 minute entreating your patron for aid to regain all your expended spell slots from your Pact Magic feature. Once you regain spell slots with this feature, you must finish a long rest before you can do so again. ")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Otherworldly Patrons")]),_c('p',[_vm._v("The beings that serve as patrons for warlocks are mighty inhabitants of other planes of existencenot gods, but almost godlike in their power. Various patrons give their warlocks access to different powers and invocations, and expect significant favors in return. Some patrons collect warlocks, doling out mystic knowledge relatively freely or boasting of their ability to bind mortals to their will. Other patrons bestow their power only grudgingly, and might make a pact with only one warlock. Warlocks who serve the same patron might view each other as allies, siblings, or rivals. ")]),_c('h4',[_vm._v("The Archfey")]),_c('p',[_vm._v("Your patron is a lord or lady of the fey, a creature of legend who holds secrets that were forgotten before the mortal races were born. This beings motivations are often inscrutable, and sometimes whimsical, and might involve a striving for greater magical power or the settling of age-old grudges. Beings of this sort include the Prince of Frost; the Queen of Air and Darkness, ruler of the Gloaming Court; Titania of the Summer Court; her consort Oberon, the Green Lord; Hyrsam, the Prince of Fools; and ancient hags. ")]),_c('h5',[_vm._v("Expanded Spell List")]),_c('p',[_vm._v("The Archfey lets you choose from an expanded list of spells when you learn a warlock spell. ")]),_c('ul',[_c('li',[_c('strong',[_vm._v("1st")]),_vm._v(". "),_c('em',[_vm._v("faerie fire, sleep")])]),_c('li',[_c('strong',[_vm._v("2nd")]),_vm._v(". "),_c('em',[_vm._v("calm emotions, phantasmal force")])]),_c('li',[_c('strong',[_vm._v("3rd")]),_vm._v(". "),_c('em',[_vm._v("blink, plant growth")])]),_c('li',[_c('strong',[_vm._v("4th")]),_vm._v(". "),_c('em',[_vm._v("dominate beast, greater invisibility")])]),_c('li',[_c('strong',[_vm._v("5th")]),_vm._v(". "),_c('em',[_vm._v("dominate person, seeming")])])]),_c('h5',[_vm._v("Fey Presence")]),_c('p',[_vm._v("1st level: Your patron bestows upon you the ability to project the beguiling and fearsome presence of the fey. As an action, you can cause each creature in a 10-foot cube originating from you to make a Wisdom saving throw against your warlock spell save DC. The creatures that fail their saving throws are all charmed or frightened by you (your choice) until the end of your next turn. Once you use this feature, you cant use it again until you finish a short or long rest. ")]),_c('h5',[_vm._v("Misty Escapes")]),_c('p',[_vm._v("6th level: You can vanish in a puff of mist in response to harm. When you take damage, you can use your reaction to turn invisible and teleport up to 60 feet to an unoccupied space you can see. You remain invisible until the start of your next turn or until you attack or cast a spell. Once you use this feature, you cant use it again until you finish a short or long rest.")]),_c('h5',[_vm._v("Beguiling Defenses")]),_c('p',[_vm._v("10th level: Your patron teaches you how to turn the mind-affecting magic of your enemies against them. You are immune to being charmed, and when another creature attempts to charm you, you can use your reaction to attempt to turn the charm back on that creature. ")]),_c('h5',[_vm._v("Dark Delirium")]),_c('p',[_vm._v("14th level: You can plunge a creature into an illusory realm. As an action, choose a creature that you can see within 60 feet of you. It must make a Wisdom saving throw against your warlock spell save DC. On a failed save, it is charmed or frightened by you (your choice) for 1 minute or until your concentration is broken (as if you are concentrating on a spell), This effect ends early if the creature takes any damage. Until this illusion ends, the creature thinks it is lost in a misty realm, the appearance of which you choose. The creature can see and hear only itself, you, and the illusion. You must finish a short or long rest before you can use this feature again. ")]),_c('h4',[_vm._v("The Fiend")]),_c('p',[_vm._v("You have made a pact with a fiend from the lower planes of existence, a being whose aims are evil, even if you strive against those aims. Such beings desire the corruption or destruction of all things, ultimately including you. Fiends powerful enough to forge a pact include demon lords such as Demogorgon, Orcus, FrazUrb-luu, and Baphomet; archdevils such as Asmodeus, Dispater, Mephistopheles, and Belial; pit fiends and balors that are especially mighty; and ultroloths and other lords of the yugoloths. ")]),_c('h5',[_vm._v("Expanded Spell List")]),_c('p',[_vm._v("The Fiend lets you choose from an expanded list of spells when you learn a warlock spell. ")]),_c('ul',[_c('li',[_c('strong',[_vm._v("1st")]),_vm._v(". "),_c('em',[_vm._v("burning hands, command")])]),_c('li',[_c('strong',[_vm._v("2nd")]),_vm._v(". "),_c('em',[_vm._v("blindness/deafness, scorching ray")])]),_c('li',[_c('strong',[_vm._v("3rd")]),_vm._v(". "),_c('em',[_vm._v("fireball, stinking cloud")])]),_c('li',[_c('strong',[_vm._v("4th")]),_vm._v(". "),_c('em',[_vm._v("fire shield, wall of fire")])]),_c('li',[_c('strong',[_vm._v("5th")]),_vm._v(". "),_c('em',[_vm._v("flame strike, hallow")])])]),_c('h5',[_vm._v("Dark One's Blessings")]),_c('p',[_vm._v("1st level: When you reduce a hostile creature to 0 hit points, you gain temporary hit points equal to your Charisma modifier + your warlock level (minimum of 1). ")]),_c('h5',[_vm._v("Dark One's Own Luck")]),_c('p',[_vm._v("6th level: You can call on your patron to alter fate in your favor. When you make an ability check or a saving throw, you can use this feature to add a d10 to your roll. You can do so after seeing the initial roll but before any of the rolls effects occur. Once you use this feature, you cant use it again until you finish a short or long rest.")]),_c('h5',[_vm._v("Fiendish Resilience")]),_c('p',[_vm._v("10th level: You can choose one damage type when you finish a short or long rest. You gain resistance to that damage type until you choose a different one with this feature. Damage from magical weapons or silver weapons ignores this resistance. ")]),_c('h5',[_vm._v("Hurl Through Hell")]),_c('p',[_vm._v("14th level: When you hit a creature with an attack, you can use this feature to instantly transport the target through the lower planes. The creature disappears and hurtles through a nightmare landscape. At the end of your next turn, the target returns to the space it previously occupied, or the nearest unoccupied space. If the target is not a fiend, it takes 10d10 psychic damage as it reels from its horrific experience. Once you use this feature, you cant use it again until you finish a long rest. ")]),_c('h4',[_vm._v("The Great Old One")]),_c('p',[_vm._v("Your patron is a mysterious entity whose nature is utterly foreign to the fabric of reality. It might come from the Far Realm, the space beyond reality, or it could be one of the elder gods known only in legends. Its motives are incomprehensible to mortals, and its knowledge so immense and ancient that even the greatest libraries pale in comparison to the vast secrets it holds. The Great Old One might be unaware of your existence or entirely indifferent to you, but the secrets you have learned allow you to draw your magic from it. Entities of this type include Ghaunadar, called That Which Lurks; Tharizdun, the Chained God; Dendar, the Night Serpent; Zargon, the Returner; Great Cthulhu; and other unfathomable beings. ")]),_c('h5',[_vm._v("Expanded Spell List")]),_c('p',[_vm._v("The Great Old One lets you choose from an expanded list of spells when you learn a warlock spell. ")]),_c('ul',[_c('li',[_c('strong',[_vm._v("1st")]),_vm._v(". "),_c('em',[_vm._v("dissonant whispers, Tashas hideous laughter")])]),_c('li',[_c('strong',[_vm._v("2nd")]),_vm._v(". "),_c('em',[_vm._v("detect thoughts, phantasmal force")])]),_c('li',[_c('strong',[_vm._v("3rd")]),_vm._v(". "),_c('em',[_vm._v("clairvoyance, sending")])]),_c('li',[_c('strong',[_vm._v("4th")]),_vm._v(". "),_c('em',[_vm._v("dominate beast, Evards black tentacles")])]),_c('li',[_c('strong',[_vm._v("5th")]),_vm._v(". "),_c('em',[_vm._v("dominate person, telekinesis")])])]),_c('h5',[_vm._v("Awakened Mind")]),_c('p',[_vm._v("1st level: Your alien knowledge gives you the ability to touch the minds of other creatures. You can communicate telepathically with any creature you can see within 30 feet of you. You dont need to share a language with the creature for it to understand your telepathic utterances, but the creature must be able to understand at least one language. ")]),_c('h5',[_vm._v("Entropic Ward")]),_c('p',[_vm._v("6th level: You learn to magically ward yourself against attack and to turn an enemys failed strike into good luck for yourself. When a creature makes an attack roll against you, you can use your reaction to impose disadvantage on that roll. If the attack misses you, your next attack roll against the creature has advantage if you make it before the end of your next turn. Once you use this feature, you cant use it again until you finish a short or long rest.")]),_c('h5',[_vm._v("Thought Shield")]),_c('p',[_vm._v("10th level: Your thoughts cant be read by telepathy or other means unless you allow it. You also have resistance to psychic damage, and whenever a creature deals psychic damage to you, that creature takes the same amount of damage that you do. ")]),_c('h5',[_vm._v("Create Thrall")]),_c('p',[_vm._v("14th level: You gain the ability to infect a humanoids mind with the alien magic of your patron. You can use your action to touch an incapacitated humanoid. That creature is then charmed by you until a remove curse spell is cast on it, the charmed condition is removed from it, or you use this feature again. You can communicate telepathically with the charmed creature as long as the two of you are on the same plane of existence. ")]),_c('h4',[_vm._v("Eldritch Invocations")]),_c('p',[_vm._v("If an eldritch invocation has prerequisites, you must meet them to learn it. You can learn the invocation at the same time that you meet its prerequisites.")]),_c('ul',{staticClass:"ul-ruby"},[_c('li',[_vm._v("Agonizing Blast")]),_c('li',[_vm._v("Armor of Shadows")]),_c('li',[_vm._v("Ascendant Step")]),_c('li',[_vm._v("Beast Speech")]),_c('li',[_vm._v("Beguiling Influence")]),_c('li',[_vm._v("Bewitching Whispers")]),_c('li',[_vm._v("Book of Ancient Secrets")]),_c('li',[_vm._v("Chans of Carceri")]),_c('li',[_vm._v("Devil's Sight")]),_c('li',[_vm._v("Dreadful Word")]),_c('li',[_vm._v("Eldritch Sight")]),_c('li',[_vm._v("Eldritch Spear")]),_c('li',[_vm._v("Eyes of the Rune Keeper")]),_c('li',[_vm._v("Fiendish Vigor")]),_c('li',[_vm._v("Gaze of Two Minds")]),_c('li',[_vm._v("Lifedrinker")]),_c('li',[_vm._v("Mask of Many Faces")]),_c('li',[_vm._v("Master of Myriad Forms")]),_c('li',[_vm._v("Minions of Chaos")]),_c('li',[_vm._v("Mire the Mind")]),_c('li',[_vm._v("Misty Visions")]),_c('li',[_vm._v("One with Shadows")]),_c('li',[_vm._v("Otherwordly Leap")]),_c('li',[_vm._v("Repelling Blast")]),_c('li',[_vm._v("Sculptor of Flesh")]),_c('li',[_vm._v("Sign of Ill Omen")]),_c('li',[_vm._v("Thief of Five Fates")]),_c('li',[_vm._v("Thirsting Blade")]),_c('li',[_vm._v("Visions of Distant Realms")]),_c('li',[_vm._v("Voice of the Chain Master")]),_c('li',[_vm._v("Whispers of the Grave")]),_c('li',[_vm._v("Witch Sight")])])])]),_c('i',{staticClass:"faq-toggle"})]),_c('div',{staticClass:"faq-item",attrs:{"id":"wizard-page"}},[_c('h2',[_c('span',{staticClass:"icon-text"},[_c('img',{staticClass:"icons",attrs:{"src":require("../assets/icons/wizard.svg"),"aria-label":"Wizard Icon","alt":"Wizard Icon"}}),_vm._v(" Wizard ")])]),_c('div',{staticClass:"faq-content"},[_c('div',{staticClass:"flextxt"},[_c('div',{staticClass:"esqtxt"},[_c('img',{attrs:{"src":require("../assets/classes/wizard.png"),"aria-label":"Wizard Image","alt":"Wizard Image"}}),_c('div',{staticClass:"hp"},[_c('h3',[_vm._v("Class Features")]),_c('h5',[_vm._v("Hit Points")]),_c('ul',[_c('li',[_c('strong',[_vm._v("Hit Dice:")]),_c('p',[_vm._v("1d6 per wizard level")])]),_c('li',[_c('strong',[_vm._v("Hit Points at 1st Level:")]),_c('p',[_vm._v("6 + your Constitution modifier")])]),_c('li',[_c('strong',[_vm._v("Hit Points at Higher Levels:")]),_c('p',[_vm._v("1d6 (or 4) + your Constitution modifier per wizard level after 1st ")])])]),_c('h5',[_vm._v("Proficiencies")]),_c('ul',{staticClass:"prof"},[_c('li',[_c('strong',[_vm._v("Armor:")]),_vm._v(" None")]),_c('li',[_c('strong',[_vm._v("Weapons:")]),_vm._v(" Daggers, darts, slings, quarterstaffs, light crossbows")]),_c('li',[_c('strong',[_vm._v("Tools:")]),_vm._v(" None ")]),_c('li',[_c('strong',[_vm._v("Saving Throws:")]),_vm._v(" Intelligence, Wisdom")]),_c('li',[_c('strong',[_vm._v("Skills:")]),_vm._v(" Choose two from Arcana, History, Insight, Investigation, Medicine, and Religion")])]),_c('h5',[_vm._v("Equipment")]),_c('p',[_vm._v("You start with the following equipment, in addition to the equipment granted by your background:")]),_c('ul',{staticClass:"equip"},[_c('li',[_vm._v("(a) a quarterstaff or (b) a dagger")]),_c('li',[_vm._v("(a) a component pouch or (b) an arcane focus")]),_c('li',[_vm._v("(a) ascholars pack or (b) an explorers pack")]),_c('li',[_vm._v("A spellbook")])])])]),_c('div',{staticClass:"drtxt"},[_c('p',[_c('em',[_vm._v("Clad in the silver robes that denote her station, an elf closes her eyes to shut out the distractions of the battlefield and begins her quiet chant. Fingers weaving in front of her, she completes her spell and launches a tiny bead of fire toward the enemy ranks, where it erupts into a conflagration that engulfs the soldiers. Checking and rechecking his work, a human scribes an intricate magic circle in chalk on the bare stone floor, then sprinkles powdered iron along every line and graceful curve. When the circle is complete, he drones a long incantation. A hole opens in space inside the circle, bringing a whiff of brimstone from the otherworldly plane beyond. Crouching on the floor in a dungeon intersection, a gnome tosses a handful of small bones inscribed with mystic symbols, muttering a few words of power over them. Closing his eyes to see the visions more clearly, he nods slowly, then opens his eyes and points down the passage to his left. ")]),_c('br'),_c('br'),_vm._v("_____________________ "),_c('br'),_c('br'),_vm._v("Wizards are supreme magic-users, defined and united as a class by the spells they cast. Drawing on the subtle weave of magic that permeates the cosmos, wizards cast spells of explosive fire, arcing lightning, subtle deception, and brute-force mind control. Their magic conjures monsters from other planes of existence, glimpses the future, or turns slain foes into zombies. Their mightiest spells change one substance into another, call meteors down from the sky, or open portals to other worlds. ")]),_c('h4',[_vm._v("Scholars of the Arcane")]),_c('p',[_vm._v("Wild and enigmatic, varied in form and function, the power of magic draws students who seek to master its mysteries. Some aspire to become like the gods, shaping reality itself. Though the casting of a typical spell requires merely the utterance of a few strange words, fleeting gestures, and sometimes a pinch or clump of exotic materials, these surface components barely hint at the expertise attained after years of apprenticeship and countless hours of study. ")]),_c('p',[_vm._v("Wizards live and die by their spells. Everything else is secondary. They learn new spells as they experiment and grow in experience. They can also learn them from other wizards, from ancient tomes or inscriptions, and from ancient creatures (such as the fey) that are steeped in magic. ")]),_c('h4',[_vm._v("The Lure of Knowledge")]),_c('p',[_vm._v("Wizards lives are seldom mundane. The closest a wizard is likely to come to an ordinary life is working as a sage or lecturer in a library or university, teaching others the secrets of the multiverse. Other wizards sell their services as diviners, serve in military forces, or pursue lives of crime or domination. ")]),_c('p',[_vm._v("But the lure of knowledge and power calls even the most unadventurous wizards out of the safety of their libraries and laboratories and into crumbling ruins and lost cities. Most wizards believe that their counterparts in ancient civilizations knew secrets of magic that have been lost to the ages, and discovering those secrets could unlock the path to a power greater than any magic available in the present age. ")])])]),_c('h3',[_vm._v("Class Skill")]),_c('div',{staticClass:"abstxt"},[_c('h4',[_vm._v("Spellcasting")]),_c('p',[_vm._v("As a student of arcane magic, you have a spellbook containing spells that show the first glimmerings of your true power. ")]),_c('h4',[_vm._v("Arcane Recovery")]),_c('p',[_vm._v("You have learned to regain some of your magical energy by studying your spellbook. Once per day when you finish a short rest, you can choose expended spell slots to recover. The spell slots can have a combined level that is equal to or less than half your wizard level (rounded up), and none of the slots can be 6th level or higher. ")]),_c('h4',[_vm._v("Arcane Tradition")]),_c('p',[_vm._v("2nd level: You choose an arcane tradition, shaping your practice of magic through one of eight schools: Abjuration, Conjuration, Divination, Enchantment, Evocation, Illusion, Necromancy, or Transmutation, all detailed at the end of the class description. Your choice grants you features at 2nd level and again at 6th, 10th, and 14th level. ")]),_c('h4',[_vm._v("Ability Score Improvement")]),_c('p',[_vm._v("When you reach 4th level, and again at 8th, 12th, 16th, and 19th level, you can increase one ability score of your choice by 2, or you can increase two ability scores of your choice by 1. As normal, you cant increase an ability score above 20 using this feature. ")]),_c('h4',[_vm._v("Spell Mastery")]),_c('p',[_vm._v("18th level: You have achieved such mastery over certain spells that you can cast them at will. Choose a 1st-level wizard spell and a 2nd-level wizard spell that are in your spelibook. You can cast those spells at their lowest level without expending a spell slot when you have them prepared. If you want to cast either spell at a higher level, you must expend a spell slot as normal. By spending 8 hours in study, you can exchange one or both of the spells you chose for different spells of the same levels. ")]),_c('h4',[_vm._v("Signature Spells")]),_c('p',[_vm._v("20th level: You gain mastery over two powerful spells and can cast them with little effort. Choose two 3rd-level wizard spells in your spellbook as your signature spells. You always have these spells prepared, they dont count against the number of spells you have prepared, and you can cast each of them once at 3rd level without expending a spell slot. When you do so, you can't do so again until you finish a short or long rest. If you want to cast either spell at a higher level, you must expend a spell slot as normal. ")])]),_c('div',{staticClass:"sectxt"},[_c('h3',[_vm._v("Arcane Traditions")]),_c('p',[_vm._v("The study of wizardry is ancient, stretching back to the earliest mortal discoveries of magic. It is firmly established in the worlds of D&D, with various traditions dedicated to its complex study. The most common arcane traditions in the multiverse revolve around the schools of magic. Wizards through the ages have cataloged thousands of spells, grouping them into eight categories called schools. In some places, these traditions are literally schools; a wizard might study at the School of Illusion while another studies across town at the School of Enchantment. In other institutions, the schools are more like academic departments, with rival faculties competing for students and funding. Even wizards who train apprentices in the solitude of their own towers use the division of magic into schools as a learning device, since the spells of each school require mastery of different techniques. ")]),_c('h4',[_vm._v("School of Abjuration")]),_c('p',[_vm._v("The School of Abjuration emphasizes magic that blocks, banishes, or protects. Detractors of this school say that its tradition is about denial, negation rather than positive assertion. You understand, however, that ending harmful effects, protecting the weak, and banishing evil influences is anything but a philosophical void. Itis a proud and respected vocation. Called abjurers, members of this school are sought when baleful spirits require exorcism, when important locations must be guarded against magical spying, and when portals to other planes of existence must be closed. ")]),_c('h5',[_vm._v("Abjuration Savant")]),_c('p',[_vm._v("2nd level: The gold and time you must spend to copy an abjuration spell into your spellbook is halved.")]),_c('h5',[_vm._v("Arcane Ward")]),_c('p',[_vm._v("2nd level: You can weave magic around yourself for protection. When you cast an abjuration spell of 1st level or higher, you can simultaneously use a strand of the spells magic to create a magical ward on yourself that lasts until you finish a long rest. The ward has hit points equal to twice your wizard level + your Intelligence modifier. Whenever you take damage, the ward takes the damage instead. If this damage reduces the ward to 0 hit points, you take any remaining damage. While the ward has 0 hit points, it cant absorb damage. but its magic remains. Whenever you cast an abjuration spell of 1st level or higher, the ward regains a | number of hit points equal to twice the level of the spell. Once you create the ward, you can't create it again until you finish a long rest. ")]),_c('h5',[_vm._v("Projected Ward")]),_c('p',[_vm._v("6th Level: When a creature that you can see within 30 feet of you takes damage, you can use your reaction to cause your Arcane Ward to absorb that damage. If this damage reduces the ward to 0 hit points, the warded creature takes any remaining damage. ")]),_c('h5',[_vm._v("Improved Abjuration")]),_c('p',[_vm._v("10th level: When you cast an abjuration spell that requires you to make an ability check as a part of casting that spell (as in counterspell and dispel magic), you add your proficiency bonus to that ability check. ")]),_c('h5',[_vm._v("spell Resistance")]),_c('p',[_vm._v(" 14th level: You have advantage on saving throws against spells. Furthermore, you have resistance against the damage of spells. ")]),_c('h4',[_vm._v("School of Conjuration")]),_c('p',[_vm._v("As a conjurer, you favor spells that produce objects and creatures out of thin air. You can conjure billowing clouds of killing fog or summon creatures from elsewhere to fight on your behalf. As your mastery grows, you learn spells of transportation and can teleport yourself across vast distances, even to other planes of existence, in an instant. ")]),_c('h5',[_vm._v("Conjuration Savant")]),_c('p',[_vm._v("2nd level: The gold and time you must spend to copy an conjuration spell into your spellbook is halved.")]),_c('h5',[_vm._v("Minor Conjuration")]),_c('p',[_vm._v("2nd level: You can use your action to conjure up an inanimate object in your hand or on the ground in an unoccupied space that you can see within 10 feet of you. This object can be no larger than 3 feet on a side and weigh no more than 10 pounds, and its form must be that of a nonmagical object that you have seen. The object is visibly magical, radiating dim light out to 5 feet. The object disappears after 1 hour, when you use this feature again, or if it takes any damage. ")]),_c('h5',[_vm._v("Benign Transposition")]),_c('p',[_vm._v("6th Level: You can use your action to teleport up to 30 feet to an unoccupied space that you can see. Alternatively, you can choose a space within range that is occupied by a Small or Medium creature. If that creature is willing, you both teleport, swapping places. Once you use this feature, you can't use it again until you finish a long rest or you cast a conjuration spell of 1st level or higher. ")]),_c('h5',[_vm._v("Focused Conjuration")]),_c('p',[_vm._v("10th level: While you are concentrating on a conjuration spell, your concentration cant be broken as a result of taking damage. ")]),_c('h5',[_vm._v("Durable Summons")]),_c('p',[_vm._v(" 14th level: Any creature that you summon or create with a conjuration spell has 30 temporary hit points. ")]),_c('h4',[_vm._v("School of Divination")]),_c('p',[_vm._v("The counsel of a diviner is sought by royalty and commoners alike, for all seek a clearer understanding of the past, present, and future. As a diviner, you strive to part the veils of space, time, and consciousness so that you can see clearly. You work to master spells of discernment, remote viewing, supernatural knowledge, and foresight. ")]),_c('h5',[_vm._v("Divination Savant")]),_c('p',[_vm._v("2nd level: The gold and time you must spend to copy a divination spell into your spellbook is halved.")]),_c('h5',[_vm._v("Portent")]),_c('p',[_vm._v("2nd level: Glimpses of the future begin to press in on your awareness. When you finish a long rest, roll two d20s and record the numbers rolled. You can replace any attack roll, saving throw, or ability check made by you or a creature that you can see with one of these foretelling rolls. You must choose to do so before the roll, and you can replace a roll in this way only once per turn. Each foretelling roll can be used only once. When you finish a long rest, you lose any unused foretelling rolls. ")]),_c('h5',[_vm._v("Expert Divination")]),_c('p',[_vm._v("6th Level: Casting divination spells comes so easily to you that it expends only a fraction of your spellcasting efforts. When you cast a divination spell of 2nd level or higher using a spell slot, you regain one expended spell slot. The slot you regain must be of a level lower than the spell you cast and cant be higher than 5th level. ")])