 the end of your next turn. The charmed creatures speed drops to 0, and the creature is incapacitated and visibly dazed. On subsequent turns, you can use your action to maintain this effect, extending its duration until the end of your next turn. However, the effect ends if you move more than 5 feet away from the creature, if the creature can neither see nor hear you, or if the creature takes damage. Once the effect ends, or if the creature succeeds on its initial saving throw against this effect, you can't use this feature on that creature again until you finish a long rest. ")]),_c('h5',[_vm._v("Instinctive Charm")]),_c('p',[_vm._v("6th Level: When a creature you can see within 30 feet of you makes an attack roll against you, you can use your reaction to divert the attack, provided that another creature is within the attacks range. The attacker must make a Wisdom saving throw against your wizard spell save DC. On a failed save, the attacker must target the creature that is closest to it, not including you or itself. If multiple creatures are closest, the attacker chooses which one to target. On a successful save, you cant use this feature on the attacker again until you finish a long rest. You must choose to use this feature before knowing whether the attack hits or misses. Creatures that cant be charmed are immune to this effect. ")]),_c('h5',[_vm._v("Split Enchantment")]),_c('p',[_vm._v("10th level: When you cast an enchantment spell of 1st level or higher that targets only one creature, you can have it target a second creature. ")]),_c('h5',[_vm._v("Alter Memories")]),_c('p',[_vm._v(" 14th level: You gain the ability to make a creature unaware of your magical influence on it. When you cast an enchantment spell to charm one or more creatures, you can alter one creature's understanding so that it remains unaware of being charmed. Additionally, once before the spell expires, you can use your action to try to make the chosen creature forget some of the time it spent charmed. The creature must succeed on an Intelligence saving throw against your wizard spell save DC or lose a number of hours of its memories equal to 1 + your Charisma modifier (minimum 1). You can make the creature forget less time, and the amount of time cant exceed the duration of your enchantment spell. ")]),_c('h4',[_vm._v("School of Evocation")]),_c('p',[_vm._v("You focus your study on magic that creates powerful elemental effects such as bitter cold, searing flame, rolling thunder, crackling lightning, and burning acid. Some evokers find employment in military forces, serving as artillery to blast enemy armies from afar. Others use their spectacular power to protect the weak, while some seek their own gain as bandits, adventurers, or aspiring tyrants. ")]),_c('h5',[_vm._v("Evocation Savant")]),_c('p',[_vm._v("2nd level: The gold and time you must spend to copy an evocation spell into your spellbook is halved.")]),_c('h5',[_vm._v("Sculpt Spells")]),_c('p',[_vm._v("2nd level: You can create pockets of relative safety within the effects of your evocation spells. When you cast an evocation spell that affects other creatures that you can see, you can choose a number of them equal to 1 + the spells level. The chosen creatures automatically succeed on their saving throws against the spell, and they take no damage if they would normally take half damage on a successful save. ")]),_c('h5',[_vm._v("Potent Cantrip")]),_c('p',[_vm._v("6th Level: Your damaging cantrips affect even creatures that avoid the brunt of the effect. When a creature succeeds on a saving throw against your cantrip, the creature takes half the cantrips damage (if any) but suffers no additional effect from the cantrip. ")]),_c('h5',[_vm._v("Empowered Evocation")]),_c('p',[_vm._v("10th level: You can add your Intelligence modifier to the damage roll of any wizard evocation spell you cast.")]),_c('h5',[_vm._v("Overchannel")]),_c('p',[_vm._v(" 14th level: You can increase the power of your simpler 