nst you, you can use your reaction to interpose the illusory duplicate between the attacker and l yourself. The attack automatically misses you, then the illusion dissipates. l Once you use this feature, you cant use it again until you finish a short or long rest. ")]),_c('h5',[_vm._v("Illusory Reality")]),_c('p',[_vm._v(" 14th level: You have learned the secret of weaving shadow magic into your illusions to give them a semireality. When you cast an illusion spell of 1st level or higher, you can choose one inanimate, nonmagical object that is part of the illusion and make that object real. You can do this on your turn as a bonus action while the spell is ongoing. The object remains real for 1 minute. For example, you can create an illusion of a bridge over a chasm and then make it real long enough for your allies to cross. The object cant deal damage or otherwise directly harm anyone.")]),_c('h4',[_vm._v("School of Necromancy")]),_c('p',[_vm._v("The School of Necromancy explores the cosmic forces of life, death, and undeath. As you focus your studies in this tradition, you learn to manipulate the energy that animates all living things. As you progress, you learn to sap the life force from a creature as your magic destroys its body, transforming that vital energy into magical power you can manipulate. Most people see necromancers as menacing, or even villainous, due to the close association with death. Not all necromancers are evil, but the forces they manipulate are considered taboo by many societies. ")]),_c('h5',[_vm._v("Necromancy Savant")]),_c('p',[_vm._v("2nd level: The gold and time you must spend to copy an necromancy spell into your spellbook is halved.")]),_c('h5',[_vm._v("Grim Harvest")]),_c('p',[_vm._v("2nd level: You gain the ability to reap life energy from creatures you kill with your spells. Once per turn when you kill one or more creatures with a spell of Ist level or higher, you regain hit points equal to twice the spells level, or three times its level if the spell belongs to the School of Necromancy. You dont gain this benefit for killing constructs or undead. ")]),_c('h5',[_vm._v("Undead Thralls")]),_c('p',[_vm._v("6th Level: You add the animate dead spell to your spellbook if it is not there already. When you cast animate dead, you can target one additional corpse or pile of bones, creating another zombie or skeleton, as appropriate. ")]),_c('h5',[_vm._v("Inured to Undeath")]),_c('p',[_vm._v("10th level: You have resistance to necrotic damage, and your hit point maximum cant be reduced. You have spent so much time dealing with undead and the forces that animate them that you have become inured to some of their worst effects. ")]),_c('h5',[_vm._v("Command Undead")]),_c('p',[_vm._v(" 14th level: You can use magic to bring undead under your control, even those created by other wizards. As an action, you can choose one undead that you can see within 60 feet of you. That creature must make a Charisma saving throw against your wizard spell save DC. If it succeeds, you cant use this feature on it again. If it fails, it becomes friendly to you and obeys your commands until you use this feature again. Intelligent undead are harder to control in this way. If the target has an Intelligence of 8 or higher, it has advantage on the saving throw. If it fails the saving throw and has an Intelligence of 12 or higher, it can repeat the saving throw at the end of every hour until it succeeds and breaks free. ")]),_c('h4',[_vm._v("School of Transmutation")]),_c('p',[_vm._v("You are a student of spells that modify energy and matter. To you, the world is not a fixed thing, but eminently mutable, and you delight in being an agent of change. You wield the raw stuff of creation and learn to alter both physical forms and mental qualities. Your magic gives you the tools to become a smith on realitys forge. Some transmuters are tinkerers and pranksters, turning people into toads and transforming copper into silver for fun and occasional profit. Others pursue their magical studies with deadly seriousness, seeking the power of the gods to make and destroy worlds. ")]),_c('h5',[_vm._v("Evocation Savant")]),_c('p',[_vm._v("2nd level: The gold and time you must spend to copy an transmutation spell into your spellbook is halved.")]),_c('h5',[_vm._v("Minor Alchemy")]),_c('p',[_vm._v("2nd level: you can temporarily alter the physical properties of one nonmagical object, changing it from one substance into another. You perform a special alchemical procedure on one object composed entirely of wood, stone (but not a gemstone), iron, copper, or silver, transforming it into a different one of those materials. For each 10 minutes you spend performing the procedure, you can transform up to 1 cubic foot of material. After 1 hour, or until you lose your concentration (as if you were concentrating on a spell), the material reverts to its original substance. ")]),_c('h5',[_vm._v("Transmuter's Stone")]),_c('p',[_vm._v("6th Level: You can spend 8 hours creating a transmuter's stone that stores transmutation magic. You can benefit from the stone yourself or give it to another creature. A creature gains a benefit of your choice as long as the stone is in the creatures possession. When you create the stone, choose the benefit from the following options: "),_c('ul',[_c('li',[_vm._v("Darkvision out to a range of 60 feet.")]),_c('li',[_vm._v("An increase to speed of 10 feet while the creature is unencumbered.")]),_c('li',[_vm._v("Proficiency in Constitution saving throws.")]),_c('li',[_vm._v("Resistance to acid, cold, fire, lightning, or thunder damage (your choice whenever you choose this benefit).")])]),_vm._v(" Each time you cast a transmutation spell of 1st level or higher, you can change the effect of your stone if the stone is on your person. If you create a new transmuters stone, the previous one ceases to function. ")]),_c('h5',[_vm._v("Shapechanger")]),_c('p',[_vm._v("10th level: You add the "),_c('em',[_vm._v("polymorph")]),_vm._v(" spell to your spellbook, if it is not there already. You can cast polymorph without expending a spell slot. When you do so, you can target only yourself and transform into a beast whose challenge rating is 1 or lower. Once you cast "),_c('em',[_vm._v("polymorph")]),_vm._v(" in this way, you cant do so again until you finish a short or long rest, though you can still cast it normally using an available spell slot. ")]),_c('h5',[_vm._v("Master Transmuter")]),_c('p',[_vm._v(" 14th level: you can use your action to consume the reserve of transmutation magic stored within your transmuter's stone in a single burst. When you do so, choose one of the following effects. Your transmuters stone is destroyed and can't be remade until you finish a long rest. "),_c('ul',[_c('li',[_c('strong',[_vm._v("Major Transformation ")]),_vm._v(". You can transmute one nonmagical objectno larger than a 5-foot cubeinto another nonmagical object of similar size and mass and of equal or lesser value. You must spend 10 minutes handling the object to transform it. ")]),_c('li',[_c('strong',[_vm._v("Panacea")]),_vm._v(". You remove all curses, diseases, and poisons affecting a creature that you touch with the transmuters stone. The creature also regains all its hit points. ")]),_c('li',[_c('strong',[_vm._v("Restore Life")]),_vm._v(". You cast the "),_c('em',[_vm._v("raise dead ")]),_vm._v("spell on a creature you touch with the transmuters stone, without expending a spell slot or needing to have the spell in your spellbook. ")]),_c('li',[_c('strong',[_vm._v("Restore Youth")]),_vm._v(". You touch the transmuters stone to a willing creature, and that creatures apparent age is reduced by 3d10 years, to a minimum of 13 years. This effect doesnt extend the creatures lifespan. ")])])])])]),_c('i',{staticClass:"faq-toggle"})])])])])])])
